@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/toggle";
@use "../settings/common/zindex";

.#{theme.$selector-prefix}-toggle-menu {
    width: toggle.$toggle-width;
    height: toggle.$toggle-height;
    position: fixed;
    top: 0;
    left: 0;
    z-index: zindex.$zindex-lg;
    transform: rotate(0deg);
    transition: 0.2s ease-in-out;
    cursor: pointer;

    &:hover,
    &:focus {
        background: toggle.$toggle-hover-bg-color;

        span {
            background: toggle.$toggle-line-color-hover;
        }
    }

    span {
        display: block;
        position: absolute;
        height: toggle.$toggle-line-height;
        width: toggle.$toggle-line-width;
        background: toggle.$toggle-line-color;
        border-radius: toggle.$toggle-line-border-radius;
        opacity: 1;
        left: 16px;
        transform: rotate(0deg);
        transition: 0.2s ease-in-out;

        &:nth-child(1) {
            top: 15px;
            transform-origin: center center;
        }

        &:nth-child(2) {
            top: 20px;
            transform-origin: center center;
        }

        &:nth-child(3) {
            top: 25px;
            transform-origin: center center;
        }

        &:nth-child(4) {
            top: 20px;
            transform-origin: center center;
        }
    }

    &.-open span {
        &:nth-child(1) {
            opacity: 0;
        }

        &:nth-child(2) {
            /* width: 0%; */
            transform: rotate(-45deg);
        }

        &:nth-child(3) {
            opacity: 0;
        }

        &:nth-child(4) {
            transform: rotate(45deg);
        }
    }
}
