@use "../vendors/sitefinity/framework";

.#{framework.$selector-prefix}-album {
    display: flex;
    flex-wrap: wrap;
    height: 140px;
    overflow: hidden;

    img {
        @include framework.animation-fadeIn(0.6s);
    }

    &:not(.-document) {
        padding: 7px;
        border: 1px solid framework.$color-secondary-lighter;
    }

    &__holder {
        display: flex;
        justify-content: center;
        align-items: center;
        flex: auto;
        margin: 2px;
        width: 82px;
        height: 58px;
        overflow: hidden;
        background-color: framework.$color-primary-variant;

        &.-transparent {
            background-color: transparent;
        }
    }

    &__folder {
        display: block;
        position: relative;
        width: 100%;
        height: 100%;
        background-color: framework.$color-info-bg;
        z-index: framework.$zindex-xs;

        @include framework.animation-fadeIn(0.6s);

        &::before,
        &::after {
            display: block;
            content: " ";
            position: absolute;
            background-color: framework.$color-primary;
            z-index: framework.$zindex-sm;
        }

        &::before {
            top: -7px;
            right: 27px;
            width: 12px;
            height: 9px;
            transform-origin: top left;
            transform: rotate(45deg);
            z-index: framework.$zindex-md;
        }

        &::after {
            top: 0;
            right: 0;
            width: 36px;
            height: 9px;
        }
    }
}
/***
 * Album:
 */
/**
 * Empty Album:
 * `.sf-album`
 *
 *     @example
 *	    <div class="sf-album">
 *		    <span class="sf-album__holder -empty"></span>
 *		    <span class="sf-album__holder -empty"></span>
 *		    <span class="sf-album__holder -empty"></span>
 *		    <span class="sf-album__holder -empty"></span>
 *	    </div>
 *
 */
/**
 * Album with 3 images and no sub albums:
 * `.sf-album`
 *
 *     @example
 *	    <div class="sf-album">
 *	        <span class="sf-album__holder">
 *	            <img src="image.png" class="-landscape">
 *	        </span>
 *		    <span class="sf-album__holder">
 *	            <img src="image.png" class="-landscape">
 *	        </span>
 *		    <span class="sf-album__holder">
 *	            <img src="image.png" class="-landscape">
 *	        </span>
 *		    <span class="sf-album__holder -empty"></span>
 *	    </div>
 *
 */
/**
 * Album width sub albums:
 * `.sf-album`
 *
 *     @example
 *	    <div class="sf-album">
 *	        <span class="sf-album__holder">
 *	            <span class="sf-album__folder"></span>
 *	        </span>
 *		    <span class="sf-album__holder">
 *	            <span class="sf-album__folder"></span>
 *	        </span>
 *		    <span class="sf-album__holder">
 *	            <span class="sf-album__folder"></span>
 *	        </span>
 *		    <span class="sf-album__holder -empty"></span>
 *	    </div>
 *
 */
