@use "tree-list";
@use "tree-common";
@use "../common/colors";
@use "../common/zindex";
@use "typography";

$tree-border-color: colors.$color-secondary-lighter;
$tree-active-bg: colors.$color-info-bg;
$tree-bg: colors.$color-primary;
$tree-hover-bg: colors.$color-primary-variant;
$tree-drag-over-color: colors.$color-primary-invert;
$tree-link-color: colors.$color-info;
$tree-avatar-dimensions: 36px;
$tree-initials-color: colors.$color-secondary-darker;
$tree-initials-bg: colors.$color-secondary-lightest;
$tree-items-space-between: 10px;
$tree-items-large-space-between: 15px 15px 13px 15px;
$tree-items-large-check-width: 30px;
$tree-items-space-between-condensed: 8px;
$tree-items-inner-max-width: 100%;
$tree-items-outer-padding-top: $tree-items-space-between + 5px;
$tree-items-outer-condensed-padding-top: $tree-items-space-between-condensed;
$tree-items-title-font-size: typography.$font-size-h5;
$tree-items-description-font-size: typography.$font-size-base;
$tree-checkbox-wrapper-width: 33px;
$tree-hierarchical-overlay-width: 12px;
$tree-drag-wrapper-width: 21px;
$tree-items-zindex: zindex.$zindex-sm;
$tree-drop-slot-height: 2px;
$tree-drop-slot-placeholder: 10px;
$tree-drag-handle-size: var(--icon-size-m);
$tree-border-height: 1px;
$tree-btn-padding: 5px;
$tree-toggle-wrapper-padding: 3px 7px 0 5px;
$node-wrapper-border: tree-list.$tree-list-border-width solid tree-common.$tree-common-border-color;
