@use "../../../settings/components/status";

@mixin system-icon() {
    background-color: transparent;
    color: status.$status-bg-color-system;
    font-size: status.$status-font-size-system;
    overflow: visible;

    &.-dimmed {
        background-color: transparent;
    }
}

@mixin published-color() {
    background-color: status.$status-bg-color-published;
}

@mixin restricted-color() {
    background-color: status.$status-bg-color-restricted;
}

@mixin unpublished-color() {
    background-color: status.$status-bg-color-unpublished;
}

@mixin scheduled-color() {
    background-color: status.$status-bg-color-scheduled;
}

@mixin draft-color() {
    background-color: status.$status-bg-color-draft;
}
