@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/common/zindex";
@use "../tools/mixins/base/common" as commonMixins;

.-#{theme.$selector-prefix}-disabled {
    @include commonMixins.disabled;
}

.-#{theme.$selector-prefix}-clickable {
    cursor: pointer;
}
.-#{theme.$selector-prefix}-cursor-text {
    cursor: text;
}

.-#{theme.$selector-prefix}-user-select-none {
    user-select: none;
}

.-#{theme.$selector-prefix}-overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: zindex.$zindex-5xlg;
}

/*
  Cursor/Disabled utility classes

  Disabled link

  `-#{theme.$selector-prefix}-disabled`

      Markup:
      <a class="-#{theme.$selector-prefix}-disabled" href="https://www.progress.com/">Disabled link</a>

      Styleguide: Disabled.disabled
 */
/*
  Element which looks clickable

  `-#{theme.$selector-prefix}-clickable`

      Markup:
      <span class="-#{theme.$selector-prefix}-clickable">Clickable element</span>

      Styleguide: Disabled.clickable
 */
/*
  Element with cursor text

  `-#{theme.$selector-prefix}-cursor-text`

      Markup:
      <span class="-#{theme.$selector-prefix}-cursor-text">Cursor text</span>

      Styleguide: Disabled.cursor-text
 */

/*
  Element that cannot be selected

  `-#{theme.$selector-prefix}-user-select-none`

      Markup:
      <span class="-#{theme.$selector-prefix}-user-select-none">User selection none</span>

      Styleguide: Disabled.user-select-none
 */
