@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/row";
@use "../settings/components/form";
@use "../tools/mixins/base/width";

@include width.generate-widths(row.$columns);

.-#{theme.$selector-prefix}-width-form-small {
    width: form.$form-width-small !important;
}
.-#{theme.$selector-prefix}-width-form-medium {
    width: form.$form-width-medium !important;
}
.-#{theme.$selector-prefix}-width-form-large {
    width: form.$form-width-large !important;
}
.-#{theme.$selector-prefix}-max-width-form-xsmall {
    max-width: form.$form-width-xsmall !important;
}
.-#{theme.$selector-prefix}-max-width-form-2xsmall {
    max-width: form.$form-width-2xsmall !important;
}
.-#{theme.$selector-prefix}-max-width-form-large {
    max-width: form.$form-width-large !important;
}
.-#{theme.$selector-prefix}-width-100 {
    width: 100% !important;
}
.-#{theme.$selector-prefix}-max-width-100 {
    max-width: 100% !important;
}
/* Min width */
.-#{theme.$selector-prefix}-min-width-form-3xsmall {
    min-width: form.$form-width-3xsmall !important;
}

.-#{theme.$selector-prefix}-min-width-zero {
    min-width: 0 !important;
}
/*
  Width utility classes

  Width classes

  `.-#{theme.$selector-prefix}-width-{1,2,...,12}, .-#{theme.$selector-prefix}-width-form-{small, medium, large}`

      Markup:
