      <div class="-#{theme.$selector-prefix}-width-2" style="background-color: #83e3ff;">.-#{theme.$selector-prefix}-width-2 ~16%</div>
      <div class="-#{theme.$selector-prefix}-width-3" style="background-color: #83e3ff;">.-#{theme.$selector-prefix}-width-3 25%</div>
      <div class="-#{theme.$selector-prefix}-width-4" style="background-color: #83e3ff;">.-#{theme.$selector-prefix}-width-4 ~33%</div>
      <div class="-#{theme.$selector-prefix}-width-5" style="background-color: #83e3ff;">.-#{theme.$selector-prefix}-width-5 ~41%</div>
      <div class="-#{theme.$selector-prefix}-width-6" style="background-color: #83e3ff;">.-#{theme.$selector-prefix}-width-6 50%</div>
      <div class="-#{theme.$selector-prefix}-width-7" style="background-color: #83e3ff;">.-#{theme.$selector-prefix}-width-7 ~58%</div>
