@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/status" as statusSettings;
@use "../settings/common/colors";
@use "../tools/mixins/base/circle";
@use "../tools/mixins/base/text";
@use "../tools/mixins/components/status";
@use "../tools/functions/convert";

.#{theme.$selector-prefix}-status {
    &__text {
        @include text.size-base;

        margin-top: 0;
        margin-bottom: 0;
        color: statusSettings.$status-text-color;
    }

    &__icon {
        @include circle.create-circle(statusSettings.$status-diameter);
        @include text.font-awesome;
        @include text.size-base;

        color: statusSettings.$status-color;
        background-color: statusSettings.$status-bg-color-default;

        //default value if no font awesome icon
        &:not([class*="sf-fa-"])::before {
            content: convert.get-unicode(#{statusSettings.$status-icon-default});
        }

        //Icon colors for different status groups
        &.-for-published {
            @include status.published-color;
        }

        &.-for-restricted {
            @include status.restricted-color;
        }

        &.-for-unpublished {
            @include status.unpublished-color;
        }

        &.-for-scheduled {
            @include status.scheduled-color;
        }

        &.-for-draft {
            @include status.draft-color;
        }

        &.-system-icon {
            @include status.system-icon;
        }

        //Predefined status classes
        &.-published {
            @include status.published-color;

            &::before {
                content: convert.get-unicode(#{statusSettings.$status-icon-published});
            }
        }

        &.-locked {
            @include status.restricted-color;

            &::before {
                content: convert.get-unicode(#{statusSettings.$status-icon-locked});
            }
        }

        &.-unpublished {
            @include status.unpublished-color;

            &::before {
                content: convert.get-unicode(#{statusSettings.$status-icon-unpublished});
            }
        }

        &.-deleted {
            font-size: var(--icon-size-xl);
            background-color: transparent;
            color: statusSettings.$status-color-secondary;

            &::before {
                content: convert.get-unicode(#{statusSettings.$status-icon-deleted});
            }

            &.-dimmed {
                background-color: transparent;
            }
        }

        &[class*="-rejected"] {
            @include status.restricted-color;

            &::before {
                content: convert.get-unicode(#{statusSettings.$status-icon-rejected});
            }
        }

        &.-scheduled {
            @include status.scheduled-color;

            &::before {
                content: convert.get-unicode(#{statusSettings.$status-icon-scheduled});
            }
        }

        &.-group-page {
            @include status.system-icon;

            &::before {
                content: convert.get-unicode(#{statusSettings.$status-icon-group});
            }
        }

        &.-hierarchical {
            @include status.system-icon;

            &::before {
                content: convert.get-unicode(#{statusSettings.$status-icon-group});
            }
        }

        &.-flat {
            @include status.system-icon;

            &::before {
                content: convert.get-unicode(#{statusSettings.$status-icon-flat});
            }
        }

        &.-redirecting-page {
            @include status.system-icon;

            &::before {
                content: convert.get-unicode(#{statusSettings.$status-icon-redirect});
            }
        }

        &.-draft,
        &.-draftnewer,
        &.-publishedanddraft {
            @include status.draft-color;

            &::before {
                content: convert.get-unicode(#{statusSettings.$status-icon-draft});
            }
        }

        &[class*="-awaiting"] {
            &::before {
                content: convert.get-unicode(#{statusSettings.$status-icon-awaiting});
            }
        }

        &.-dimmed {
            background-color: colors.$color-secondary;
        }

        &.-for-translation {
            font-size: var(--icon-size-3xs);
            background-color: colors.$color-info;

            &::before {
                content: convert.get-unicode(#{statusSettings.$status-icon-for-translation});
            }
        }
    }
}

/*
  Text

  `.#{theme.$selector-prefix}-status__text`

      Markup:
      <p class="#{theme.$selector-prefix}-status__text">Published</p>

      Styleguide: Status.text
 */
