@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/chip";
@use "../settings/components/input";
@use "../settings/common/colors";
@use "../settings/common/zindex";
@use "../tools/mixins/components/chip" as chipMixins;
@use "../tools/mixins/base/image";
@use "../tools/mixins/base/text";
@use "sass:math";

.#{theme.$selector-prefix}-chip {
    display: inline-block;
    position: relative;
    margin-right: chip.$chip-margin-right;
    margin-bottom: chip.$chip-margin-bottom;
    min-height: chip.$chip-height;
    max-width: chip.$chip-max-width;
    vertical-align: top;
    font-size: chip.$chip-font-size;

    @each $tag-type, $bgcolor in chip.$tag-types {
        @if ($tag-type == default) {
            &__content {
                background-color: $bgcolor;
            }
        }

        @else {

            &.-#{$tag-type} {

                .#{theme.$selector-prefix}-chip__content {
                    background-color: $bgcolor;
                }
            }
        }
    }

    &__inner-content {
        @include text.ellipsis;
    }

    &.-white {
        color: colors.$color-primary;
    }

    &.-removable {

        .#{theme.$selector-prefix}-chip__content {
            transition: padding 0.2s;
            overflow: hidden;
        }

        .#{theme.$selector-prefix}-chip__inner-content {
            transition: text-indent 0.2s;
        }

        &:hover {

            .#{theme.$selector-prefix}-chip__remove-button {
                display: inline-flex;
                align-items: center;
            }

            .#{theme.$selector-prefix}-chip__content {
                padding-right: chip.$chip-removable-content-padding-right;
            }

            .#{theme.$selector-prefix}-chip__inner-content {
                text-indent: -#{chip.$chip-removable-content-text-indent};
            }
        }
    }

    &.-avatar {

        .#{theme.$selector-prefix}-chip__content {
            padding-left: chip.$chip-identifier-width;
        }
    }

    &.-icon {

        .#{theme.$selector-prefix}-chip__content {
            padding-left: chip.$chip-identifier-width;
        }
    }

    &.-initials {

        .#{theme.$selector-prefix}-chip__content {
            padding-left: chip.$chip-identifier-width;
        }
    }

    &.-editable {

        .#{theme.$selector-prefix}-chip__content {
            display: inline-block;
            min-width: chip.$chip-editable-min-width;
        }
    }

    &.-dropped {

        .#{theme.$selector-prefix}-chip__dropdown {
            display: block;
            position: absolute;
            top: chip.$chip-dropdown-top;
            padding-top: 0;
            padding-bottom: 0;
            width: auto;
            max-width: chip.$chip-dropdown-max-width;
            z-index: zindex.$zindex-xlg;
        }
    }

    &.-input {
        display: block;
        margin-right: 0;
        margin-bottom: 0;

        .#{theme.$selector-prefix}-chip__input {
            padding-left: 5px;
            padding-right: 5px;
            height: input.$input-height;
            width: 100%;
            border: 1px solid colors.$color-secondary-lighter;
            background-color: colors.$color-primary;
            border-radius: 0;
        }

        .#{theme.$selector-prefix}-chip__dropdown {
            width: 100%;
        }
    }

    &.-stripped {
        font-size: inherit;

        .#{theme.$selector-prefix}-chip__content {
            background-color: transparent;
            padding: 0;
            border-radius: 0;
        }
    }

    &__content {
        @include text.ellipsis;

        display: inline-flex;
        padding-left: chip.$chip-horizontal-padding;
        padding-right: chip.$chip-horizontal-padding;
        height: chip.$chip-height;
        line-height: chip.$chip-line-height;
        text-align: left;
        vertical-align: top;
        border-radius: chip.$chip-height;
        background-color: colors.$color-info-bg;
        overflow: hidden;
        max-width: 100%;

        &:focus {
            outline-color: transparent;
        }

        .-size-m & {
            display: inline-block;
            max-width: 130px;
        }

        .-size-l & {
            max-width: 180px;
        }
    }

    &__hint {
        flex-shrink: 0;
    }

    &__avatar {
        @include chipMixins.chip-identifier;
    }

    &__icon {
        @include chipMixins.chip-identifier;
    }

    &__initials {
        @include chipMixins.chip-identifier;

        font-size: chip.$chip-initials-font-size;
        text-transform: uppercase;
    }

    &__image {
        @include image.fit-cover;

        border-radius: 50%;
    }

    &__remove-button {
        display: none;
        position: absolute;
        top: 0;
        right: math.div(chip.$chip-horizontal-padding, 2);
        height: chip.$chip-height;
        line-height: chip.$chip-height;
        text-align: right;
        cursor: pointer;
    }

    &__dropdown {
        display: none;
    }
}

/*
  Chip
 */
@each $tag-type, $bgcolor in chip.$tag-types {
    @if ($tag-type == default) {

        /*
          Default chip

          `.#{theme.$selector-prefix}-chip`

              Markup:
              <div class="#{theme.$selector-prefix}-chip">
                  <div class="#{theme.$selector-prefix}-chip__content">
                      Default chip
                  </div>
              </div>
              <div class="#{theme.$selector-prefix}-chip -removable">
                  <div class="#{theme.$selector-prefix}-chip__content">
                      Default chip with remove
                  </div>
                  <i class="#{theme.$selector-prefix}-icon -close -size-2xs -black #{theme.$selector-prefix}-chip__remove-button"></i>
              </div>

            Styleguide: Chip.default
         */
    }

    @else {

        /*
          #{$tag-type} Chip

          `.#{theme.$selector-prefix}-chip.-#{$tag-type}`

              Markup:
              <div class="#{theme.$selector-prefix}-chip -#{$tag-type}">
                  <div class="#{theme.$selector-prefix}-chip__content">
                      #{$tag-type} chip
                  </div>
              </div>
              <div class="#{theme.$selector-prefix}-chip -#{$tag-type} -removable">
                  <div class="#{theme.$selector-prefix}-chip__content">
                      #{$tag-type} chip with remove
                  </div>
                  <i class="#{theme.$selector-prefix}-icon -close -size-2xs -black #{theme.$selector-prefix}-chip__remove-button"></i>
              </div>

              Styleguide: Chip.default-#{$tag-type}
         */
    }
}

