@use "../vendors/sitefinity/framework";
@use "../settings/base/scroll";

.#{framework.$selector-prefix}-router-wrapper {
    &.-menu {
        position: absolute;
        z-index: framework.$zindex-4xlg;
        display: flex;
        width: calc(100vw - #{scroll.$scroll-width});
        //min-height: 100vh;
        overflow-x: hidden;

        sf-home {
            order: 1;
            z-index: framework.$zindex-xlg;
        }

        sf-content {
            order: 2;
            z-index: framework.$zindex-xlg;
        }
    }

    &.-is-animating {
        > ng-component {
            position: absolute;
            width: 100%;
        }
    }
}

.sf-router-wrapper.-active:not(.-is-animating) ~ .sf-router-wrapper,
sf-modal-window ~ .sf-router-wrapper,
.sf-insight-window ~ .sf-router-wrapper {
    display: none !important;
}

.sf-router-wrapper.-active.-is-animating ~ .sf-router-wrapper {
    width: calc(100vw - #{scroll.$scroll-width});
    overflow-x: hidden;
}

.ng-animating .sf-top-toolbar__nav-button {
    display: none;
}
