@use "../themes/sitefinity/base" as theme;
@use "../common/colors";

#{theme.$selector-app} {
    --status-icon-color-system: #{colors.$color-secondary};
    --status-icon-color-published: #{colors.$color-success};
    --status-icon-color-restricted: #{colors.$color-error};
    --status-icon-color-unpublished: #{colors.$color-secondary-darker};
    --status-icon-color-scheduled: #{colors.$color-info};
    --status-icon-color-draft: #{colors.$color-primary-invert};
    --status-bg-color-default: #{colors.$color-warning};
}
