@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/select";
@use "../tools/mixins/components/select" as selectMixins;

.#{theme.$selector-prefix}-select {
    @include selectMixins.select;

    &__item {
        @include selectMixins.select-item;
    }

    &.-inline {
        display: inline-block;
    }

    &.-small {
        height: select.$select-sm-height;
        font-size: select.$select-sm-font-size;

        .#{theme.$selector-prefix}-select__item {
            line-height: select.$select-sm-line-height;
            padding-left: select.$select-item-sm-horizontal-spacing;
            padding-right: calc(#{select.$select-item-sm-horizontal-spacing} + #{select.$select-arrow-sm-font-size});
        }

        &::after {
            height: select.$select-sm-height;
            line-height: select.$select-sm-height;
            font-size: select.$select-arrow-sm-font-size;
            right: select.$select-arrow-sm-right-position;
        }
    }
}

