@use "../../../settings/common/colors";
@use "../../../settings/common/zindex";

@mixin item-overlay($bg-color: colors.$color-info-bg, $opacity: 0.5, $zIndex: 1, $top: 0, $left: 0, $width: 100%, $height: 100%) {
    display: block;
    content: " ";
    position: absolute;
    top: $top;
    left: $left;
    width: $width;
    height: $height;
    opacity: $opacity;
    background: $bg-color;
    z-index: $zIndex;
}

@mixin absolute-in-wrapper($zIndex: 1, $top: 0, $left: 0, $width: 100%, $height: 100%) {
    position: absolute;
    top: $top;
    left: $left;
    width: $width;
    height: $height;
    z-index: $zIndex;
}

@mixin full-height-container {
    position: relative;
    min-height: 100vh;
    text-align: center;
    background-color: colors.$color-primary;
    z-index: zindex.$zindex-4xlg;
}
