@use "../../../settings/components/link";
@use "../../../settings/common/colors";
@use "../base/decoration";
@use "../base/center";

@mixin default-link() {
    color: link.$link-color;
    text-decoration: none;
    cursor: pointer;

    &.-secondary {
        color: link.$link-secondary-color;
    }

    &:hover,
    &:active,
    &:focus {
        color: link.$link-color-hover;
    }

    &.-no-hover {
        &:hover,
        &:active,
        &:focus {
            color: link.$link-color;
        }
    }

    &.-side {
        display: inline-block;
        margin-bottom: link.$link-navigation-margin-bottom;
        padding: link.$link-navigation-padding;
        color: colors.$color-primary-invert;

        &.-active,
        &:focus,
        &:hover {
            background-color: colors.$color-secondary-lighter;
            border-radius: link.$link-navigation-border-radius;
        }
    }

    &.-side-secondary {
        color: link.$link-side-secondary-color;

        &:hover {
            color: link.$link-side-secondary-color-hover;
        }

        &.-active {
            color: link.$link-side-secondary-color-active;
        }
    }

    &.-basic {
        color: colors.$color-primary-invert;

        &:hover,
        &:active,
        &:focus {
            color: colors.$color-primary-invert;
        }
    }

    &.-top-toolbar {
        @include center.flex;

        display: inline-flex;
        padding: link.$link-toolbar-padding;
        color: colors.$color-primary-invert;
        border: link.$link-toolbar-border-width;
        background-color: colors.$color-primary;
        border-radius: link.$link-toolbar-border-width;
        height: link.$link-toolbar-height;

        &.-active,
        &:focus,
        &:hover {
            @include decoration.fill(colors.$color-primary-invert, colors.$color-primary-variant, colors.$color-secondary-lightest);
        }
    }
}
