@use "../vendors/sitefinity/framework";
@use "../settings/components/layout";
@use "../settings/base/break-points";

.#{framework.$selector-prefix}-section-nav {
    position: fixed;
    top: layout.$details-section-nav-top;
    bottom: 0;
    left: layout.$details-section-nav-left;
    max-width: layout.$details-section-nav-width;
    overflow: auto;
    animation: sectionNav framework.$animation-time 1 framework.$animation-timing;
    z-index: framework.$zindex-sm;

    &.-in-form {
        position: absolute;
        top: layout.$details-section-nav-in-form-top;
        left: calc(100% + 100px);
    }
}

@media only screen and (max-width: break-points.$break-point-xl) {
    .sf-section-nav {
        display: none;
    }
}

@media only screen and (max-width: #{break-points.$break-point-xl + framework.$sidebar-width}) {
    .sf-main.-shrink + sf-content-section-menu .sf-section-nav {
        display: none;
    }
}

@media only screen and (max-width: #{break-points.$break-point-xl + framework.$sidebar-width-wide}) {
    .sf-main.-narrow + sf-content-section-menu .sf-section-nav {
        display: none;
    }
}

@keyframes sectionNav {
    from {
        transform: translateX(calc(-100% - #{layout.$details-section-nav-left}));
    }

    to {
        transform: translateX(0);
    }
}
