@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/typography";
@use "../settings/components/choice-group";

.#{theme.$selector-prefix}-choice-group {
    &.-chip {
        display: inline-flex;
        font-size: typography.$font-size-base;

        .#{theme.$selector-prefix}-radio {
            margin-right: choice-group.$choice-group-radio-margin-right;

            &:first-child {
                .#{theme.$selector-prefix}-radio__label {
                    border-left: 1px solid choice-group.$choice-group-radio-chip-border-color;
                    border-top-left-radius: choice-group.$choice-group-radio-chip-height;
                    border-bottom-left-radius: choice-group.$choice-group-radio-chip-height;
                }
            }

            &:last-child {
                .#{theme.$selector-prefix}-radio__label {
                    border-top-right-radius: choice-group.$choice-group-radio-chip-height;
                    border-bottom-right-radius: choice-group.$choice-group-radio-chip-height;
                }
            }

            &.-size-m {

                .#{theme.$selector-prefix}-radio__label {
                    width: choice-group.$choice-group-radio-chip-size-md-width;
                }
            }

            &.-size-l {

                .#{theme.$selector-prefix}-radio__label {
                    width: auto;
                }

                .#{theme.$selector-prefix}-radio__text {
                    margin: choice-group.$choice-group-radio-text-lg-margin;
                }
            }
        }

        .#{theme.$selector-prefix}-radio__label {
            display: inline-flex;
            justify-content: center;
            height: choice-group.$choice-group-radio-chip-height;
            line-height: choice-group.$choice-group-radio-chip-height;
            min-width: choice-group.$choice-group-radio-chip-width;
            text-align: center;
            border-top: 1px solid choice-group.$choice-group-radio-chip-border-color;
            border-bottom: 1px solid choice-group.$choice-group-radio-chip-border-color;
            border-right: 1px solid choice-group.$choice-group-radio-chip-border-color;
            user-select: none;

            &:hover {
                background-color: choice-group.$choice-group-radio-chip-hover-bg-color;
                color: choice-group.$choice-group-radio-chip-hover-color;
            }
        }
        .#{theme.$selector-prefix}-radio__icon {
            display: none;
        }

        .#{theme.$selector-prefix}-radio__text {
            margin: choice-group.$choice-group-radio-text-margin;
        }

        .#{theme.$selector-prefix}-radio__input:checked + .#{theme.$selector-prefix}-radio__label {
            background-color: choice-group.$choice-group-radio-chip-selected-bg-color;
            color: choice-group.$choice-group-radio-chip-selected-color;
        }
    }

    &.-list {
        .#{theme.$selector-prefix}-radio,
        .#{theme.$selector-prefix}-checkbox {
            margin-bottom: 0;
            margin-right: 0;

            &:first-child {
                .#{theme.$selector-prefix}-radio__label,
                .#{theme.$selector-prefix}-checkbox__label {
                    border-top: 1px solid choice-group.$choice-group-radio-chip-border-color;
                }
            }
        }
        .#{theme.$selector-prefix}-radio__label,
        .#{theme.$selector-prefix}-checkbox__label {
            padding: 10px 9px;
            font-size: 13px;
            border-bottom: 1px solid choice-group.$choice-group-radio-chip-border-color;
        }
        .#{theme.$selector-prefix}-radio__icon {
            display: none;
        }

        .#{theme.$selector-prefix}-radio__text {
            margin-left: 0;
        }

        .#{theme.$selector-prefix}-radio__input:checked + .#{theme.$selector-prefix}-radio__label,
        .#{theme.$selector-prefix}-checkbox__input:checked + .#{theme.$selector-prefix}-checkbox__label {
            background-color: choice-group.$choice-group-radio-chip-selected-bg-color;
        }
    }
}
