@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/typography";
@use "../tools/mixins/components/headings";

@include headings.headings;

h1 {
    &.-#{theme.$selector-prefix}-h-large {
        @include headings.h1-large;
    }
}

.h1 {
    &.-#{theme.$selector-prefix}-h-large {
        @include headings.h1-large;
    }
}

h4 {
    &.-#{theme.$selector-prefix}-h-large {
        @include headings.h4-large;
    }
}

.h4 {
    &.-#{theme.$selector-prefix}-h-large {
        @include headings.h4-large;
    }
}

p {
    margin-bottom: typography.$line-height-px-base;
}

/*
  Typography

  Headings

      Markup:
      <h1 class="-#{theme.$selector-prefix}-h-large">H1 List Page Title</h1>
      <h1>H1 Detail Page Title</h1>
      <h2>H2 Group Title</h2>
      <h3>H3 Item Title</h3>
      <h4 class="-#{theme.$selector-prefix}-h-large">H4 Form Label</h4>
      <h4>H4 Regular Label</h4>
      <h5>H5 Small Labels</h5>

      Styleguide: Typography.headings
 */
/*
 Heading CSS Classes

     Markup:
     <div class="h1 -#{theme.$selector-prefix}-h-large">H1 List Page Title</div>
     <div class="h1">H1 Detail Page Title</div>
     <div class="h2">H2 Group Title</div>
     <div class="h3">H3 Item Title</div>
     <div class="h4 -#{theme.$selector-prefix}-h-large">H4 Form Label</div>
     <div class="h4">H4 Regular Label</div>
     <div class="h5">H5 Small Labels</div>

     Styleguide: Typography.headings-classes
 */

