@use "../themes/sitefinity/base" as theme;
@use "../common/colors";

#{theme.$selector-app} {
    --badge-white-border-color: #{colors.$color-secondary-lighter};
    --badge-color-bg-positive: #{colors.$color-success};
    --badge-color-bg-negative: #{colors.$color-error};
    --badge-add-variation-bg-color: #{colors.$color-info-light};
    --badge-add-variation-border-color: #{colors.$color-info-light};
    --badge-add-variation-hover: #{lighten(desaturate(colors.$color-info, 25.64), 34.51)};
    --badge-add-variation-border-hover: #{lighten(desaturate(colors.$color-info, 25.64), 34.51)};
    --badge-add-variation-color: #{colors.$color-primary};
    --badge-edit-variation-color: #{colors.$color-primary};
    --badge-edit-variation-bg-color: #{colors.$color-info};
    --badge-edit-variation-hover: #{darken(colors.$color-info, 6.67)};
    --badge-add-color: #{colors.$color-primary-invert};
    --badge-icon-color: #{colors.$color-primary-invert};
    --badge-white-color: #{colors.$color-primary-invert};
    --badge-negative-color: #{colors.$color-primary};
    --badge-positive-color: #{colors.$color-primary};
    --badge-color: #{colors.$color-primary};
}
