@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/input" as inputSettings;
@use "../settings/components/typography" as typographySettings;
@use "../settings/common/colors";
@use "../settings/common/zindex";
@use "../tools/mixins/components/headings";
@use "../tools/mixins/components/inputs" as inputMixins;
@use "../tools/mixins/base/focus";

::-ms-input-placeholder { /* WebKit, Blink, Edge */
    color: inputSettings.$input-placeholder-color;
    font-weight: typographySettings.$font-weight-normal;
}

:placeholder { /* Mozilla Firefox 4 to 18 */
    color: inputSettings.$input-placeholder-color;
    font-weight: typographySettings.$font-weight-normal;
    opacity: 1;
}

::placeholder { /* Mozilla Firefox 19+ */
    color: inputSettings.$input-placeholder-color;
    font-weight: typographySettings.$font-weight-normal;
    opacity: 1;
}

:-moz-ui-invalid:not(output) {
    box-shadow: none;
}

::-ms-clear {
    display: none;
}

:-webkit-autofill {
    box-shadow: inset 0 0 0 25px inputSettings.$input-autofill-shadow-color;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

input[type="week"] {
    display: flex;
    align-items: center;
}

.#{theme.$selector-prefix}-input {
    position: relative;
    display: inline-block;
    line-height: inputSettings.$input-line-height;
    vertical-align: top;
    width: 100%;

    input,
    textarea {
        &:not([readonly]) {
            @include inputMixins.default-inputs;

            color: inputSettings.$input-color;
        }
    }

    input {
        padding: inputSettings.$input-padding;
    }

    textarea {
        min-height: inputSettings.$textarea-min-height;
        padding: inputSettings.$textarea-padding;
        white-space: pre-wrap;
    }

    &:not(.-bordered) {
        input,
        textarea {
            &[readonly] {
                @include inputMixins.read-only;
            }
        }
    }

    &.-clearable {

        .#{theme.$selector-prefix}-input__field {
            padding-right: 20px;
        }
    }

    &.-side-content {

        .#{theme.$selector-prefix}-input__title {
            border-right: 1px solid inputSettings.$input-border;
        }

        .#{theme.$selector-prefix}-input__field {
            padding-right: inputSettings.$input-side-content-padding-right;
        }
    }

    &.-bold {

        .#{theme.$selector-prefix}-input__field {
            font-weight: typographySettings.$font-weight-bold;
        }
    }

    &.-unit {
        display: flex;
        flex-wrap: nowrap;

        .#{theme.$selector-prefix}-input__label {
            width: 100%;
        }

        .#{theme.$selector-prefix}-input__field {
            flex: 1;
            width: auto;
            min-width: 40px;
        }

        &.-left {

            .#{theme.$selector-prefix}-input__field {
                order: 2;
            }

            .#{theme.$selector-prefix}-input__unit {
                order: 1;
                border-right: 0;
                border-left: 1px solid inputSettings.$input-border;

                &::before {
                    right: auto;
                    left: calc(100% + #{inputSettings.$input-unit-before-horizontal-position - inputSettings.$input-unit-before-horizontal-width});
                    border-left: 0;
                }
            }
        }
    }

    &.-textarea {
        &.-resizable {
            textarea {
                resize: both;
                overflow: auto;
            }
        }

        .#{theme.$selector-prefix}-input__side-content {
            margin-left: inputSettings.$textarea-side-content-margin-left;
            top: inputSettings.$textarea-side-content-position-top;
            left: inputSettings.$textarea-side-content-position-left;
            right: inputSettings.$textarea-side-content-position-right;
            background: inputSettings.$textarea-side-content-background;
            border: inputSettings.$textarea-side-content-background;
            font-size: inputSettings.$textarea-side-content-font-size;
            line-height: inputSettings.$textarea-side-content-line-height;
            color: inputSettings.$textarea-side-content-color;
            height: inputSettings.$textarea-side-content-height;
            width: inputSettings.$textarea-side-content-width;
            text-align: inputSettings.$textarea-side-content-text-align;
        }
    }

    &.-char-counter {

        .#{theme.$selector-prefix}-input__side-content {
            top: inputSettings.$textarea-side-content-counter-position-top;
        }
    }

    &.-title {

        .#{theme.$selector-prefix}-input__title[contenteditable="true"] {
            @include focus.focusable($position: bottom, $paddingBottom: 10px, $hover: true);
        }

        &.-large {

            .#{theme.$selector-prefix}-input__title {
                @include headings.h1-large;
            }
        }

        &.-small {

            .#{theme.$selector-prefix}-input__title {
                font-size: typographySettings.$font-size-h3;
                min-height: 0;
            }
        }

        .#{theme.$selector-prefix}-input__side-content {
            margin-left: 10px;
            top: 0;
            left: 100%;
            right: auto;
            font-size: typographySettings.$font-size-base;
            line-height: 1.55;
            text-align: left;
        }
    }

    &.-short {
        &.-textarea {
            textarea {
                min-height: inputSettings.$textarea-min-height-short;
            }
        }
        .#{theme.$selector-prefix}-input__field {
            height: inputSettings.$input-short-height;
        }
    }

    &.-large {
        &.-textarea {
            textarea {
                min-height: inputSettings.$textarea-min-height-large;
            }
        }

        .#{theme.$selector-prefix}-input__field,
        .#{theme.$selector-prefix}-input__textarea {
            font-size: typographySettings.$font-size-lg;
        }
    }

    &.-centered {

        .#{theme.$selector-prefix}-input__field {
            text-align: center;
        }
    }

    &.-no-border {
        border: 0;
        padding: 0;

        .#{theme.$selector-prefix}-input__textarea {
            border: 0;
            padding: 0;
        }
    }

    &.-no-resize {

        .#{theme.$selector-prefix}-input__textarea {
            resize: none;
        }
    }

    &.-inline {

        .#{theme.$selector-prefix}-input__field,
        .#{theme.$selector-prefix}-input__textarea {
            font-size: inputSettings.$input-inline-font-size;
            border: 0;

            &:not([disabled]):hover {
                background-color: colors.$color-warning-bg;
            }

            &:focus {
                box-shadow: none;
            }
        }

        .#{theme.$selector-prefix}-input__field {
            padding-left: 0;
            padding-right: 0;
            height: inputSettings.$input-inline-height;
        }

        .#{theme.$selector-prefix}-input__textarea {
            padding: 0;
        }
    }

    &.-small-label {

        .#{theme.$selector-prefix}-input__label {
            @include headings.h5;

            margin: inputSettings.$input-label-margin;
        }
    }

    &__label:not(.-small-label) {
        display: block;
        margin: inputSettings.$input-label-margin;
        font-weight: inputSettings.$input-label-font-weight;
        font-size: inputSettings.$input-label-font-size;
    }

    &__field {
        color: inherit;
        height: inputSettings.$input-height;

        &:focus + .#{theme.$selector-prefix}-input__unit {
            @include focus.focusable($native: false);
        }
    }

    &__no-value {
        font-size: inputSettings.$input-no-value-font-size;
        color: inputSettings.$input-no-value-color;
    }

    &__hint {
        margin: inputSettings.$input-hint-margin;
        color: inputSettings.$input-hint-color;
        width: 100%;
    }

    &__required {
        display: block;
        margin: inputSettings.$input-required-margin;
        color: inputSettings.$input-required-color;
    }

    &__clear-button {
        @include inputMixins.icon;

        z-index: zindex.$zindex-xs;

        &:hover,
        &:focus,
        &:active {

            .#{theme.$selector-prefix}-icon {
                color: inputSettings.$input-clear-button-color-hover;
            }
        }

        &.-show {
            @include inputMixins.show-clear-icon;
        }
    }

    &__side-content {
        position: absolute;
        opacity: 1;
        top: inputSettings.$input-side-content-position-top;
        color: inputSettings.$input-side-content-color;
    }

    &__unit {
        cursor: pointer;
        text-decoration: none;
        text-align: center;
        vertical-align: middle;
        padding-left: inputSettings.$input-unit-horizontal-padding;
        padding-right: inputSettings.$input-unit-horizontal-padding;
        width: auto;
        height: inputSettings.$input-unit-dimensions;
        line-height: inputSettings.$input-unit-dimensions;
        background-color: inputSettings.$input-unit-bg-color;
        border-top: 1px solid inputSettings.$input-border;
        border-right: 1px solid inputSettings.$input-border;
        border-bottom: 1px solid inputSettings.$input-border;
        outline: none;
        white-space: nowrap;

        &::before {
            display: block;
            content: "";
            position: relative;
            left: -#{inputSettings.$input-unit-before-horizontal-position};
            width: inputSettings.$input-unit-before-horizontal-width;
            height: calc(100% + 2px);
            border-top: 1px solid inputSettings.$input-border;
            border-bottom: 1px solid inputSettings.$input-border;
            background-color: inputSettings.$input-unit-bg-color;
        }
    }

    &__char-counter {
        position: absolute;
        margin-left: inputSettings.$input-char-counter-margin-left;
        font-size: inputSettings.$input-char-counter-font-size;
        top: inputSettings.$input-char-counter-position-top;
        left: inputSettings.$input-char-counter-position-left;
        color: inputSettings.$input-char-counter-success-color;

        &.-error {
            color: inputSettings.$input-char-counter-error-color;
        }
    }

    &__title {
        @include headings.h1;

        outline: none;
        cursor: text;
        min-height: 58px;

        @at-root {
            @include inputMixins.input-placeholder(&, (font-family: inputSettings.$input-title-font-family, font-size: inputSettings.$input-title-font-size, line-height: inputSettings.$input-title-line-height, font-weight: inputSettings.$input-title-font-weight));
        }

        &.-placeholder {
            &::before {
                display: block;
                content: attr(data-placeholder);
                color: inputSettings.$input-title-placeholder-color;
            }

            br:only-child {
                position: absolute;
                top: 0;
            }
        }

        .#{theme.$selector-prefix}-input__required {
            margin-top: inputSettings.$input-title-required-margin-top;
            padding-top: inputSettings.$input-title-required-padding-top;
            border-top: 1px solid inputSettings.$input-title-required-color;
        }
    }

    &.ng-dirty.ng-invalid,
    &.-required {

        .#{theme.$selector-prefix}-input__field,
        .#{theme.$selector-prefix}-input__textarea {
            &.ng-dirty.ng-invalid {
                &,
                &:focus,
                &:hover {
                    border-color: inputSettings.$input-required-color;
                }
            }
        }

        // TODO: check if needs to stay, looks like #{theme.$selector-prefix}-field__error is used to display errors

        &.-title {

            .#{theme.$selector-prefix}-input__required {
                margin-top: 0;
                padding-top: 3px;
                border-top: 1px solid inputSettings.$input-required-color;
            }
        }
    }
}

.#{theme.$selector-prefix}-input-file {
    display: flex;

    &__label {
        position: relative;
        cursor: pointer;
        z-index: zindex.$zindex-sm;
    }

    input[type="file"] {
        position: absolute;
        height: 0;
        width: 0;
        opacity: 0;
        z-index: zindex.$zindex-default;

        &:focus + {

            .#{theme.$selector-prefix}-input-file__label {

                .#{theme.$selector-prefix}-button {
                    background-color: colors.$color-secondary-lighter;

                    &.-action {
                        background-color: #309255;
                    }
                }
            }
        }
    }
}

