@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/expander";
@use "../tools/mixins/components/headings";
@use "../tools/mixins/components/button";

.#{theme.$selector-prefix}-expander {
    display: block;
    margin-bottom: expander.$expander-margin-bottom;
    margin-left: calc(#{expander.$expander-left-offset} * -1);

    > hr {
        margin-left: expander.$expander-left-offset;
    }

    &__header {
        @include headings.h2;

        display: inline-block;
        padding-left: expander.$expander-header-padding-horizontal;
        padding-top: expander.$expander-header-padding-vertical;
        padding-bottom: expander.$expander-header-padding-vertical;
        width: 100%;
        position: relative;
        cursor: pointer;

        &:focus .#{theme.$selector-prefix}-expander__icon {
            @include button.focus;
        }
    }

    &__icon {
        margin-right: expander.$expander-icon-margin-right !important;
        margin-left: expander.$expander-icon-margin-left !important;
    }

    &__content {
        margin-left: expander.$expander-left-offset;
    }

    &__checkbox {
        position: absolute;
        left: -9999px;

        ~ .#{theme.$selector-prefix}-expander__content {
            visibility: hidden;
        }

        &:checked {
            ~ .#{theme.$selector-prefix}-expander__content {
                visibility: visible;
            }

            //used in .net core inline editting
            ~ .#{theme.$selector-prefix}-expander__section {
                padding: expander.$expander-section-padding;
                background: expander.$expander-section-background;
                margin-right: expander.$expander-section-margin-right;
            }
        }
    }
}

