@use "../common/colors";

$loader-rect-width: 6px;
$loader-rect-height: $loader-rect-width;
$loader-big-rect-width: 10px;
$loader-big-rect-height: $loader-big-rect-width;
$loader-mini-rect-width: 4px;
$loader-mini-rect-height: $loader-mini-rect-width;
$loader-mode-skeleton-stripe-height: 10px;
$loader-mode-skeleton-stripe-bottom-offset: 6px;
$loader-list-padding: 13px 8px;
$loader-border-radius-mini: 1px;
$loader-border-radius: 2px;
$loader-final-left-indent: 75%;
$loader-margin: 0 1px 0 0;
$loader-mode-skeleton-margin: 0;
$loader-animation-speed: 1s;
$loader-animation-opacity: 0.3;
$loader-color: colors.$color-success;
$loader-color-mode-color: colors.$color-primary-invert;
$loader-color-mode-transparent: colors.$color-primary;
$loader-bg: transparent;
$loader-bg-cover: colors.$color-primary;
$loader-bg-skeleton-mode: colors.$color-primary-variant;
$loader-border-mode-skeleton: colors.$color-secondary-lightest;

@keyframes loader {
    40% {
        opacity: 1;
    }
}

@keyframes loadingProgressbar {
    0% { transform: scaleX(0); }
    50% { transform: scaleX(1); }
    100% { transform: translateX(100%); }
}

@keyframes loadingSkeleton {
    0% {
        opacity: 0.7;
    }

    50% {
        opacity: 0.8;
    }

    100% {
        opacity: 0.9;
    }
}

@keyframes pulsingColorLoading {
    0% {
        background-color: colors.$color-primary-variant;
    }

    50% {
        background-color: lighten(colors.$color-primary-variant, 2%);
    }

    100% {
        background-color: lighten(colors.$color-primary-variant, 0.5%);
    }
}
