@use "../../../settings/common/shadows";

// Adds Iris like focus
// {$postition: bottom}adds only bottom line. E.g. Editable <h1>
// {$paddingBottom: ...} with {$position: bottom} adds additional spacing between the bottom line and the text of the input
// {$hover: true}adds border when the input is overed. E.g. focus on Rich text
// {$native: false}adds the outline on the element itself not using native :focus
@mixin focusable($position: all, $paddingBottom: null, $hover: false, $native: true) {
    $hover-shadow: shadows.$input-box-shadow-hover;
    $focus-shadow: shadows.$input-box-shadow-focus;

    @if ($position == bottom) {
        $hover-shadow: shadows.$input-box-shadow-bottom-hover;
        $focus-shadow: shadows.$input-box-shadow-bottom-focus;
    }

    @if ($paddingBottom) {
        padding-bottom: $paddingBottom;
    }

    @if ($native) {
        @if ($hover == true) {
            &:hover {
                box-shadow: $hover-shadow;
            }
        }
    
        &:focus {
            box-shadow: $focus-shadow;
        }
    }

    @else {
        box-shadow: $focus-shadow;
    }
}
