@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/common/colors";
@use "../settings/components/typography";
@use "../tools/mixins/base/text";

.#{theme.$selector-prefix}-tabs {
    display: flex;
    justify-content: center;

    &.-large {
        @include text.size-large;
    }

    &__item {
        margin-left: 20px;
        margin-right: 20px;
        position: relative;

        &.-selected {
            padding-bottom: 5px;

            &,
            &:hover,
            &:active,
            &:focus {
                color: colors.$color-primary-invert;
                border-bottom: 2px solid colors.$color-secondary-darkest;
            }

            .sf-button.-link {
                color: colors.$color-primary-invert;
            }
        }
    }

    &__badge {
        position: absolute;
        top: 0;
        right: -27px;
        font-size: typography.$font-size-base;
    }
}

/*
  Default Tabstrip

  `.#{theme.$selector-prefix}-tabs`

      Markup:
      <div class="#{theme.$selector-prefix}-tabs">
           <a class="#{theme.$selector-prefix}-tabs__item">Recent</a>
           <a class="#{theme.$selector-prefix}-tabs__item -selected">Libraries</a>
           <a class="#{theme.$selector-prefix}-tabs__item">Selected <span class="#{theme.$selector-prefix}-tabs__badge sf-badge">0</span></a>
      </div>

      Styleguide: Tabstrip.default
 */
