@use "../common/colors";

//Dropdown
$dropdown-color: colors.$color-primary-invert;
$dropdown-min-width: 200px;
$dropdown-padding: 10px;
$dropdown-box-shadow: 0 3px 5px rgba(colors.$color-secondary-lighter, 0.5);
$dropdown-border-radius: 0;
$dropdown-white-space: nowrap;
$dropdown-max-height: calc(100vh - 46px); //100vh - $top-toolbar-height

//Dropdown item
$dropdown-item-margin-bottom: 1px;
$dropdown-item-padding: 3px;
$dropdown-item-padding-horizontal-small: 10px;
$dropdown-item-padding-horizontal: 20px;
$dropdown-item-padding-vertical-large: 30px;
$dropdown-item-disabled-color: mix(colors.$color-primary, colors.$color-primary-invert, 60);
$dropdown-item-separator-color: colors.$color-secondary-lightest;
$dropdown-item-description-line-height: 1;
$dropdown-item-counter-margin-left: 30px;
$dropdown-item-counter-positive: var(--color-success);
$dropdown-item-counter-neutral: var(--dropdown-item-counter-neutral);
$dropdown-item-heading-padding-top: 10px;
$dropdown-item-heading-padding-bottom: 8px;
$dropdown-item-separator-margin-top: 10px;
$dropdown-item-separator-padding-top: 10px;
$dropdown-item-notification-margin-top: 5px;
$dropdown-item-notification-margin-bottom: 15px;

//Dropdown condensed
$dropdown-condensed-vertical-padding: 0;

//Dropwdown color picker
$dropdown-color-picker-width: 231px;
$dropdown-color-picker-padding: 4px;

//Colors
$dropdown-bg: colors.$color-primary;
$dropdown-hover-bg: colors.$color-primary-variant;
$dropdown-transparent-hover-bg: transparent;
$dropdown-active-bg: colors.$color-secondary-lightest;
$dropdown-border: var(--dropdown-border);
