@use "colors";

$input-box-shadow-focus: 0 0 2px rgba(colors.$color-info, 0.8);
$input-box-shadow-bottom-focus: 0 3px 2px -3px rgba(colors.$color-info, 0.8);
$input-box-shadow-hover: 0 0 0 1px var(--input-box-shadow-hover);
$input-box-shadow-bottom-hover: 0 3px 0 -2px var(--input-box-shadow-hover);
$toolbar-shadow-color: rgba(237, 237, 238, 5000%);
$toolbar-shadow: 0 3px 5px $toolbar-shadow-color;
