@use "typography";

$select-height: 36px;
$select-font-size: typography.$font-size-lg;
$select-arrow-right-position: 10px;
$select-arrow-font-size: var(--icon-size-m);
$select-arrow-color: var(--select-arrow-color);
$select-arrow-code: "f0d7";
$select-item-height: 100%;
$select-item-width: 100%;
$select-item-horizontal-spacing: 10px;
$select-sm-height: 26px;
$select-sm-font-size: typography.$font-size-base;
$select-sm-line-height: 1.8;
$select-item-sm-horizontal-spacing: 6px;
$select-arrow-sm-font-size: var(--icon-size-xs);
$select-arrow-sm-right-position: 6px;
