@use "../settings/components/tree";
@use "../settings/components/tree-list";
@use "../settings/components/sidebar";
@use "../settings/components/status";
@use "../settings/components/typography";
@use "../tools/mixins/components/tree" as treeMixins;
@use "../tools/mixins/base/list-indent";
@use "../tools/mixins/base/text";
@use "../tools/mixins/base/circle";
@use "../settings/common/zindex";
@use "../settings/common/colors";
@use "sass:color";
@use "sass:math";

$pageGridMinWidth: 1010px;

.-sf-virtualized__tree {
    // 100 viewport width - paddings
    transition: width sidebar.$animation-time sidebar.$animation-timing;
    width: Max(#{$pageGridMinWidth}, calc(100vw - 2*#{tree-list.$tree-list-padding-horizontal} - 10px));
    border-top: tree-list.$tree-list-border-width solid tree-list.$tree-list-border-color;

    .-shrink & {
        width: Max(#{$pageGridMinWidth}, calc(100vw - 2*#{tree-list.$tree-list-padding-horizontal} - #{sidebar.$sidebar-width} - 10px));
    }

    tree-viewport {
        height: 100%;
        display: block;
    }

    .angular-tree-component {
        width: 100%;
        position: relative;
        display: inline-block;
    }

    tree {
        display: inline;
    }

    .sf-status__text {
        @include text.ellipsis;
    }

    .tree-children:not(:focus-within) {
        overflow: hidden;
    }
}

tree {
    display: block;
}

.sf-tree {
    position: relative;
    width: 100%;
    text-align: left;

    .tree-node-active > tree-node-wrapper {
        > .node-wrapper {
            &,
            &:hover,
            &:focus {
                background-color: tree.$tree-active-bg;
            }
        }
    }

    &:not(.-no-hover) {
        .node-wrapper {
            position: relative;
            display: flex;

            &:hover,
            &:focus {
                background-color: tree.$tree-hover-bg;
            }
        }
    }

    &:not(.-sidebar) {
        @include list-indent.generate-list-row-indent(".tree-children", "tree-node-expander", tree-list.$tree-list-child-left-indent);
    }

    .node-content-wrapper {
        flex: 1 1 0;
        display: block;
        min-width: 0;
    }

    .toggle-children-wrapper,
    .toggle-children-placeholder {
        display: block;
        margin-left: -#{tree-list.$tree-list-expander-margin-horizontal};
        margin-right: -#{tree-list.$tree-list-expander-margin-horizontal};
        padding: tree-list.$tree-list-expander-margin-top tree-list.$tree-list-expander-margin-horizontal;
        position: relative;
        min-width: tree-list.$tree-list-expander-flat-width;
        z-index: 1;
    }

    .toggle-children-wrapper {
        @include text.font-awesome;

        font-size: var(--icon-size-s);
        line-height: inherit;
        vertical-align: top;
        text-align: right;
        color: tree-list.$tree-list-expander-color;
        cursor: pointer;
    }

    .toggle-children-wrapper-collapsed > .toggle-children {
        &::before {
            content: "\f0da";
        }
    }

    .toggle-children-wrapper-expanded > .toggle-children {
        &::before {
            content: "\f0d7";
        }
    }

    &__row {
        display: flex;
        width: 100%;

        &.-no-row-hover:hover {
            background-color: colors.$color-primary;
        }

        &.-inverted {
            color: tree-list.$tree-sidebar-active-color;
        }
    }

    &__outer {
        position: absolute;
        left: 0;
        //margin-right: -$tree-items-space-between;
        padding-top: tree.$tree-items-outer-padding-top;
        text-align: center;
        z-index: zindex.$zindex-sm;
    }

    &:not(.-flat) {
        .sf-drag-handle {
            display: block;
            margin-top: -(tree-list.$tree-list-offset-adjustment-cell * 2);
        }
    }

    &__drop-not-allowed {
        display: none;
    }

    &__inner {
        @include treeMixins.tree-inner;

        &.-large {
            padding: tree.$tree-items-large-space-between;
        }

        &.-inverted {
            color: tree-list.$tree-sidebar-active-color;
        }

        &.-loading {
            padding: 0;

            &:hover {
                &::after {
                    content: none;
                }
            }
        }

        &.-no-pointer {
            cursor: default;
        }

        &.-active {
            background-color: tree.$tree-active-bg;
        }

        &.-active-light {
            background-color: color.scale(tree.$tree-active-bg, $lightness: 50%);

            &:hover {
                background-color: color.scale(tree.$tree-active-bg, $lightness: 20%);
            }
        }

        &.-link {
            padding-top: tree.$tree-items-space-between;
            padding-bottom: tree.$tree-items-space-between;
            padding-left: tree.$tree-items-space-between;
            color: tree.$tree-link-color;

            &:hover {
                color: colors.$color-primary-invert;
            }
        }

        &.-with-check {
            &:hover {
                .sf-tree__check {
                    visibility: visible;
                    color: colors.$color-secondary;

                    &:hover {
                        color: colors.$color-secondary-darkest;
                    }
                }
            }
        }
    }

    &__content,
    &__description {
        @include text.overflow-ellipsis;

        line-height: normal;

        &.-no-ellipsis {
            white-space: normal;
            overflow: auto;
        }
    }

    &__description {
        font-size: tree.$tree-items-description-font-size;
    }

    &__content,
    &__status,
    &__icon,
    &__avatar,
    &__right-align {
        position: relative;
        z-index: tree.$tree-items-zindex;
    }

    &__status {
        margin-right: tree.$tree-items-space-between;
        align-self: flex-start;
    }

    &__content {
        &.-no-status {
            margin-left: status.$status-diameter + tree.$tree-items-space-between;
        }
    }

    &__right-align,
    &__right-status {
        margin-left: auto;
        flex-shrink: 0;
    }

    &__right-align {
        margin-right: math.div(tree.$tree-items-space-between, 2);
        align-self: center;
    }

    &__right-status {
        padding: tree.$tree-items-space-between;
    }

    &__check {
        display: flex;
        justify-content: center;
        align-items: center;
        visibility: hidden;
        width: tree.$tree-items-large-check-width;
        height: tree.$tree-items-large-check-width;
        text-align: right;
        cursor: pointer;
    }

    &__action {
        font-size: typography.$font-size-base;

        &:hover {
            cursor: pointer;
        }
    }

    &__avatar {
        @include circle.create-circle(tree.$tree-avatar-dimensions);

        width: tree.$tree-avatar-dimensions;
        margin-right: tree.$tree-items-space-between;
        background-color: tree.$tree-initials-bg;
        color: tree.$tree-initials-color;
    }

    &__icon {
        margin-right: tree.$tree-items-space-between;

        + .sf-tree__content.-no-status {
            margin-left: 0;
        }
    }

    &__separator {
        border-top: tree-list.$tree-list-border-width solid tree.$tree-border-color;
        position: relative;
        width: 200%;
        left: -100%;
        z-index: zindex.$zindex-sm;
    }

    &__item-title {
        &.-heading {
            font-size: tree.$tree-items-title-font-size;
            color: tree.$tree-initials-color;
            text-transform: uppercase;
        }
    }

    &__item-btn {
        margin-left: tree.$tree-btn-padding;
        display: flex;
        align-items: center;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            width: tree.$tree-btn-padding;
            height: calc(100% + 2px);
            left: -#{tree.$tree-btn-padding};
            background: colors.$color-primary-variant;
        }
    }

    &.-checkbox {
        tree-node-checkbox {
            @include treeMixins.tree-checkbox;
        }

        &:not(.-main-list-view) {
            tree-node-checkbox {
                padding-top: 12px;
            }
        }

        .-inactive {
            position: relative;

            &::before {
                content: "";
                position: absolute;
                top: 0;
                left: -(tree.$tree-checkbox-wrapper-width);
                right: 0;
                bottom: 0;
                user-select: none;
            }
        }

        &:not(.-flat) .-inactive::before {
            left: -(tree.$tree-checkbox-wrapper-width + tree.$tree-hierarchical-overlay-width);
        }
    }

    &.-drag {
        .node-wrapper {
            padding-left: tree.$tree-drag-wrapper-width !important;
        }

        &.-sidebar {
            @include list-indent.generate-list-row-indent(".tree-children", ".node-wrapper", tree-list.$tree-list-sidebar-child-left-indent);

            .-not-draggable {
                margin-left: tree.$tree-drag-wrapper-width !important;

                &.-expandable {
                    margin-left: tree.$tree-drag-wrapper-width - tree-list.$tree-list-negative-expander-margin !important;
                }

                .node-wrapper {
                    padding-left: 0 !important;
                }
            }

            .node-wrapper {
                display: inline-flex;

                .node-content-wrapper {
                    border: 1px solid transparent;
                    border-radius: 3px;

                    &.is-dragging-over {
                        border-color: #000;
                    }
                }

                .toggle-children-wrapper {
                    width: 19px;
                }
            }

            .sf-tree__outer {
                padding: 5px 4px 0;
            }

            .node-drop-slot {
                margin-top: -5px;
                height: 5px;

                &.is-dragging-over {
                    visibility: visible;
                    height: 22px;
                    border: 1px solid #000;
                    border-radius: 3px;

                    &::before {
                        height: 22px;
                        background: transparent;
                    }
                }
            }

            .is-dragging-over-disabled {
                [hidden] {
                    display: block !important;
                }

                .sf-tree__drop-not-allowed {
                    display: block;
                }

                .sf-tree_drag-handle {
                    display: none;
                }
            }
        }

        .tree {
            padding-top: tree.$tree-drop-slot-height;

            > treenode:last-child > .tree-node-collapsed > treenodedropslot .node-drop-slot,
            > treenode:last-child > .tree-node-expanded > treenodedropslot .node-drop-slot {
                bottom: -#{tree.$tree-drop-slot-height};
            }
        }

        .node-drop-slot {
            display: block;
            position: relative;
            margin-top: -#{tree.$tree-drop-slot-placeholder};
            height: tree.$tree-drop-slot-placeholder;
            z-index: zindex.$zindex-lg;
            border: 0;

            &::before {
                position: absolute;
                display: block;
                content: "";
                top: calc(#{tree.$tree-drop-slot-placeholder} / 2);
                width: 100%;
                height: calc(#{tree.$tree-drop-slot-placeholder} / 2);
            }

            &.is-dragging-over {
                &::before {
                    background-color: colors.$color-primary-invert;
                }
            }
        }

        &:not(.-flat) {
            @include list-indent.generate-reorder-list-row-indent("tree-node-children", ".node-drop-slot");

            .sf-tree__outer.-sf-just-added {
                height: 100%;
                padding-right: calc(100% - #{tree.$tree-drag-wrapper-width});
            }

            .is-dragging-over {
                .sf-tree__item-title {
                    background-color: #000;
                    color: #fff;
                }
            }
        }
    }

    &.-checkbox.-drag {
    }

    &.-flat {
        tree-node-expander {
            display: none;
        }

        .sf-tree__outer + .sf-tree__inner {
            margin-left: -#{tree.$tree-items-space-between};
        }

        &.-sidebar {
            .sf-tree__outer + .sf-tree__inner {
                margin-left: 0;
            }
        }

        .node-wrapper {
            .toggle-children-wrapper {
                padding: tree.$tree-toggle-wrapper-padding;
                margin: 0;
                color: colors.$color-primary-invert;
                min-width: 0;
            }
        }
    }

    &.-actions-on-hover {
        .sf-tree__action {
            visibility: hidden;
        }

        .sf-tree__inner:hover {
            .sf-tree__action {
                visibility: visible;
            }
        }
    }

    &.-condensed {
        .sf-tree__inner:not(.-link) {
            padding-top: tree.$tree-items-space-between-condensed;
            padding-bottom: tree.$tree-items-space-between-condensed;
        }

        .sf-tree__outer {
            padding-top: tree.$tree-items-outer-condensed-padding-top;
            height: calc(100% - #{tree-list.$tree-list-border-width});

            &.-tree-item {
                height: 36px;
            }
        }
    }

    &:not(.-no-separators) {
        border-top: tree-list.$tree-list-border-width solid tree-list.$tree-list-border-color;

        .node-wrapper {
            border-bottom: tree.$node-wrapper-border;
        }
    }

    &.-status {
        .toggle-children-wrapper,
        .toggle-children-placeholder {
            padding-top: tree-list.$tree-list-expander-status-margin-top;
        }
    }

    &.-text-status {
        .sf-tree__status {
            margin-top: 4px;
        }
    }

    &.-list {
        border-left: tree-list.$tree-list-border-width solid tree.$tree-border-color;
        border-right: tree-list.$tree-list-border-width solid tree.$tree-border-color;

        .sf-tree__file-extension {
            display: none;
        }
    }

    &.-read-only,
    .-read-only & {
        .node-wrapper {
            &:hover,
            &:focus {
                background-color: transparent;
            }
        }

        .tree-node-active,
        .tree-node-focused {
            > tree-node-wrapper > .node-wrapper {
                background-color: transparent;
            }
        }

        .sf-tree__inner {
            cursor: default;
        }
    }

    &.-drag,
    &.-status,
    &.-checkbox {
        .node-wrapper {
            &:hover,
            &:focus {
                .sf-tree__row.-system-link {
                    &::before {
                        background-color: tree.$tree-hover-bg;
                    }
                }
            }
        }

        .sf-tree__row.-system-link {
            &::before {
                display: block;
                content: "";
                position: absolute;
                left: 0;
                width: tree.$tree-checkbox-wrapper-width;
                height: 100%;
                background-color: colors.$color-primary;
                z-index: zindex.$zindex-md;
            }

            .sf-tree__icon {
                margin-left: auto;
                margin-right: 0;
                padding-right: 5px;
            }

            .sf-tree__icon,
            .sf-tree__content {
                cursor: pointer;
            }
        }
    }

    &.-main-list-view:not(.-flat) {
        tree-node-checkbox {
            padding-left: 10px;
            width: 43px;
        }

        .toggle-children-placeholder,
        .toggle-children-wrapper {
            margin-top: 4px;
            min-width: tree-list.$tree-list-expander-width;
        }
    }

    &.-sidebar {
        @include list-indent.generate-reorder-list-row-indent("tree-node-children", ".node-drop-slot", 18, 20, 40);

        .sf-tree__inner {
            padding: tree-list.$tree-sidebar-padding;
            border-radius: tree-list.$tree-sidebar-border-radius;
        }

        .tree-children {
            tree-node-expander {
                margin-left: 0 !important;
            }
        }

        tree-node-wrapper.-expandable {
            margin-left: -(tree-list.$tree-list-negative-expander-margin);
        }

        .node-wrapper {
            border-radius: tree-list.$tree-sidebar-border-radius;
            margin-bottom: tree-list.$tree-sidebar-margin-bottom;
        }

        .tree-node-focused > tree-node-wrapper > .node-wrapper,
        .node-wrapper:hover {
            display: inline-flex;
            background-color: tree-list.$tree-sidebar-hover-bg-color;
        }

        .tree-node-focused > tree-node-wrapper > .node-wrapper {
            &.-editable {
                display: inline-flex;
                background-color: tree-list.$tree-sidebar-active-bg-color;

                &:hover {
                    background-color: tree-list.$tree-sidebar-active-bg-color;
                }

                .toggle-children-wrapper {
                    color: tree-list.$tree-sidebar-active-color;
                }
            }
        }
    }

    &.-sidebar-hiarachical-nav {
        .sf-tree__inner {
            padding: 0;
        }

        .sf-tree__content,
        .sf-tree__right-align:not(:empty) {
            &:hover, &:focus {
                background-color: tree-list.$tree-sidebar-hover-bg-color;
            }
        }

        .sf-tree__content {
            flex: auto;
            padding: 10px;
        }

        .sf-tree__right-align {
            margin-right: 0;
            padding: 6px;
            min-width: 38px; //$button-height-sm + pl + pr
            height: 100%;
        }
    }
}

.sf-drag-handle,
.-sf-drag-handle {
    width: tree.$tree-drag-wrapper-width;
    font-size: tree.$tree-drag-handle-size;
    color: colors.$color-secondary;
    cursor: move;

    &:hover,
    &:focus {
        color: colors.$color-primary-invert;
    }
}

.-sf-tree-dragging {
    .sf-sticky,
    .sf-modal-window__buttons {
        display: none !important;
    }
}
