@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/typography";
@use "../tools/mixins/base/common" as commonMixins;

#{theme.$selector-app} {
    @include commonMixins.antialias;

    //needed for proper position of button dropdown in pages virtualized tree
    position: relative;
    font-family: typography.$font-family;
    font-size: typography.$font-size-base;
    line-height: typography.$line-height-base;
    color: typography.$font-color-base;
}
