@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/field";
@use "../settings/components/tree-common";
@use "../settings/components/tree-list";
@use "../settings/common/colors";
@use "../settings/components/typography";
@use "../settings/common/spacing";
@use "../tools/mixins/components/tree" as treeMixins;

.#{theme.$selector-prefix}-field {
    position: relative;

    &.-has-errors {
        .sf-field__input,
        .sf-field__input + .sf-input__unit,
        .sf-field__input + .sf-input__unit::before,
        .k-datepicker .k-picker-wrap,
        .k-timepicker .k-picker-wrap {
            border-color: field.$field-error-color;
        }
    }

    &.-filter {
        margin-top: 3px;
        margin-bottom: 0;
        padding-top: 10px;
        padding-bottom: 0;
        border-top: 1px solid colors.$color-secondary-lighter;

        .sf-field__label {
            display: inline-block;
            font-weight: normal;
        }
    }

    &.-boolean-tooltip {
        display: flex;

        .sf-field__tooltip {
            margin-top: 5px;
            order: 2;
        }
    }

    &.-sequence {
        margin-bottom: 0;
        padding-top: 0;
        padding-bottom: 0;
    }

    &.-group {
        &:not(.-first) {
            padding-top: 0;
        }

        &:not(.-last) {
            padding-bottom: 5px;
        }
    }

    &__label {
        display: inline-block;
        font-size: field.$field-label-font-size;
        font-weight: typography.$font-weight-bold;

        &.-legend {
            float: left;
            margin-right: spacing.$spacing-4xs;
            margin-bottom: spacing.$spacing-4xs;
        }
    }

    &__content {
        display: block;
        clear: both;
        margin-top: 5px;
    }

    &__error {
        font-size: typography.$font-size-base;
        margin-top: 5px;
        color: field.$field-error-color;

        &.-underline {
            padding-top: 5px;
            border-top: 1px solid field.$field-error-color;
        }
    }

    &__hint {
        color: var(--secondary-text-color);
    }

    &__no-value {
        color: var(--secondary-text-color);
    }

    &__content,
    &__no-value {
        font-size: typography.$font-size-lg;
    }

    &__table {
        width: field.$field-table-width;
        table-layout: fixed;

        &.-middle {
            td {
                vertical-align: middle;
            }
        }

        .sf-field {
            padding-top: 10px;
            padding-bottom: 10px;
        }

        &.-has-hover tbody tr:hover {
            background-color: colors.$color-primary-variant;
        }

        &.-top-align {
            td {
                vertical-align: top;

                &:not(.-form-control) {
                    padding-top: field.$field-table-top-aliged-cell-vertical-padding;
                }

                &.-form-control {
                    sf-choice {
                        display: block;
                        padding-top: field.$field-table-cell-vertical-padding;
                    }
                }

                &.-input-check {
                    padding-top: field.$field-table-top-aliged-input-check-vertical-padding;
                }
            }
        }

        td {
            padding-top: field.$field-table-cell-vertical-padding;
            padding-bottom: field.$field-table-cell-vertical-padding;
            vertical-align: top;

            .sf-field {
                padding-top: 0;
                padding-bottom: 0;
                margin-bottom: 0;
            }

            &.-dragged {
                position: absolute;
                top: 0;
                bottom: 0;
                right: 0;
                left: 0;
                background-color: field.$field-table-row-dragged-bg-color;
            }
        }

        .-col-size-4xs {
            width: tree-list.$tree-list-cell-4xs;
        }

        .-col-size-3xs {
            width: tree-list.$tree-list-cell-3xs;
        }

        .-col-size-2xs {
            width: tree-list.$tree-list-cell-2xs;
        }

        .-col-size-s {
            width: tree-list.$tree-list-cell-s;
        }

        .-col-size-sm {
            width: tree-list.$tree-list-cell-sm;
        }

        .-col-size-m {
            width: tree-list.$tree-list-cell-m;
        }

        .-col-size-l {
            width: tree-list.$tree-list-cell-l;
        }

        .-col-size-lxl {
            width: tree-list.$tree-list-cell-lxl;
        }

        &.-col-size-3xl {
            width: tree-list.$tree-list-cell-3xl;
        }
    }

    td {
        vertical-align: top;
    }

    &__table-header {
        @include treeMixins.tree-common-header;

        padding-top: field.$field-table-header-vertical-padding;
        padding-bottom: field.$field-table-header-padding-bottom;

        &.-first {
            width: field.$field-table-horizontal-header-width;
        }

        &.-col-size-xs {
            width: tree-list.$tree-list-cell-xs;
        }

        &.-col-size-4xl {
            width: tree-list.$tree-list-cell-4xl;
        }

        &.-col-size-xl {
            width: tree-list.$tree-list-cell-xl;
        }
    }

    &__table-row {
        position: relative;
        height: field.$field-table-row-height;

        &.-middle {
            td {
                vertical-align: middle;
            }
        }

        &:not(.-additional) {
            border-bottom: 1px solid tree-common.$tree-common-border-color;
        }

        &.-additional {
            height: field.$field-table-row-height - field.$field-table-cell-vertical-padding;

            td {
                padding-bottom: 0;
            }
        }

        &.-head {
            height: auto;
        }
    }
}
