@use "../../../settings/components/chip";
@use "../../../settings/common/colors";
@use "../../../settings/common/zindex";

@mixin chip-identifier() {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    margin-top: chip.$chip-identifier-position-top;
    margin-left: chip.$chip-identifier-position-left;
    margin-right: chip.$chip-identifier-position-right;
    width: chip.$chip-identifier-dimenssion;
    height: chip.$chip-identifier-dimenssion;
    line-height: chip.$chip-identifier-dimenssion;
    text-align: center;
    border-radius: 50%;
    background-color: colors.$color-primary;
    color: colors.$color-secondary-darkest;
    z-index: zindex.$zindex-sm;
    max-width: 100%;

    &.-transparent {
        background-color: transparent;
    }
}
