@use "../settings/common/spacing";
@use "../settings/components/tree";
@use "../settings/components/tree-list";
@use "../settings/components/typography";
@use "../tools/mixins/base/list-indent";
@use "../tools/mixins/base/image";
@use "../tools/mixins/components/tree" as treeMixins;
@use "../settings/themes/sitefinity/base" as theme;

$responseColWidth: 80px; 
$checkboxColWidth: 33px;
$responseActionsColWidth: 60px;
$responseGridHorizontalSapcing: 120px;

.#{theme.$selector-prefix}-tree-list {
    &.-medium {
        min-width: tree-list.$tree-list-width-medium;
    }

    &.-normal {
        min-width: tree-list.$tree-list-width-normal;
    }

    &.-center-vertical {
        .sf-tree-list__cell:not(.-head, .-group, .-action, .-media, .-translation, .-top-align) {
            padding-top: tree-list.$tree-list-vertical-spacing-cell + tree-list.$tree-list-top-offset-cell;
            padding-bottom: tree-list.$tree-list-top-offset-cell - (tree-list.$tree-list-offset-adjustment-cell * 2);
        }

        .sf-tree-list__cell.-translation:not(.-head) {
            padding-top: tree-list.$tree-list-vertical-spacing-cell + tree-list.$tree-list-top-offset-cell - tree-list.$tree-list-offset-adjustment-cell;
            padding-bottom: tree-list.$tree-list-top-offset-cell - tree-list.$tree-list-offset-adjustment-cell;
        }

        tree-node-checkbox {
            margin-top: tree-list.$tree-list-top-offset-cell;
        }

        tree-node-expander {
            margin-top: tree-list.$tree-list-top-offset-cell;
        }
    }

    &.-bulk {
        .sf-tree-list__row {
            padding-left: 0;
        }

        .sf-tree-list__cell {
            padding-left: tree-list.$tree-list-horizontal-spacing-bulk;
            padding-right: tree-list.$tree-list-horizontal-spacing-bulk;
        }
    }

    &.-form-responses {
        width: calc(100vw - $responseGridHorizontalSapcing);
        overflow-x: auto;
        overflow-y: visible;
        position: static;
        min-height: 99px;

        .sf-tree {
            position: static;
        }

        &:not(.-no-hover) .node-wrapper {
            position: static;
        }

        .sf-tree-list__row.-head > .sf-tree-list__cell:first-child,
        .sf-tree-list__row > .sf-tree-list__cell:first-child,
        .sf-tree.-checkbox tree-node-checkbox,
        .sf-tree-list__cell.-action {
            position: absolute;
            background-color: #fff;
        }

        .sf-tree-list__row > .sf-tree-list__cell:first-child,
        .sf-tree-list__cell.-action {
            height: calc(var(--row-height) * 1px);
        }

        .sf-tree.-checkbox tree-node-checkbox {
            z-index: 20;
        }

        .sf-tree-list__row:not(.-head) {
            cursor: pointer;
        }

        .sf-tree-list__row.-head {
            &::before {
                content: "";
                display: block;
                background-color: #fff;
                position: absolute;
                top: 0;
                left: -33px;
                height: 24px;
                width: 70px;
            }
        }

        .node-wrapper {
            &::before {
                content: "";
                display: block;
                background-color: #eee;
                position: absolute;
                left: 0;
                height: 1px;
                width: 100%;
                margin-top: -1px;
            }
        }

        .sf-tree-list__cell.-action {
            right: 0;
            width: $responseActionsColWidth;
        }

        .sf-tree-list__cell:first-child.-head {
            width: $responseColWidth;
        }

        .sf-tree-list__cell:first-child:not(.-head) {
            padding-left: $checkboxColWidth + 10px;
            width: $checkboxColWidth + $responseColWidth;
        }

        .sf-tree-list__cell:nth-child(2).-head {
            margin-left: $responseColWidth + 1;
        }

        .sf-tree-list__cell:nth-child(2):not(.-head) {
            margin-left: $responseColWidth + 35px;
        }

        .sf-tree-list__cell:nth-last-child(2):not(.-head) {
            padding-right: $responseActionsColWidth;
        }

        .node-wrapper tree-node-checkbox {
            padding-top: 6px;
        }

        .sf-tree:not(.-no-hover) .node-wrapper:hover .sf-tree-list__cell,
        .sf-tree:not(.-no-hover) .node-wrapper:focus .sf-tree-list__cell,
        .sf-tree:not(.-no-hover) .node-wrapper:hover tree-node-checkbox,
        .sf-tree:not(.-no-hover) .node-wrapper:focus tree-node-checkbox {
            background-color: #f5f5f5;
        }

        .sf-tree:not(.-no-hover) .tree-node-active > tree-node-wrapper > .node-wrapper .sf-tree-list__cell,
        .sf-tree:not(.-no-hover) .tree-node-active > tree-node-wrapper > .node-wrapper tree-node-checkbox {
            background-color: #dcecf5;
        }
    }

    &__row {
        display: flex;
        width: 100%;

        &.-border {
            border-bottom: 1px solid tree-list.$tree-list-border-color;
        }

        &.-head {
            @include treeMixins.tree-common-header;

            padding-left: tree.$tree-checkbox-wrapper-width;

            + .sf-tree-list__row.-loading {
                border-top: 1px solid tree-list.$tree-list-border-color;
            }

            .-title {
                min-width: tree-list.$tree-list-cell-m;
            }

            .-two-lines {
                display: flex;
                align-items: flex-end;
                white-space: normal;
            }
        }

        &.-active {
            background-color: tree-list.$tree-list-active-bg-color;
        }

        &.-loading {
            border-bottom: 1px solid tree-list.$tree-list-border-color;

            .sf-tree-list__cell {
                padding: 0;
                width: 100%;

                &:hover {
                    cursor: default;
                    background-color: transparent;
                }
            }
        }

        &.-no-pointer {
            .sf-tree-list__cell {
                cursor: inherit;

                &.-title {
                    cursor: inherit;
                }
            }
        }

        &.-disabled {
            &::before {
                display: block;
                content: "";
                height: 100%;
                position: absolute;
                z-index: 300;
                left: 0;
                right: 0;
            }
        }

        &.-disable-checkbox {
            &::before {
                display: block;
                content: "";
                height: 100%;
                width: 33px;
                position: absolute;
                z-index: 300;
                left: 0;
                right: 0;
            }
        }
    }

    &__cell {
        padding: tree-list.$tree-list-vertical-spacing-cell tree-list.$tree-list-horizontal-spacing;
        word-break: break-all;

        &:not(.-title) {
            flex: none;
            width: tree-list.$tree-list-cell-s;
        }

        &.-head {
            vertical-align: top;
            padding: tree-list.$tree-list-vertical-spacing-head tree-list.$tree-list-horizontal-spacing;
            white-space: nowrap;
            text-align: left;
            font-weight: normal;

            &.-status {
                padding-left: tree-list.$tree-list-head-cell-title-spacing-left;

                &.-toggle {
                    padding-left: tree-list.$tree-list-head-cell-title-spacing-left + tree-list.$tree-list-head-cell-title-toggle-spacing - tree-list.$tree-list-expander-margin-horizontal;
                }

                &.-thumbnail {
                    padding-left: tree-list.$tree-list-head-cell-title-thumbnail-spacing;
                }
            }

            &.-toggle {
                padding-left: tree-list.$tree-list-head-cell-title-toggle-spacing;
            }

            &:not(.-action, .-with-tooltip, .-two-lines) {
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        &.-title:not(.-head) {
            width: 100%;

            &:not(.-no-pointer) {
                cursor: pointer;
            }
        }

        &.-title {
            margin-right: auto;
            min-width: tree-list.$tree-list-cell-m;
        }

        &.-action {
            padding-right: spacing.$spacing-4xs;
            text-align: right;
        }

        &.-group {
            position: absolute;
            top: tree-list.$tree-list-group-letter-offset;
            left: -(tree-list.$tree-list-group-letter-offset * 2);
            display: flex;
            padding: 0;
            width: auto;
        }

        &.-fluid {
            width: auto;
        }

        &.-fill {
            flex: 1;
        }

        &.-sf-simple-media {
            &:not(.-document, .-video) {
                padding-right: 10px;
                width: 90px;

                sf-image {
                    display: block;
                    width: tree-list.$tree-list-media-height;
                    height: tree-list.$tree-list-media-height;

                    img {
                        @include image.fit-cover;
                    }
                }
            }

            &.-document {
                sf-document {
                    display: flex;

                    .sf-media__title {
                        margin-left: tree-list.$tree-list-vertical-spacing-cell;
                        font-size: typography.$font-size-base;
                    }
                }
            }

            &.-video {
                sf-image {
                    display: block;

                    img {
                        width: 100%;
                    }
                }
            }
        }

        &.-sf-short-text,
        &.-sf-number,
        &.-sf-yes-no-choice,
        &.-sf-simple-media.-document {
            width: tree-list.$tree-list-cell-m;
        }

        &.-sf-text-area,
        &.-sf-related-data,
        &.-sf-taxa {
            width: tree-list.$tree-list-cell-xl;
        }

        &.-sf-date-time {
            width: tree-list.$tree-list-cell-3xl;
        }

        &.-sf-media {
            width: tree-list.$tree-list-cell-media;

            &.-wide {
                width: tree-list.$tree-list-cell-media-wide;
            }
        }
        // column width is used in dynamic-item-index.component.ts to determine whether list has many columns or not
        &.-col-size-4xs {
            width: tree-list.$tree-list-cell-4xs;
        }

        &.-col-size-3xs {
            width: tree-list.$tree-list-cell-3xs;
        }

        &.-col-size-2xs {
            width: tree-list.$tree-list-cell-2xs;
        }

        &.-col-size-xs,
        &.-sf-simple-media.-video {
            width: tree-list.$tree-list-cell-xs;
        }

        &.-col-size-s {
            width: tree-list.$tree-list-cell-s;
        }

        &.-col-size-sm {
            width: tree-list.$tree-list-cell-sm;
        }

        &.-col-size-m {
            width: tree-list.$tree-list-cell-m;
        }

        &.-col-size-l {
            width: tree-list.$tree-list-cell-l;
        }

        &.-col-size-xl {
            width: tree-list.$tree-list-cell-xl;
        }

        &.-col-size-2xl {
            width: tree-list.$tree-list-cell-2xl;
        }

        &.-col-size-3xl {
            width: tree-list.$tree-list-cell-3xl;
        }

        &.-col-stretched {
            flex-grow: 1;
        }
    }

    &__controlls {
        &:not(:empty) {
            min-height: tree-list.$tree-list-controls-height;

            + .sf-tree-list__controlls:not(.-out-of-view) {
                min-height: auto;
            }
        }
    }

    &__back {
        margin-top: -19px;
        position: relative;
    }

    &__status {
        min-width: tree-list.$tree-list-status-min-width;
    }

    @include list-indent.generate-list-title-min-width(".tree-children", ".sf-tree-list__cell.-title", tree-list.$tree-list-child-left-indent, tree-list.$tree-list-cell-m);
}

.sf-form-responses-wrapper {
    position: relative;
    width: 100%;
    width: calc(100vw - $responseGridHorizontalSapcing);
    overflow-x: hidden;
    overflow-y: visible;
    padding-bottom: 120px;
    margin-bottom: -120px;
    
    + .sf-main__paging {
        position: relative;
        z-index: 1;
    }
}
