@use "../common/colors";
@use "button";
@use "sass:math";

$notification-types: (
    info: (
        colors.$color-info-bg /* background color */
    ),
    success: (
        colors.$color-success-bg /* background color */
    ),
    error: (
        colors.$color-error-bg /* background color */
    ),
    warning: (
        colors.$color-warning-bg /* background color */
    ),
    "black": (
        colors.$color-primary-invert,
        colors.$color-primary
    ),
    "transparent": (
        transparent
    )
) !default;

//Dimensions
$notification-padding: 10px 9px;
$notification-border-radius: 3px;

//Color
$notification-bg: colors.$color-info-bg;
$notification-color: colors.$color-primary-invert;

//Arrow
$notification-arrow-size: 10px;
$notification-arrow-border-size: 1px;
$notification-arrow-shadow-color: colors.$color-secondary-lighter;
$notification-arrow-margin: $notification-arrow-size * 2;

//Tooltip
$notification-arrows-positions: (
    "up": top,
    "down": bottom,
    "left": left,
    "right": right,
    "down-left": bottom,
    "down-right": bottom,
    "up-left": top,
    "up-right": top,
    "left-down": left,
    "left-up": left,
    "right-down": right,
    "right-up": right
);
$notification-arrow-tooltip-padding: 20px;
$notification-arrow-tooltip-border-radius: 0;
$notification-tooltip-box-shadow: 0 0 8px rgba(228, 228, 228, 60%);

//Close button
$notification-close-button-margin-left: 10px;
$notification-tooltip-close-button-top: 10px;
$notification-tooltip-close-button-right: $notification-tooltip-close-button-top;
$notification-close-button-hover-color: colors.$color-primary-invert;

//Faq
$notification-faq-margin-bottom: 5px;
$notification-faq-bottom: 100%;
$notification-faq-right: 0;
$notification-faq-width: 290px;

//Contextual help
$notification-help-padding: 30px;

//Toolset
$notification-toolset-height: 55px;
$notification-toolset-tool-offset: 20px;
$notification-toolset-tool-group-offset: math.div($notification-toolset-tool-offset, 2);
$notification-toolset-tool-inner-offset: 5px;
$notification-toolset-tool-separator-color: colors.$color-secondary-darkest;
$notification-toolset-tool-active-bg: colors.$color-secondary-darker;
$notification-toolset-dropdown-min-width: 200px;

//Notification fixed wrapper
$notification-wrapper-width: 100%;
$notification-wrapper-height: 0;
$notification-wrapper-position-top: 60px;
$notification-wrapper-position-left: 0;

//Narrow
$notification-narrow-width: 250px;

//Medium
$notification-medium-width: 350px;

//Small
$notification-sm-height: button.$button-height;
$notification-sm-padding: 0 20px;

//Separator
$notification-separator-padding-horizontal: 10px;

//Fixed
$notification-fixed-max-width: 850px;
