@use "../common/colors";

/// Base font family
$font-family: "Open Sans", sans-serif;
$font-family-title: "Open Sans Condensed", sans-serif;
$font-awesome: normal normal normal 14px/1 FontAwesome;
$font-weight-light: 300;
$font-weight-normal: normal;
$font-weight-semibold: 600;
$font-weight-bold: bold;
$font-size-h1-lg: var(--font-size-h1-lg);
$font-size-h1: var(--font-size-h1);
$font-size-h2: var(--font-size-h2);
$font-size-h3: var(--font-size-h3);
$font-size-h4-lg: var(--font-size-h4-lg);
$font-size-h4: var(--font-size-h4);
$font-size-h5: var(--font-size-h5);
$font-size-base: var(--font-size-base);
$font-size-lg: var(--font-size-lg);
$line-height-base: 1.4;
$line-height-heading: 1.3;
$line-height-px-base: calc(#{$font-size-base} * #{$line-height-base});
$font-color-base: colors.$color-primary-invert;
$color-h2: var(--color-h2);
$color-h5: var(--color-h5);
$headings: (
    h1: (font-family: $font-family-title, font-weight: $font-weight-bold, font-size: $font-size-h1, line-height: $line-height-heading, margin-top: 0, margin-bottom: 0),
    h2: (font-size: $font-size-h2, font-weight: $font-weight-light, line-height: $line-height-heading, color: $color-h2, margin-top: 0, margin-bottom: 0),
    h3: (font-family: $font-family-title, font-weight: $font-weight-bold, font-size: $font-size-h3, line-height: $line-height-heading, margin-top: 0, margin-bottom: 0),
    h4: (font-size: $font-size-h4, font-weight: $font-weight-bold, margin-top: 0, margin-bottom: 0),
    h5: (font-size: $font-size-h5, color: $color-h5, text-transform: uppercase, font-weight:normal, margin-top: 0, margin-bottom: 0)
) !default;
