@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/badge";
@use "../settings/components/typography";
@use "../tools/mixins/base/circle";

.#{theme.$selector-prefix}-badge {
    @include circle.create-circle(badge.$badge-diameter-m, badge.$badge-diameter-m, true);

    white-space: nowrap;
    font-style: normal;
    background-color: badge.$badge-color-bg;
    font-family: typography.$font-family;
    font-weight: normal;
    color: badge.$badge-color;

    &-group {
        position: relative;

        &__icon {
            position: absolute;
            top: -5px;
            right: -8px;
        }
    }

    &:not(sub, sup) {
        vertical-align: middle;
    }

    &:not(.-icon, .-add) {
        padding-left: badge.$badge-padding-h;
        padding-right: badge.$badge-padding-h;

        &.-floating-txt {
            padding-left: badge.$badge-floating-txt-padding-horizontal;
        }
    }

    &.-hover {
        cursor: pointer;
    }

    &.-size-s {
        @include circle.change-circle-diameter(badge.$badge-diameter-s, badge.$badge-diameter-s, true);
    }

    &.-size-m {
        @include circle.change-circle-diameter(badge.$badge-diameter-m, badge.$badge-diameter-m, true);
    }

    &.-size-l {
        @include circle.change-circle-diameter(badge.$badge-diameter-l, badge.$badge-diameter-l, true);
    }

    &.-size-xl {
        @include circle.change-circle-diameter(badge.$badge-diameter-xl, badge.$badge-diameter-xl, true);
    }

    &.-positive {
        background-color: badge.$badge-color-bg-positive;
        color: badge.$badge-positive-color;
    }

    &.-negative {
        background-color: badge.$badge-color-bg-negative;
        color: badge.$badge-negative-color;
    }

    &.-white {
        background-color: badge.$badge-color-bg-white;
        color: badge.$badge-white-color;
    }

    &.-icon {
        color: badge.$badge-icon-color;

        .#{theme.$selector-prefix}-icon {
            color: inherit;
            //TODO: move to CF
            margin-right: 0;
            display: inline-flex;
            justify-content: center;
            align-items: center;
        }
    }

    &.-add {
        background-color: badge.$badge-color-bg-white;
        border: 1px solid badge.$badge-white-border-color;
        color: badge.$badge-add-color;

        &.-hover {
            &:hover {
                background-color: badge.$badge-white-hover-bg;
            }
        }
    }

    &.-add-variation {
        border: 1px solid badge.$badge-add-variation-border-color;
        background-color: badge.$badge-add-variation-bg-color;
        color: badge.$badge-add-variation-color;

        &.-hover {
            &:hover {
                border: 1px solid badge.$badge-add-variation-border-hover;
                background-color: badge.$badge-add-variation-hover;
            }
        }

        &.-dimmed {
            border-color: #cae0f7;
            background-color: #cae0f7;
        }
    }

    &.-edit-variation {
        border: 1px solid badge.$badge-edit-variation-bg-color;
        background-color: badge.$badge-edit-variation-bg-color;
        color: badge.$badge-edit-variation-color;

        &.-hover {
            &:hover {
                border: 1px solid badge.$badge-edit-variation-hover;
                background-color: badge.$badge-edit-variation-hover;
            }
        }

        &.-dimmed {
            border-color: #80b6ec;
            background-color: #80b6ec;
        }
    }

    &.-floating-txt {
        justify-content: left;

        &:hover {

            .#{theme.$selector-prefix}-badge__animated-content {
                display: inline-block;
                animation: badge.$badge-floating-txt-animation;
            }
        }
    }

    &.-square {
        border-radius: 0;
    }

    &.-bordered {
        border: 1px solid currentColor;
    }
}

/*
  Default Badge

  `.#{theme.$selector-prefix}-badge`

  Markup:
  <i class="#{theme.$selector-prefix}-badge">5</i> Default diameter size 22px and default color grey <br />
  <i class="#{theme.$selector-prefix}-badge -hover">5</i> Default with hover <br />

  Styleguide: Badge.default
*/

/* Badge Size

 `.#{theme.$selector-prefix}-badge.-size-{s,m,l,xl}`

 Markup:
 <i class="#{theme.$selector-prefix}-badge -size-s">5</i> Size s: 16px diameter <br />
 <i class="#{theme.$selector-prefix}-badge -size-m">5</i> Size m: 22px diameter (Default) <br />
 <i class="#{theme.$selector-prefix}-badge -size-l">5</i> Size l: 24px diameter <br />
 <i class="#{theme.$selector-prefix}-badge -size-xl">5</i> Size xl: 26px diameter <br />

 Styleguide: Badge.size
*/

/* Badge Color

 `.#{theme.$selector-prefix}-badge.-{positive,negative,white}`

 Markup:
 <i class="#{theme.$selector-prefix}-badge">0</i> Default background color <br /><br />
 <i class="#{theme.$selector-prefix}-badge -positive">5</i> Positive background color <br /><br />
 <i class="#{theme.$selector-prefix}-badge -negative">5</i> Negative background color <br /><br />
 <span style="padding: 5px; background-color: #000; color: #fff;"><i class="#{theme.$selector-prefix}-badge -white -icon"><i class="sf-fa sf-fa-check-circle #{theme.$selector-prefix}-icon -size-m"></i></i> White background color used for icons</span> <br />

 Styleguide: Badge.color
*/

/*
 Badge for Languages

 `.#{theme.$selector-prefix}-badge.-{add, -add-variation, -edit-variation}`

 Markup:
 <i class="#{theme.$selector-prefix}-badge -add">5</i> White badge with border and hover <br />
 <i class="#{theme.$selector-prefix}-badge -add -hover">5</i> White badge with border <br /><br />
 <i class="#{theme.$selector-prefix}-badge -add-variation -#{theme.$selector-prefix}-txt-small">EN</i> Light blue background background color <br />
 <i class="#{theme.$selector-prefix}-badge -add-variation -hover -#{theme.$selector-prefix}-txt-small">EN</i> Light blue background background color and hover <br /><br />
 <i class="#{theme.$selector-prefix}-badge -edit-variation -#{theme.$selector-prefix}-txt-small">BG</i> Dark blue background background color <br />
 <i class="#{theme.$selector-prefix}-badge -edit-variation -hover -#{theme.$selector-prefix}-txt-small">BG</i> Dark blue background background color and hover <br /><br />
 <i class="#{theme.$selector-prefix}-badge -add-variation -floating-txt -#{theme.$selector-prefix}-txt-small"><span class="#{theme.$selector-prefix}-badge__animated-content">Bulgaria on three seas</span></i> Badge with floating text

 Styleguide: Badge.languages
*/

/*
 Badge with Icon

 `.#{theme.$selector-prefix}-badge.-icon`

 Markup:
 <i class="#{theme.$selector-prefix}-badge -white -icon"><i class="sf-fa sf-fa-question-circle #{theme.$selector-prefix}-icon"></i></i> Default badge size and default icon size <br />
 <i class="#{theme.$selector-prefix}-badge -white -icon"><i class="sf-fa sf-fa-check-circle #{theme.$selector-prefix}-icon -size-l"></i></i> Default badge size and L icon size <br />
 <i class="#{theme.$selector-prefix}-badge -white -icon -size-s"><i class="sf-fa sf-fa-info-circle #{theme.$selector-prefix}-icon -size-s"></i></i> Badge size S and icon size S <br />

 Styleguide: Badge.icon
*/
