@use "../vendors/sitefinity/framework";

.#{framework.$selector-prefix}-zone-toolset {
    display: inline-block;
    border-radius: 3px;
    background-color: framework.$color-primary-invert;
    min-width: 320px;
    font-size: framework.$font-size-base;
    color: framework.$color-primary;
    overflow: hidden;

    &__content {
        padding: 20px;
    }

    &__label {
        display: block;
        margin-bottom: 5px;
        text-transform: uppercase;
        color: framework.$color-primary;
        font-size: 10px;
    }

    &__tab {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 15px;
        font-size: 13px;
        font-weight: bold;
        cursor: pointer;
        color: #fff;
        background: #4d4d4d;

        &.-selected {
            cursor: default;
            background: #333;
        }
    }

    input[type="number"] {
        appearance: textfield;
    }

    .sf-expander {
        margin-bottom: 0;

        &__header {
            display: inline-block !important;
        }

        &__icon {
            background: transparent;
            border: 0;
            margin-top: -5px;

            .sf-button__icon {
                color: framework.$color-primary;
            }

            &:hover {
                background: transparent;
                border: 0;
                color: framework.$color-primary;
                transition: none;
            }
        }
    }

    .sf-expander__checkbox,
    .sf-checkbox__input,
    .sf-radio__input {
        display: inline-flex !important;
    }
}
