$color-primary: #fff !default;            //white
$color-primary-variant: #f5f5f5 !default;         //light grey
$color-primary-invert: #333 !default;             //black
$color-primary-invert-variant: #000 !default;             //black

$color-secondary-lightest: #eee !default;
$color-secondary-lighter: #e4e4e4 !default;
$color-secondary-lighter-contrast: #cbcbcb !default;
$color-secondary: #ccc !default;                  //grey
$color-secondary-contrast: #909090 !default;
$color-secondary-darker: #999 !default;
$color-secondary-darker-contrast: #696969 !default;
$color-secondary-darkest: #777 !default;
$color-secondary-kendo: #656565 !default;
$color-secondary-inline-edit: #4d4d4d !default;
$color-success: #38ab63 !default;       //green
$color-success-contrast: #0e874c !default;
$color-info: #006cd9 !default;    //dark blue
$color-info-light: lighten(desaturate($color-info, 25.29), 40.39);
$color-info-contrast: #005fcc !default;    //dark blue
$color-error: #ff4848 !default;        //red
$color-error-contrast: #d40808 !default;
$color-warning: #ed8527 !default;        //orange
$color-warning-contrast: #ff8151 !default;
$color-success-bg: #caeed8 !default;            //light green
$color-info-bg: #dcecf5 !default;               //light blue
$color-error-bg: #fcc !default;              //light red
$color-warning-bg: #ffc !default;               //light yellow
