@mixin create-circle($diameter, $border-radius: 50%, $ellipse: false) {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: $diameter;
    height: $diameter;
    border-radius: $border-radius;
    overflow: hidden;

    @if ($ellipse) {
        @include add-ellipse-modifier($diameter);
    }
}

@mixin change-circle-diameter($diameter, $border-radius: null, $ellipse: false) {
    width: $diameter;
    height: $diameter;

    @if ($border-radius) {
        border-radius: $border-radius;
    }

    @if ($ellipse) {
        @include add-ellipse-modifier($diameter);
    }
}

@mixin add-ellipse-modifier($diameter) {
    &.-ellipse {
        width: auto;
        min-width: $diameter;
    }
}
