@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/checkbox";
@use "../settings/components/radio";
@use "../tools/mixins/base/text";
@use "../tools/mixins/components/choice";

.#{theme.$selector-prefix}-radio {
    position: relative;
    display: block;

    &.-inline {
        display: inline-block;
    }

    &__label {
        @include choice.choice-label;
    }

    &__description {
        @include text.size-base;

        margin-left: checkbox.$checkbox-label-margin-left + radio.$radio-input-icon-size;
        margin-bottom: checkbox.$checkbox-description-margin-bottom;
        color: var(--secondary-text-color);
    }

    &__input {
        @include choice.choice-input(50%, radio.$radio-input-icon-size);
    }

    &__icon {
        @include choice.radio-icon;
    }

    &.-ellipsis {
        @include choice.choice-ellipsis;
    }
}

/*
  Default Radio Button

  `.#{theme.$selector-prefix}-radio`

      Markup:
 <div class="#{theme.$selector-prefix}-radio">
      <input type="radio" class="#{theme.$selector-prefix}-radio__input" name="navigation" id="radio_1"/>
      <label class="#{theme.$selector-prefix}-radio__label" for="radio_1">
          <span class="#{theme.$selector-prefix}-radio__icon"></span>
           <span class="#{theme.$selector-prefix}-radio__text">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</span>
      </label>
 </div>
 <div class="#{theme.$selector-prefix}-radio">
      <input type="radio" class="#{theme.$selector-prefix}-radio__input" name="navigation" id="radio_2" disabled />
      <label class="#{theme.$selector-prefix}-radio__label" for="radio_2">
          <span class="#{theme.$selector-prefix}-radio__icon"></span>
           <span class="#{theme.$selector-prefix}-radio__text">Radio label</span>
      </label>
 </div>
 <div class="#{theme.$selector-prefix}-radio">
      <input type="radio" class="#{theme.$selector-prefix}-radio__input" name="navigation" id="radio_3" checked/>
      <label class="#{theme.$selector-prefix}-radio__label" for="radio_3">
          <span class="#{theme.$selector-prefix}-radio__icon"></span>
           <span class="#{theme.$selector-prefix}-radio__text">Radio label</span>
      </label>
 </div>

 Styleguide: Radio.default
 */

/*
  Radio Button Without a Label

  `.#{theme.$selector-prefix}-radio`

      Markup:
 <div class="#{theme.$selector-prefix}-radio">
      <input type="radio" class="#{theme.$selector-prefix}-radio__input" name="navigation1" id="radio_5" checked />
      <label class="#{theme.$selector-prefix}-radio__label" for="radio_5">
          <span class="#{theme.$selector-prefix}-radio__icon"></span>
      </label>
 </div>

 Styleguide: Radio.without-label
 */

/*
  Radio Button Ellipsis

  `.#{theme.$selector-prefix}-radio.-ellipsis`

   Markup:
 <div style="width: 100px">
     <div class="#{theme.$selector-prefix}-radio -ellipsis">
          <input type="radio" class="#{theme.$selector-prefix}-radio__input" name="navigation2" id="radio_6" />
          <label class="#{theme.$selector-prefix}-radio__label" for="radio_6">
              <span class="#{theme.$selector-prefix}-radio__icon"></span>
              <span class="#{theme.$selector-prefix}-radio__text">Radio label ellipsis</span>
          </label>
     </div>
 </div>

 Styleguide: Radio.Ellipsis
 */
