@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/loader";
@use "../settings/common/colors";
@use "../settings/common/zindex";
@use "../tools/functions/colors" as colorFunctions;

.#{theme.$selector-prefix}-loader {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: loader.$loader-bg;

    &__content {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        font-size: 0;
    }

    &__rect {
        display: inline-block;
        vertical-align: middle;
        width: loader.$loader-rect-width;
        height: loader.$loader-rect-height;
        margin: loader.$loader-margin;
        background-color: loader.$loader-color;
        animation: loader loader.$loader-animation-speed infinite ease-in-out;
        opacity: loader.$loader-animation-opacity;
        border-radius: loader.$loader-border-radius;

        &.-second {
            animation-delay: -0.9s;
        }

        &.-third {
            animation-delay: -0.8s;
        }
    }

    &.-home {
        position: fixed;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: loader.$loader-bg-cover;
        z-index: zindex.$zindex-2xlg;
    }

    &.-big,
    &.-home {

        .#{theme.$selector-prefix}-loader__rect {
            width: loader.$loader-big-rect-width;
            height: loader.$loader-big-rect-height;
        }
    }

    &.-mini {
        // fix loader position so it appears at the same place where the icon was
        margin-top: -4px;

        .#{theme.$selector-prefix}-loader__rect {
            width: loader.$loader-mini-rect-width;
            height: loader.$loader-mini-rect-height;
            border-radius: loader.$loader-border-radius-mini;
        }
    }

    &.-inline {
        position: relative;
        display: inline-block;
    }

    &.-color {

        .#{theme.$selector-prefix}-loader__rect {
            background-color: loader.$loader-color-mode-color;
        }
    }

    &.-transparent {

        .#{theme.$selector-prefix}-loader__rect {
            background-color: loader.$loader-color-mode-transparent;
        }
    }

    &.-progressbar {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        height: 2px;
        width: 100%;
        overflow: hidden;
        z-index: zindex.$zindex-xlg;

        .#{theme.$selector-prefix}-loader__content {
            position: absolute;
            width: 100%;
            height: 2px;
            background-color: colors.$color-success;
            animation: loadingProgressbar 1s linear infinite;
            transform-origin: 0 0;
        }

        .#{theme.$selector-prefix}-loader__rect {
            display: none;
        }

        &.-overlay {
            bottom: 0;
            height: 100%;
            z-index: zindex.$zindex-3xlg;

            .#{theme.$selector-prefix}-loader__content {
                top: 0;
                left: 0;
            }
        }
    }

    &.-skeleton {
        position: static;

        &.-fluid-content {

            .#{theme.$selector-prefix}-loader__content {
                flex-direction: column;
                align-items: flex-start;
                justify-content: flex-start;
            }

            .#{theme.$selector-prefix}-loader__rect {
                width: 100%;
                height: loader.$loader-mode-skeleton-stripe-height;
                margin-bottom: loader.$loader-mode-skeleton-stripe-bottom-offset;
                background-color: colors.$color-primary-variant;
                animation: loadingSkeleton 1s linear infinite;

                &:last-of-type {
                    width: loader.$loader-final-left-indent;
                    margin-bottom: 0;
                }
            }
        }

        &:not(.-fluid-content) {

            .#{theme.$selector-prefix}-loader__content {
                position: relative;
                height: 100%;
                color: colors.$color-primary-variant;
                background-repeat: no-repeat;
                animation: loadingSkeleton 1s linear infinite;
            }

            .#{theme.$selector-prefix}-loader__rect {
                display: none;
            }
        }

        &.-content {

            .#{theme.$selector-prefix}-loader__content {
                height: 126px;
                background-image: colorFunctions.get-gradient-stripes(7);
                background-size: 100% loader.$loader-mode-skeleton-stripe-height, 100% loader.$loader-mode-skeleton-stripe-height, 100% loader.$loader-mode-skeleton-stripe-height, 100% loader.$loader-mode-skeleton-stripe-height, 100% loader.$loader-mode-skeleton-stripe-height, 100% loader.$loader-mode-skeleton-stripe-height, loader.$loader-final-left-indent loader.$loader-mode-skeleton-stripe-height;
                background-position: 0 0, 0 16px, 0 32px, 0 48px, 0 64px, 0 80px, 0 96px;
            }
        }

        &.-title {

            .#{theme.$selector-prefix}-loader__content {
                height: 94px;
                background-image: colorFunctions.get-gradient-stripes(2);
                background-size: 100% 42px, loader.$loader-final-left-indent 42px;
                background-position: 0 0, 0 52px;
            }
        }

        &.-single-title {

            .#{theme.$selector-prefix}-loader__content {
                height: 88px;
                background-image: linear-gradient(currentColor, currentColor);
                background-size: 100% 42px;
                background-position: 0 16px;
            }
        }

        &.-single-list {
            padding: loader.$loader-list-padding;

            .#{theme.$selector-prefix}-loader__content {
                height: 10px;
                background-color: currentColor;
            }
        }

        &.-list {
            height: 53px;
            padding: loader.$loader-list-padding;

            .#{theme.$selector-prefix}-loader__content {
                height: 26px;
                background-image: colorFunctions.get-gradient-stripes(2);
                background-size: 100% loader.$loader-mode-skeleton-stripe-height, loader.$loader-final-left-indent loader.$loader-mode-skeleton-stripe-height;
                background-position: 0 0, 0 16px;
            }
        }

        &.-list-horizontal {
            padding: 39px 18px;
            width: 190px;
            height: 230px;
            border: 1px solid loader.$loader-border-mode-skeleton;

            .#{theme.$selector-prefix}-loader__content {
                height: 58px;
                background-image: colorFunctions.get-gradient-stripes(4);
                background-size: 100% loader.$loader-mode-skeleton-stripe-height, 100% loader.$loader-mode-skeleton-stripe-height, 100% loader.$loader-mode-skeleton-stripe-height, loader.$loader-final-left-indent loader.$loader-mode-skeleton-stripe-height;
                background-position: 0 0, 0 16px, 0 32px, 0 48px;
            }
        }

        &.-list-with-thumb {
            width: 190px;
            height: 230px;
            border: 1px solid loader.$loader-border-mode-skeleton;

            .#{theme.$selector-prefix}-loader__content {
                height: 100%;
                background-image: colorFunctions.get-gradient-stripes(4);
                background-size: 100% 138px, 80% loader.$loader-mode-skeleton-stripe-height, 80% loader.$loader-mode-skeleton-stripe-height, 50% loader.$loader-mode-skeleton-stripe-height;
                background-position: 0 0, 50% 164px, 50% 180px, 20% 196px;
            }
        }

        &.-side-content {

            .#{theme.$selector-prefix}-loader__content {
                height: 100px;
                background-image: colorFunctions.get-gradient-stripes(3);
                background-size: 90% loader.$loader-mode-skeleton-stripe-height, 50% loader.$loader-mode-skeleton-stripe-height, loader.$loader-final-left-indent loader.$loader-mode-skeleton-stripe-height;
                background-position: 0 15px, 0 45px, 0 75px;
            }
        }

        &.-darker {

            .#{theme.$selector-prefix}-loader__content {
                color: colors.$color-secondary-lighter;
            }
        }

        &.-sequence {
            border-bottom: 1px solid colors.$color-secondary-lightest;

            &:first-child {
                border-top: 1px solid colors.$color-secondary-lightest;
            }
        }

        &.-media {
            .#{theme.$selector-prefix}-loader__content {
                flex-direction: row;
            }
            .#{theme.$selector-prefix}-loader__rect {
                height: 150px;
                background-color: colors.$color-primary-variant;
                margin-bottom: 0;

                &:not(:first-child) {
                    margin-left: 10px;
                }
            }

            .-first {
                flex-basis: 15%;
            }

            .-second {
                flex-basis: 22%;
            }

            .-third {
                flex-basis: 11%;
            }

            .-fourth {
                flex-basis: 22%;
            }

            .-fifth {
                flex-basis: 15%;
            }

            .-sixth {
                flex-basis: 15%;
            }

            + .-media {
                .-first {
                    flex-basis: 11%;
                }

                .-second {
                    flex-basis: 15%;
                }

                .-third {
                    flex-basis: 15%;
                }

                .-fourth {
                    flex-basis: 22%;
                }

                .-fifth {
                    flex-basis: 15%;
                }

                .-sixth {
                    flex-basis: 22%;
                }

                + .-media {
                    .-first {
                        flex-basis: 22%;
                    }

                    .-second {
                        flex-basis: 15%;
                    }

                    .-third {
                        flex-basis: 22%;
                    }

                    .-fourth {
                        flex-basis: 15%;
                    }

                    .-fifth {
                        flex-basis: 15%;
                    }

                    .-sixth {
                        flex-basis: 11%;
                    }
                }
            }
        }
    }

    &.-negative-zindex {
        z-index: zindex.$zindex--2xs;
    }

    &.-solid-block {
        background-color: colors.$color-primary-variant;
        animation: pulsingColorLoading 1.2s linear infinite;
    }
}

