@use "../common/colors";

$badge-diameter-s: 16px;
$badge-diameter-m: 22px;
$badge-diameter-l: 24px;
$badge-diameter-xl: 26px;
$badge-padding-h: 5px;
$badge-color: var(--badge-color);
$badge-icon-color: var(--badge-icon-color);
$badge-color-bg: colors.$color-secondary;
$badge-color-bg-positive: var(--badge-color-bg-positive);
$badge-color-bg-negative: var(--badge-color-bg-negative);
$badge-color-bg-white: colors.$color-primary;
$badge-add-color: var(--badge-add-color);
$badge-add-variation-color: var(--badge-add-variation-color);
$badge-add-variation-bg-color: var(--badge-add-variation-bg-color);
$badge-add-variation-border-color: var(--badge-add-variation-border-color);
$badge-add-variation-hover: var(--badge-add-variation-hover);
$badge-add-variation-border-hover: var(--badge-add-variation-border-hover);
$badge-edit-variation-hover: var(--badge-edit-variation-hover);
$badge-edit-variation-color: var(--badge-edit-variation-color);
$badge-edit-variation-bg-color: var(--badge-edit-variation-bg-color);
$badge-floating-txt-padding-horizontal: 3px;
$badge-floating-txt-animation: 3s ease floatingText;
$badge-white-color: var(--badge-white-color);
$badge-negative-color: var(--badge-negative-color);
$badge-positive-color: var(--badge-positive-color);
$badge-white-border-color: var(--badge-white-border-color);
$badge-white-hover-bg: colors.$color-secondary-lighter;

@keyframes floatingText {
    0% {
        transform: translate(0);
    }

    50% {
        transform: translate(calc(-100% + #{$badge-diameter-xl}/2));
    }

    100% {
        transform: translate(0);
    }
}
