@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/error";
@use "../settings/components/typography";

.#{theme.$selector-prefix}-error {
    display: block;
    color: error.$error-message-color;
    font-size: typography.$font-size-base;
}
.#{theme.$selector-prefix}-error-block {
    border-color: error.$error-message-color !important;
}
/*
 Default Error

 `.#{theme.$selector-prefix}-error`

     Markup:
     <div class="#{theme.$selector-prefix}-error">This is an error!</div>
     <div class="#{theme.$selector-prefix}-error-block">This is an error block!</div>

     Styleguide: Error.default
 */
