@use "../themes/sitefinity/base" as theme;
@use "../common/colors";

#{theme.$selector-app} {
    --radio-chip-border: #{colors.$color-secondary-lighter-contrast};
    --radio-chip-selected-bg-color: #{colors.$color-info-bg};
    --radio-chip-selected-color: inherit;
    --choice-group-radio-chip-hover-bg-color: var(--button-interaction-background-color);
    --choice-group-radio-chip-hover-color: inherit;
}
