@use "../settings/themes/sitefinity/base" as theme;
@use "../tools/mixins/base/text";

.-#{theme.$selector-prefix}-txt-hint {
    @include text.hint;
}

.-#{theme.$selector-prefix}-txt-dimmed {
    color: var(--secondary-text-color);
}

.-#{theme.$selector-prefix}-txt-success {
    color: var(--color-success);
}

.-#{theme.$selector-prefix}-txt-failure {
    color: var(--color-error);
}

.-#{theme.$selector-prefix}-txt-align-left {
    text-align: left;
}

.-#{theme.$selector-prefix}-txt-align-right {
    text-align: right;
}

.-#{theme.$selector-prefix}-txt-align-center {
    text-align: center;
}
.-#{theme.$selector-prefix}-txt-vertical-align-middle {
    vertical-align: middle;
}

.-#{theme.$selector-prefix}-txt-vertical-align-top {
    vertical-align: top;
}

.-#{theme.$selector-prefix}-txt-nowrap {
    white-space: nowrap;
}

.-#{theme.$selector-prefix}-txt-pre-wrap {
    white-space: pre-wrap;
}

.-#{theme.$selector-prefix}-txt-lower-case {
    text-transform: lowercase;
}

.-#{theme.$selector-prefix}-txt-upper-case {
    text-transform: uppercase;
}

.-#{theme.$selector-prefix}-overflow-hidden {
    overflow: hidden;
}

.-#{theme.$selector-prefix}-overflow-auto {
    overflow: auto;
}

.-#{theme.$selector-prefix}-txt-code {
    font-family: "Courier New Regular", "Courier New", Courier, monospace;
}

.-#{theme.$selector-prefix}-overflow-ellipsis {
    @include text.overflow-ellipsis;
}

.-#{theme.$selector-prefix}-wrap-ellipsis {
    @include text.wrap-ellipsis;
}

.-#{theme.$selector-prefix}-overflow-multiline-ellipsis {
    @include text.multiline-text-overflow-ellipsis;
}

.-#{theme.$selector-prefix}-word-break-all {
    word-break: break-all;
}

.-#{theme.$selector-prefix}-word-break-word {
    word-break: break-word;
}

.-#{theme.$selector-prefix}-direction-rtl {
    direction: rtl;
    text-align: right;
}

.-#{theme.$selector-prefix}-direction-ltr {
    direction: ltr;
    text-align: left;
}
.-#{theme.$selector-prefix}-txt-large {
    @include text.size-large;
}

.-#{theme.$selector-prefix}-txt-small {
    @include text.size-small;
}

.-#{theme.$selector-prefix}-txt-base {
    @include text.size-base;
}
/*
  Text utility classes

  Hint, dimmed, success, failure

 `.-#{theme.$selector-prefix}-txt-hint, .-#{theme.$selector-prefix}-txt-dimmed, .-#{theme.$selector-prefix}-txt-success, .-#{theme.$selector-prefix}-txt-failure`

      Markup:
      <div class="-#{theme.$selector-prefix}-txt-hint">Hint: Pellentesque luctus, erat sed elementum aliquam, libero nibh aliquam lacus, sollicitudin accumsan justo nibh eu nisi.</div>
      <div class="-#{theme.$selector-prefix}-txt-dimmed">Dimmed: Pellentesque luctus, erat sed elementum aliquam, libero nibh aliquam lacus, sollicitudin accumsan justo nibh eu nisi.</div>
      <div class="-#{theme.$selector-prefix}-txt-success">Success: Pellentesque luctus, erat sed elementum aliquam, libero nibh aliquam lacus, sollicitudin accumsan justo nibh eu nisi.</div>
      <div class="-#{theme.$selector-prefix}-txt-failure">Failure: Pellentesque luctus, erat sed elementum aliquam, libero nibh aliquam lacus, sollicitudin accumsan justo nibh eu nisi.</div>

      Styleguide: Text.utility
 */
