@use "../../../settings/themes/sitefinity/base" as theme;
@use "../../../settings/components/checkbox";
@use "../../../settings/components/radio";
@use "../../../settings/common/zindex";
@use "../base/common" as commonMixins;
@use "../base/text";

@mixin choice-input($radius, $checkbox-input-icon-size) {
    position: absolute;
    margin: 0;
    width: $checkbox-input-icon-size;
    height: $checkbox-input-icon-size;
    z-index: zindex.$zindex-xs;
    opacity: 0;
    cursor: pointer;

    &:checked {
        + label {
            .#{theme.$selector-prefix}-radio__icon,
            .#{theme.$selector-prefix}-checkbox__icon {
                border-radius: $radius;
                border-color: checkbox.$checkbox-checked-border;
                background-color: checkbox.$checkbox-bg;
                color: checkbox.$checkbox-checked-color;

                &::before {
                    opacity: 1;
                }
            }
        }
    }

    &:checked:hover {
        + label {
            .#{theme.$selector-prefix}-radio__icon,
            .#{theme.$selector-prefix}-checkbox__icon {
                border-color: checkbox.$checkbox-border-hover;
            }
        }
    }

    &:active {
        + label {
            .#{theme.$selector-prefix}-radio__icon,
            .#{theme.$selector-prefix}-checkbox__icon {
                background-color: checkbox.$checkbox-bg-active;
            }
        }
    }

    &:hover,
    &:focus {
        + label {
            .#{theme.$selector-prefix}-radio__icon,
            .#{theme.$selector-prefix}-checkbox__icon {
                border-color: checkbox.$checkbox-border-hover;
            }
        }
    }

    &:disabled {
        cursor: default;

        + label {
            cursor: default;

            .#{theme.$selector-prefix}-radio__icon,
            .#{theme.$selector-prefix}-checkbox__icon {
                border-color: checkbox.$checkbox-border;
                background: checkbox.$checkbox-bg-disabled;
                color: checkbox.$checkbox-color-disabled;
            }

            .#{theme.$selector-prefix}-radio__icon {
                &::before {
                    background-color: checkbox.$checkbox-border;
                }
            }
        }
    }
}

@mixin checkbox-icon() {
    @include choice-icon;

    border-radius: checkbox.$checkbox-border-radius;
    width: checkbox.$checkbox-input-icon-size;
    height: checkbox.$checkbox-input-icon-size;

    &::before {
        top: 0;
        left: 0;
        width: checkbox.$checkbox-input-icon-size;
        height: checkbox.$checkbox-input-icon-size;
        font-size: checkbox.$checkbox-input-icon-font-size;
        content: "\f00c";
    }
}

@mixin radio-icon() {
    @include choice-icon;

    border-radius: 50%;
    width: radio.$radio-input-icon-size;
    height: radio.$radio-input-icon-size;

    &::before {
        top: (radio.$radio-input-icon-size - radio.$radio-icon-size) *0.5 - 1px;
        left: (radio.$radio-input-icon-size - radio.$radio-icon-size) *0.5 - 1px;
        width: radio.$radio-icon-size;
        height: radio.$radio-icon-size;
        content: "";
        border-radius: 50%;
        background-color: radio.$radio-checked-bg-color;
    }
}

@mixin choice-ellipsis() {
    width: 100%;

    label {
        .#{theme.$selector-prefix}-checkbox__text,
        .#{theme.$selector-prefix}-radio__text {
            padding-left: 21px;
            margin-left: -16px;

            @include text.ellipsis;
        }
    }
}

@mixin choice-icon() {
    display: block;
    position: relative;
    flex: none;
    border: 1px solid checkbox.$checkbox-border;
    transition: checkbox.$checkbox-transition;

    &::before {
        @include text.font-awesome;

        position: absolute;
        transition: opacity 0.3s;
        opacity: 0;
    }
}

@mixin choice-label() {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    font-weight: checkbox.$checkbox-label-font-weight;
    cursor: pointer;
    transition: checkbox.$checkbox-transition;

    .#{theme.$selector-prefix}-radio__icon,
    .#{theme.$selector-prefix}-checkbox__icon {
        transition: checkbox.$checkbox-transition;
    }

    .#{theme.$selector-prefix}-radio__text,
    .#{theme.$selector-prefix}-checkbox__text {
        margin-left: checkbox.$checkbox-label-margin-left;
        width: 100%;
    }

    .#{theme.$selector-prefix}-radio__description {
        margin-left: checkbox.$checkbox-label-margin-left;
        width: 100%;
    }

    &.-inline {
        display: inline-flex;
        width: auto;
    }

    &.-width-auto {
        width: auto;
    }
}
