@use "vendors/sitefinity/framework";
@use "settings/base/sitefinity-integration" as sf-int;

//Iris variables
.-sf-contrast #{framework.$selector-app} {
    //Typography

    --color-h2: #{framework.$color-secondary-darker-contrast};
    --color-h5: #{framework.$color-secondary-darker-contrast};

    //Colors

    --color-error: #{framework.$color-error-contrast};
    --color-success: #{framework.$color-success-contrast};

    //Link

    --link-secondary-color: #{framework.$color-secondary-darker-contrast};
    --link-side-secondary-color-active: #{framework.$color-primary-invert-variant};
    --link-side-secondary-color-hover: #{framework.$color-primary-invert-variant};
    --link-side-secondary-color: #{framework.$color-secondary-darker-contrast};
    --link-color: #{framework.$color-info-contrast};

    //Buttons

    //default button
    --button-border-color: #{framework.$color-secondary-lighter-contrast};
    --button-interaction-border-color: #{framework.$color-secondary-lighter-contrast};
    --button-disabled-border-color: #{lighten(framework.$color-secondary-lighter-contrast, 14.51)};
    --button-disabled-color: #{framework.$color-secondary-lighter};

    //action button
    --action-button-border-color: #{framework.$color-success-contrast};
    --action-button-background-color: #{framework.$color-success-contrast};
    --action-button-interaction-border-color: #{darken(saturate(adjust-hue(framework.$color-success-contrast, -3), 18.79), 7.65)};
    --action-button-interaction-background-color: #{darken(saturate(adjust-hue(framework.$color-success-contrast, -3), 18.79), 7.65)};
    --action-button-disabled-border-color: #{lighten(desaturate(adjust-hue(framework.$color-success-contrast, 1), 47.87), 49.61)};
    --action-button-disabled-background-color: #{lighten(desaturate(adjust-hue(framework.$color-success-contrast, 1), 47.87), 49.61)};

    //delete button
    --delete-button-border-color: #{framework.$color-error-contrast};
    --delete-button-background-color: #{framework.$color-error-contrast};
    --delete-button-interaction-border-color: #{darken(saturate(framework.$color-error-contrast, 7.27), 6.47)};
    --delete-button-interaction-background-color: #{darken(saturate(framework.$color-error-contrast, 7.27), 6.47)};
    --delete-button-disabled-border-color: #{lighten(desaturate(framework.$color-error-contrast, 22.61), 39.8)};
    --delete-button-disabled-background-color: #{lighten(desaturate(framework.$color-error-contrast, 22.61), 39.8)};

    //Datepicker

    --date-time-icon-border-color: #{framework.$color-secondary-lighter-contrast};
    --date-time-icon-color: #{framework.$color-secondary-darker-contrast};
    --date-time-content-color: #{framework.$color-secondary-darker-contrast};

    //Icons

    --default-icon-color: #{framework.$color-secondary-darker-contrast};
    --icon-lighter-color: #{framework.$color-secondary-lighter-contrast};
    --icon-light-color: #{framework.$color-secondary-lighter-contrast};
    --icon-illustrative-font-color: #{framework.$color-secondary-lighter-contrast};
    --icon-positive-color: #{framework.$color-success-contrast};
    --icon-badge-add-color: #{framework.$color-primary-invert};
    --icon-badge-add-bg-color: #{framework.$color-primary};
    --icon-badge-add-border-color: #{framework.$color-secondary-lighter-contrast};

    //Status icons

    --status-icon-color-published: #{framework.$color-success-contrast};
    --status-icon-color-restricted: #{framework.$color-error-contrast};
    --status-bg-color-default: #{framework.$color-warning-contrast};
    --status-icon-color-system: #{framework.$color-secondary-lighter-contrast};
    --status-icon-color-scheduled: #{framework.$color-info-contrast};

    //Badge

    --badge-white-border-color: #{framework.$color-secondary-lighter-contrast};
    --badge-color-bg-positive: #{framework.$color-success-contrast};
    --badge-color-bg-negative: #{framework.$color-error-contrast};
    --badge-add-variation-bg-color: #{framework.$color-primary};
    --badge-add-variation-border-color: #{framework.$color-secondary-lighter-contrast};
    --badge-add-variation-hover: #{framework.$color-secondary-lighter};
    --badge-add-variation-border-hover: #{framework.$color-secondary-lighter-contrast};
    --badge-add-variation-color: #{framework.$color-primary-invert};
    --badge-edit-variation-bg-color: #{framework.$color-info-contrast};
    --badge-edit-variation-hover: #{darken(framework.$color-info-contrast, 6.67)};
    --badge-color: #{framework.$color-primary-invert};

    //Text

    --secondary-text-color: #{framework.$color-secondary-darker-contrast};

    //Inputs

    --input-placeholder-color: #{framework.$color-secondary-darker-contrast};
    --input-title-placeholder-color: #{framework.$color-secondary-contrast};
    --input-border: #{framework.$color-secondary-darker-contrast};
    --input-border-hover: #{framework.$color-secondary-darker-contrast};
    --input-char-counter-success-color: #{framework.$color-success-contrast};
    --input-char-counter-error-color: #{framework.$color-error-contrast};

    //Select

    --select-border-color: #{framework.$color-secondary-darker-contrast};
    --select-arrow-color: #{framework.$color-secondary-darker-contrast};

    //Checkbox

    --checkbox-checked-border: #{framework.$color-secondary-darker-contrast};
    --checkbox-border: #{framework.$color-secondary-darker-contrast};
    --checkbox-border-hover: #{framework.$color-secondary-darker-contrast};

    //Dropdown

    --dropdown-border: #{framework.$color-secondary-lighter-contrast};
    --dropdown-item-counter-neutral: #{framework.$color-secondary-darker-contrast};

    //Media

    --image-empty-border: #{framework.$color-secondary-darker-contrast};
    --not-translated-image-label-bg: #{framework.$color-secondary-darker-contrast};

    //Shadows

    --input-box-shadow-hover: #{framework.$color-secondary-darker-contrast};

    //Tree

    --tree-list-expander-color: #{framework.$color-secondary-darker-contrast};
}

//Sitefinity integration

body.-sf-contrast {
    --sf-footer-color: #{framework.$color-secondary-darker-contrast};
    --sf-topbar-color: #{framework.$color-secondary-darker-contrast};
    --sf-h5-color: #{framework.$color-secondary-darker-contrast};
    --sf-navigation-border-color: #{framework.$color-secondary-lighter-contrast};
    --sf-user-info-border-color: #{framework.$color-secondary-lighter-contrast};
    --sf-navigation-caret: #{sf-int.$sf-navigation-caret};
    --sf-live-link: #{framework.$color-success-contrast};
    --sf-green-main: #{framework.$color-success-contrast};
}
