@use "../../vendors/sitefinity/framework";

$menu-sub-menu-width: 300px;
$menu-sub-menu-vertical-space: framework.$spacing-xs;
$menu-fixed-header-bg: linear-gradient(to bottom, rgba(255, 255, 255, 95%) 80%, rgba(255, 255, 255, 20%) 100%);
$menu-title-color: framework.$color-primary-invert;
$menu-sub-title-color: framework.$color-secondary-darker;
$menu-sub-title-border-color: framework.$color-secondary-lighter;
$menu-link-color: framework.$color-primary-invert;
$menu-link-leaf-padding: 3px 5px 2px;
$menu-link-leaf-border-radius: framework.$border-radius-small;
$menu-link-leaf-hover-bg-color: framework.$color-secondary-lighter;
$menu-icon-box-width: 130px;
$menu-icon-box-icon-font-size: var(--icon-menu-font-size);
$menu-icon-box-icon-color: framework.$color-success;
$menu-icon-box-border-color: framework.$color-secondary-lighter;
$menu-icon-box-border-radius: framework.$border-radius-small;
$menu-icon-box-hover-bg-color: framework.$color-primary-variant;
$menu-icon-box-hover-box-shadow: 2px 2px 1px framework.$color-primary-variant;
