@use "../settings/themes/sitefinity/base" as theme;
@use "../tools/mixins/base/common" as commonMixins;
@use "../tools/mixins/base/image";

.-#{theme.$selector-prefix}-hidden {
    display: none !important;
}

.-#{theme.$selector-prefix}-invisible {
    visibility: hidden !important;
}

.-#{theme.$selector-prefix}-block {
    display: block !important;
}

.-#{theme.$selector-prefix}-inline-block {
    display: inline-block !important;
}

.-#{theme.$selector-prefix}-flex {
    display: flex !important;
}

.-#{theme.$selector-prefix}-flex-shrink-0 {
    flex-shrink: 0 !important;
}

.-#{theme.$selector-prefix}-inline-flex {
    display: inline-flex !important;
}

.-#{theme.$selector-prefix}-absolute {
    position: absolute !important;
}
.-#{theme.$selector-prefix}-relative {
    position: relative !important;
}
.-#{theme.$selector-prefix}-static {
    position: static !important;
}
.-#{theme.$selector-prefix}-transparent {
    opacity: 0;
}
.-#{theme.$selector-prefix}-align-self-center {
    align-self: center !important;
}
.-#{theme.$selector-prefix}-align-self-start {
    align-self: flex-start !important;
}
.-#{theme.$selector-prefix}-visually-hidden {
    @include commonMixins.visually-hidden;
}
.-#{theme.$selector-prefix}-img-fit-cover {
    @include image.fit-cover;
}

.-#{theme.$selector-prefix}-rotate-90 {
    transform: rotate(90deg);
}
.-#{theme.$selector-prefix}-rotate--90 {
    transform: rotate(-90deg);
}
.-#{theme.$selector-prefix}-rotate-180 {
    transform: rotate(180deg);
}

/*
  Visibility utility classes

  Hide classes

  `-#{theme.$selector-prefix}-hidden, -#{theme.$selector-prefix}-invisible, -#{theme.$selector-prefix}-transparent`

      Markup:
      <div class="-#{theme.$selector-prefix}-hidden">.-#{theme.$selector-prefix}-hidden</div>
      <div class="-#{theme.$selector-prefix}-invisible">.-#{theme.$selector-prefix}-invisible</div>
      <div class="-#{theme.$selector-prefix}-transparent">.-#{theme.$selector-prefix}-transparent</div>

      Styleguide: Visibility.hide
 */
/*
  Visually hidden but accessible to screen readers

  `-#{theme.$selector-prefix}-visually-hidden`

      Markup:
      <div class="-#{theme.$selector-prefix}-visually-hidden">This text is visually hidden but accessible to screen readers</div>

      Styleguide: Visibility.visually-hidden
 */
/*
 Image fit cover

 `-#{theme.$selector-prefix}-img-fit-cover`

     Markup:
     <div style="width: 200px; height: 300px"><img src="/assets/svg_new_user_interface.svg"  alt="" class="-sf-img-fit-cover"></div>

      Styleguide: Visibility.img-fit-cover
 */
/*
  Positions

  `-#{theme.$selector-prefix}-absolute, -#{theme.$selector-prefix}-relative`

      Markup:
      <div class="-#{theme.$selector-prefix}-absolute">Absolute position</div>
      <div class="-#{theme.$selector-prefix}-relative">Relative position</div>
      <div class="-#{theme.$selector-prefix}-static">Static position</div>

      Styleguide: Visibility.positions
 */
/*
  Rotate

  `-#{theme.$selector-prefix}-rotate-90, #{theme.$selector-prefix}-rotate--90, #{theme.$selector-prefix}-rotate-180`

      Markup:
      <div class="-#{theme.$selector-prefix}-rotate-90">Rotate 90deg</div>
      <div class="-#{theme.$selector-prefix}-rotate--90">Rotate -90deg</div>
      <div class="-#{theme.$selector-prefix}-rotate-180">Rotate 180deg</div>

      Styleguide: Visibility.rotate
 */
