@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/switch";
@use "../settings/common/colors";

.#{theme.$selector-prefix}-switch {
    border: 0;
    background: 0;
    display: inline-flex;
    align-items: center;
    min-height: switch.$switch-height;
    color: colors.$color-secondary-darkest;
    padding: 0;

    button {
        padding: 0;
        border: 0;
        background: 0;
    }

    &.-disabled {
        color: switch.$switch-color;
    }

    &.-right {
        button {
            margin-right: 10px;
            order: -1;
        }
    }
}
