@mixin full-height() {
    height: 100%;
}

/// Overflow X `auto`
@mixin overflow-x-auto() {
    overflow: hidden;
    overflow-x: auto;
}

/// Overflow Y `auto`
@mixin overflow-y-auto() {
    overflow: hidden;
    overflow-y: auto;
}

/// Turn off the bullet for an element of a list
@mixin no-bullet() {
    margin-left: 0;
    list-style-image: none;
    list-style-type: none;
}

/// Turns off the bullets for an entire list
@mixin no-bullets() {
    list-style: none;

    li {
        @include no-bullet;
    }
}

@mixin antialias() {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin visually-hidden() {
    padding: 0;
    position: absolute;
    height: 1px;
    width: 1px;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px);
    border: 0;
}

@mixin disabled() {
    pointer-events: none;
    cursor: default;
}
