@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/checkbox";
@use "../tools/mixins/components/choice";

.#{theme.$selector-prefix}-checkbox {
    position: relative;
    display: block;

    &.-inline {
        display: inline-block;
    }

    &__label {
        @include choice.choice-label;
    }

    &__input {
        @include choice.choice-input(checkbox.$checkbox-border-radius, checkbox.$checkbox-input-icon-size);
    }

    &__icon {
        @include choice.checkbox-icon;
    }

    &.-ellipsis {
        @include choice.choice-ellipsis;
    }
}
/*
  Default Checkbox

 `.#{theme.$selector-prefix}-checkbox`

     Markup:
<div class="#{theme.$selector-prefix}-checkbox">
    <input type="checkbox" class="#{theme.$selector-prefix}-checkbox__input" id="id_1"/>
    <label class="#{theme.$selector-prefix}-checkbox__label" for="id_1">
         <span class="#{theme.$selector-prefix}-checkbox__icon"></span>
          <span class="#{theme.$selector-prefix}-checkbox__text">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</span>
      </label>
   </div>
<div class="#{theme.$selector-prefix}-checkbox">
    <input type="checkbox" class="#{theme.$selector-prefix}-checkbox__input" id="id_3" checked/>
    <label class="#{theme.$selector-prefix}-checkbox__label" for="id_3">
         <span class="#{theme.$selector-prefix}-checkbox__icon"></span>
          <span class="#{theme.$selector-prefix}-checkbox__text">CheckBox checked</span>
      </label>
</div>
<div class="#{theme.$selector-prefix}-checkbox">
    <input type="checkbox" class="#{theme.$selector-prefix}-checkbox__input" id="id_2" disabled/>
    <label class="#{theme.$selector-prefix}-checkbox__label" for="id_2">
         <span class="#{theme.$selector-prefix}-checkbox__icon"></span>
          <span class="#{theme.$selector-prefix}-checkbox__text">Checkbox disabled</span>
      </label>
</div>
<div class="#{theme.$selector-prefix}-checkbox">
    <input type="checkbox" class="#{theme.$selector-prefix}-checkbox__input" id="id_4" checked disabled />
    <label class="#{theme.$selector-prefix}-checkbox__label" for="id_4">
         <span class="#{theme.$selector-prefix}-checkbox__icon"></span>
          <span class="#{theme.$selector-prefix}-checkbox__text">Checkbox disabled checked</span>
      </label>
</div>

 Styleguide: Checkbox.default
*/

