@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/common/zindex";

.#{theme.$selector-prefix}-icon-stack {
    position: relative;
    display: inline-flex;

    &__in {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: zindex.$zindex-sm;
    }

    .#{theme.$selector-prefix}-icon-stack__in + .#{theme.$selector-prefix}-icon-stack__in {
        z-index: zindex.$zindex-xs;
    }

    &__out {
        position: relative;
    }
}

/*
 Stacked icons

 `.#{theme.$selector-prefix}-stack {.#{theme.$selector-prefix}-stack__in,.#{theme.$selector-prefix}-stack__out}`

     Markup:
     <div class="#{theme.$selector-prefix}-icon-stack">
       <i class="#{theme.$selector-prefix}-icon-stack__in sf-fa sf-fa-info #{theme.$selector-prefix}-icon -size-3xs -white"></i>
       <i class="#{theme.$selector-prefix}-icon-stack__out sf-fa sf-fa-circle #{theme.$selector-prefix}-icon -size-l -black"></i>
     </div>
     <div class="#{theme.$selector-prefix}-icon-stack">
       <i class="#{theme.$selector-prefix}-icon-stack__in sf-fa sf-fa-check #{theme.$selector-prefix}-icon -size-2xs -light"></i>
       <i class="#{theme.$selector-prefix}-icon-stack__in sf-fa sf-fa-circle #{theme.$selector-prefix}-icon -size-xl -black"></i>
       <i class="#{theme.$selector-prefix}-icon-stack__out sf-fa sf-fa-circle #{theme.$selector-prefix}-icon -size-2xl -light"></i>
     </div>

      Styleguide: Icon.stacked
*/
