@use "../vendors/sitefinity/framework";
@use "../settings/components/menu";

.#{framework.$selector-prefix}-menu {
    margin: 0;
    padding: 0;
    list-style-type: none;

    &__sub-wrapper {
        padding-top: 315px;
        width: 3 * (menu.$menu-sub-menu-width + 2 * menu.$menu-sub-menu-vertical-space);
    }

    &__sub {
        margin-left: menu.$menu-sub-menu-vertical-space;
        margin-right: menu.$menu-sub-menu-vertical-space;
        margin-bottom: framework.$spacing-5xl;
        width: menu.$menu-sub-menu-width;

        &.-two-columns {
            width: menu.$menu-sub-menu-width * 2 + menu.$menu-sub-menu-vertical-space * 2;

            .sf-menu {
                column-count: 2;
                column-gap: 2 * menu.$menu-sub-menu-vertical-space;
            }
        }

        &.-three-columns {
            width: menu.$menu-sub-menu-width * 3 + menu.$menu-sub-menu-vertical-space * 4;

            .sf-menu {
                column-count: 3;
                column-gap: 2 * menu.$menu-sub-menu-vertical-space;
            }
        }
    }

    &__sticky-title {
        width: 100%;
        position: fixed;
        top: 0;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: menu.$menu-fixed-header-bg;
            z-index: framework.$zindex-default;
        }

        .sf-menu__back,
        .sf-menu__title {
            position: relative;
            z-index: framework.$zindex-xs;
        }
    }

    &__back {
        display: inline-block;
        margin-top: framework.$spacing-6xl;
        margin-bottom: framework.$spacing-3xs;
    }

    &__title {
        margin-top: framework.$spacing-3xs;
        margin-bottom: framework.$spacing-xl;
        font-family: framework.$font-family-title;
        font-weight: framework.$font-weight-bold;
        font-size: framework.$font-size-h1-lg;
        line-height: framework.$line-height-base;
        color: menu.$menu-title-color;

        &.-sub {
            margin-top: 0;
            margin-bottom: framework.$spacing-m;
            font-size: framework.$font-size-h2;
            color: menu.$menu-sub-title-color;
            border-bottom: 1px solid menu.$menu-sub-title-border-color;
        }
    }

    &__item {
        &:not(.-vertical) {
            margin-left: framework.$spacing-3xs;
            margin-right: framework.$spacing-3xs;
        }
    }

    &__link {
        display: block;
        cursor: pointer;
        font-size: framework.$font-size-lg;
        color: menu.$menu-link-color;

        &:hover,
        &:focus {
            color: menu.$menu-link-color;

            .sf-menu__icon-box {
                background-color: menu.$menu-icon-box-hover-bg-color;
                box-shadow: menu.$menu-icon-box-hover-box-shadow;
            }
        }

        &.-leaf {
            display: inline-block;
            padding: menu.$menu-link-leaf-padding;
            border-radius: menu.$menu-link-leaf-border-radius;
            overflow: hidden;
            text-overflow: ellipsis;

            &:hover,&:focus,&:active {
                background-color: menu.$menu-link-leaf-hover-bg-color;
            }
        }

        &.-disabled {
            @include framework.common-disabled;

            color: framework.$color-secondary-lighter;

            .sf-menu__icon-box {
                color: framework.$color-secondary-lighter;
            }
        }
    }

    &__icon-box {
        display: block;
        margin-bottom: framework.$spacing-2xs;
        width: menu.$menu-icon-box-width;
        height: menu.$menu-icon-box-width;
        line-height: menu.$menu-icon-box-width;
        font-size: menu.$menu-icon-box-icon-font-size;
        color: menu.$menu-icon-box-icon-color;
        border: 1px solid menu.$menu-icon-box-border-color;
        border-radius: menu.$menu-icon-box-border-radius;
    }

    &__logout {
        margin-top: framework.$spacing-7xl;

        .#{framework.$selector-prefix}-button {
            margin-right: 0;
        }
    }
}
