@use "../themes/sitefinity/base" as theme;
@use "../common/colors";

#{theme.$selector-app} {
    --input-border: #{colors.$color-secondary-lighter};
    --input-border-hover: #{colors.$color-secondary-lighter};
    --input-char-counter-success-color: #{colors.$color-success};
    --input-char-counter-error-color: #{colors.$color-error};
    --input-placeholder-color: #{colors.$color-secondary};
    --input-title-placeholder-color: #{colors.$color-secondary};
}
