@use "sass:meta";
@use "settings/base/app";
@use "vendors/sitefinity/framework" with (
    $integrated-app: ".-sf-integrated-app",
    $should-add-additional-wrap: true,
    $fa-font-path: "/assets/fonts/font-awesome",
    $selector-app: app.$selector-app
);

// Kendo
@import "vendors/kendo/kendo-overrides";

@include meta.load-css("base/initials");
@include meta.load-css("framework/src/scss/sitefinity-main");

// Fonts
@include meta.load-css("settings/fonts");

// Custom variables
@include meta.load-css("settings/custom-variables");

//// Base - elements
#{framework.$selector-app} {
    @include meta.load-css("base/custom-element");
    @include meta.load-css("base/table");
    @include meta.load-css("base/progress");
    @include meta.load-css("base/hr");
    @include meta.load-css("base/input");
    @include meta.load-css("base/video");
    @include meta.load-css("base/select");
    @include meta.load-css("base/sub-sup");

    //Objects
    @include meta.load-css("objects/router-wrapper");
    @include meta.load-css("objects/wrapper");
    @include meta.load-css("objects/host");
    @include meta.load-css("objects/main");
    @include meta.load-css("objects/details-section-nav");
    @include meta.load-css("objects/main-list-header");

    //Components
    @include meta.load-css("components/menu");
    @include meta.load-css("components/logo");
    @include meta.load-css("components/sticky");
    @include meta.load-css("components/thumbnail");
    @include meta.load-css("components/album");
    @include meta.load-css("components/form");
    @include meta.load-css("components/item");
    @include meta.load-css("components/filters-list");
    @include meta.load-css("components/help");
    @include meta.load-css("components/card");
    @include meta.load-css("components/datepicker");
    @include meta.load-css("components/zone-toolset");
    @include meta.load-css("components/masonry");

    //Trumps
    @include meta.load-css("trumps/highlights");
}
//New zone editor
@include meta.load-css("components/zone-editor");

//Sitefinity integrated styles
@include meta.load-css("sitefinity-integration");

//High contrast theme
@include meta.load-css("high-contrast");
