@use "../tools/mixins/base/common" as commonMixins;

ul, ol {
    &.-list-style-none {
        list-style: none;
    }
 
    &.-list-style-disc {
        list-style-type: disc;
    }

    &.-list-style-decimal {
        list-style-type: decimal;
    }
 
    &.-list-inline {
        li {
            display: inline-block;
        }
    }
 
    &.-full-height {
        @include commonMixins.full-height;
 
        li {
            @include commonMixins.full-height;
        }
    }
}

/*
 Default lists

 Markup:
  <ul>
    <li>Item 1</li>
    <li>Item 2</li>
  </ul>
  <br>
  <ol>
    <li>Item 1</li>
    <li>Item 2</li>
  </ol>

 Styleguide: List.default
*/

