@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/row";
@use "../tools/mixins/base/common" as commonMixins;
@use "../tools/mixins/components/row" as rowMixins;

.#{theme.$selector-prefix}-row {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    width: 100%;
    max-width: row.$row-max-width;
    list-style-type: none;

    /*
      Row with auto sized columns (default)

      `.#{theme.$selector-prefix}-row .#{theme.$selector-prefix}-row__col`

          Markup:
          <div class="#{theme.$selector-prefix}-row">
             <div class="#{theme.$selector-prefix}-row__col">
                 .#{theme.$selector-prefix}-row__col
             </div>
             <div class="#{theme.$selector-prefix}-row__col">
                 .#{theme.$selector-prefix}-row__col
             </div>
          </div>
          <div class="#{theme.$selector-prefix}-row">
             <div class="#{theme.$selector-prefix}-row__col">
                 .#{theme.$selector-prefix}-row__col
             </div>
             <div class="#{theme.$selector-prefix}-row__col">
                 .#{theme.$selector-prefix}-row__col
             </div>
             <div class="#{theme.$selector-prefix}-row__col">
                 .#{theme.$selector-prefix}-row__col
             </div>
          </div>

          Styleguide: Row.auto-sized
     */

    /*
      Nested rows

      `.#{theme.$selector-prefix}-row .#{theme.$selector-prefix}-row`

          Markup:
          <div class="#{theme.$selector-prefix}-row">
             <div class="#{theme.$selector-prefix}-row__col -col-6">
               .#{theme.$selector-prefix}-row__col.-col-6
               <div class="#{theme.$selector-prefix}-row">
                  <div class="#{theme.$selector-prefix}-row__col -col-4">
                      .#{theme.$selector-prefix}-row__col.-col-4
                  </div>
                  <div class="#{theme.$selector-prefix}-row__col -col-8">
                      .#{theme.$selector-prefix}-row__col.-col-8
                  </div>
               </div>
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-6">
                 .#{theme.$selector-prefix}-row__col.-col-6
             </div>
          </div>

        Styleguide: Row.nested-rows
     */

    // --------------------------------------------------
    // row no gutter (no internal padding)
    // --------------------------------------------------

    &.-no-gutter {

        .#{theme.$selector-prefix}-row__col {
            padding: 0;
        }
    }

    /*
      Row with no gutter

      `.#{theme.$selector-prefix}-row.-no-gutter`

          Markup:
          <div class="#{theme.$selector-prefix}-row -no-gutter">
             <div class="#{theme.$selector-prefix}-row__col -col-6">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-6">
                 I am <strong>second</strong> in the markup
             </div>
          </div>

     Styleguide: Row.no-gutter
     */

    // --------------------------------------------------
    // row inline-flex
    // --------------------------------------------------

    &.-inline {
        display: inline-flex;
        width: auto;
    }

    /*
      Row inline

      `.#{theme.$selector-prefix}-row.-inline`

          Markup:
          <div class="#{theme.$selector-prefix}-row -inline">
             <div class="#{theme.$selector-prefix}-row__col -col-6">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-6">
                 I am <strong>second</strong> in the markup
             </div>
          </div>

        Styleguide: Row.inline
     */
    // --------------------------------------------------
    // height 100% row
    // --------------------------------------------------

    &.-full-height {
        @include commonMixins.full-height;
    }

    /*
      Row full height

      `.#{theme.$selector-prefix}-row.-full-height`

          Markup:
          <div class="#{theme.$selector-prefix}-row -full-height">
             <div class="#{theme.$selector-prefix}-row__col -col-6">
                 I am tall
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-6">
                 I am tall
             </div>
          </div>

     Styleguide: Row.full-height
     */

    &.-auto-width {
        width: auto;
    }

    /*
      Width auto

      `.#{theme.$selector-prefix}-row.-auto-width`

          Markup:
          <div class="#{theme.$selector-prefix}-row -auto-width">
          </div>

        Styleguide: Row.width-auto
     */

    // --------------------------------------------------
    // flex-wrap
    // --------------------------------------------------

    &.-wrap {
        flex-wrap: wrap;
    }

    /*
      flex-wrap: wrap (Default)

      `.#{theme.$selector-prefix}-row.-wrap`

      If emitted flex-wrap: wrap

          Markup:
          <div class="#{theme.$selector-prefix}-row -wrap">
             <div class="#{theme.$selector-prefix}-row__col -col-6">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-6">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-10">
                 I am <strong>third</strong> in the markup
             </div>
          </div>

          Styleguide: Row.flex-wrap-wrap
    */

    &.-no-wrap {
        flex-wrap: nowrap;
    }

    /*
      flex-wrap: no-wrap

      `.#{theme.$selector-prefix}-row.-no-wrap`

          Markup:
          <div class="#{theme.$selector-prefix}-row -no-wrap">
             <div class="#{theme.$selector-prefix}-row__col -col-6">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-6">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-10">
                 I am <strong>third</strong> in the markup
             </div>
          </div>

          Styleguide: Row.no-wrap
    */

    &.-wrap-reverse {
        flex-wrap: wrap-reverse;
    }

    /*
      flex-wrap: wrap-reverse

      `.#{theme.$selector-prefix}-row.-wrap-reverse`

          Markup:
          <div class="#{theme.$selector-prefix}-row -wrap-reverse">
             <div class="#{theme.$selector-prefix}-row__col -col-6">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-6">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-10">
                 I am <strong>third</strong> in the markup
             </div>
          </div>

          Styleguide: Row.wrap-reverse
     */

    // --------------------------------------------------
    // flex-direction
    // --------------------------------------------------

    &.-row {
        flex-direction: row;
    }

    /*
      flex-direction: row (default)

      `.#{theme.$selector-prefix}-row.-row`

      If emitted flex-direction: row

          Markup:
          <div class="#{theme.$selector-prefix}-row -row">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>third</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fourth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fifth</strong> in the markup
             </div>
          </div>

          Styleguide: Row.flex-direction-row
     */

    &.-row-reverse {
        flex-direction: row-reverse;
    }

    /*
      flex-direction: row-reverse

      `.#{theme.$selector-prefix}-row.-row-reverse `

          Markup:
          <div class="#{theme.$selector-prefix}-row -row-reverse">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>third</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fourth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fifth</strong> in the markup
             </div>
          </div>

          Styleguide: Row.row-reverse
     */

    &.-column {
        flex-direction: column;
    }

    /*
      flex-direction: column

      `.#{theme.$selector-prefix}-row.-column`

          Markup:
          <div class="#{theme.$selector-prefix}-row -column">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>third</strong> in the markup
             </div>
          </div>

          Styleguide: Row.column
     */

    &.-column-reverse {
        flex-direction: column-reverse;
    }

    /*
      flex-direction: column-reverse

      `.#{theme.$selector-prefix}-row.-column-reverse`

          Markup:
          <div class="#{theme.$selector-prefix}-row -column-reverse">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>third</strong> in the markup
             </div>
          </div>

          Styleguide: Row.column-reverse
     */

    // --------------------------------------------------
    // align-items (cross axis on the current line)
    // --------------------------------------------------

    &.-align-items-stretch {
        align-items: stretch;
    }

    /*
      align-items: stretch (default)

      `.#{theme.$selector-prefix}-row.-align-items-stretch`

      If emitted align-items: stretch

          Markup:
          <div class="#{theme.$selector-prefix}-row -align-items-stretch">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>third</strong> in the markup
             </div>
          </div>

          Styleguide: Row.align-items-stretch

     */

    &.-align-items-start {
        align-items: flex-start;
    }

    /*
      align-items: flex-start

      `.#{theme.$selector-prefix}-row.-align-items-start`

          Markup:
          <div class="#{theme.$selector-prefix}-row -align-items-start">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>third</strong> in the markup
             </div>
          </div>

          Styleguide: Row.align-items-start
     */

    &.-align-items-end {
        align-items: flex-end;
    }

    /*
      align-items: flex-end

      `.#{theme.$selector-prefix}-row.-align-items-end`

          Markup:
          <div class="#{theme.$selector-prefix}-row -align-items-end">
             <div class="#{theme.$selector-prefix}-row__col -flex-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -flex-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -flex-4">
                 I am <strong>third</strong> in the markup
             </div>
          </div>

          Styleguide: Row.align-items-end
     */

    &.-align-items-center {
        align-items: center;
    }

    /*
      align-items: center

      `.#{theme.$selector-prefix}-row.-align-items-center`

          Markup:
          <div class="#{theme.$selector-prefix}-row -align-items-center">
             <div class="#{theme.$selector-prefix}-row__col -flex-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -flex-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -flex-4">
                 I am <strong>third</strong> in the markup
             </div>
          </div>

          Styleguide: Row.align-items-center
     */

    &.-align-items-baseline {
        align-items: baseline;
    }

    /*
      align-items: baseline

      `.#{theme.$selector-prefix}-row.-align-items-baseline`

          Markup:
          <div class="#{theme.$selector-prefix}-row -align-items-baseline">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>third</strong> in the markup
             </div>
          </div>

          Styleguide: Row.align-items-baseline
     */

    // --------------------------------------------------
    // align-content (cross axis)
    // --------------------------------------------------

    &.-align-content-stretch {
        align-content: stretch;
    }

    /*
      align-content: stretch (default)

      `.#{theme.$selector-prefix}-row.-align-content-stretch`

      If emitted align-content: stretch

          Markup:
          <div class="#{theme.$selector-prefix}-row -align-content-stretch">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>third</strong> in the markup
             </div>
            <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fourth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fifth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>sixth</strong> in the markup
             </div>
          </div>

          Styleguide: Row.align-content-stretch
     */

    &.-align-content-start {
        align-content: flex-start;
    }

    /*
      align-content: flex-start

      `.#{theme.$selector-prefix}-row.-align-content-start`

          Markup:
          <div class="#{theme.$selector-prefix}-row -align-content-start">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>third</strong> in the markup
             </div>
            <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fourth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fifth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>sixth</strong> in the markup
             </div>
          </div>

          Styleguide: Row.align-content-start
     */

    &.-align-content-end {
        align-content: flex-end;
    }

    /*
      align-content: flex-end

      `.#{theme.$selector-prefix}-row.-align-content-end`

          Markup:
          <div class="#{theme.$selector-prefix}-row -align-content-end">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>third</strong> in the markup
             </div>
            <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fourth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fifth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>sixth</strong> in the markup
             </div>
          </div>

          Styleguide: Row.align-content-end
     */

    &.-align-content-center {
        align-content: center;
    }

    /*
      align-content: center

      `.#{theme.$selector-prefix}-row.-align-content-center`

          Markup:
          <div class="#{theme.$selector-prefix}-row -align-content-center">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>third</strong> in the markup
             </div>
            <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fourth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fifth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>sixth</strong> in the markup
             </div>
          </div>

          Styleguide: Row.align-content-center
     */

    &.-align-content-between {
        align-content: space-between;
    }

    /*
      align-content: space-between

      `.#{theme.$selector-prefix}-row.-align-content-between`

          Markup:
          <div class="#{theme.$selector-prefix}-row -align-content-between">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>third</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fourth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fifth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>sixth</strong> in the markup
             </div>
          </div>

          Styleguide: Row.align-content-between
     */

    &.-align-content-around {
        align-content: space-around;
    }

    /*
      align-content: space-around

      `.#{theme.$selector-prefix}-row.-align-content-around`

          Markup:
          <div class="#{theme.$selector-prefix}-row -align-content-around">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>third</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fourth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fifth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>sixth</strong> in the markup
             </div>
          </div>

          Styleguide: Row.align-content-around
     */

    // --------------------------------------------------
    // justify-content (main axis)
    // --------------------------------------------------

    &.-justify-content-start {
        justify-content: flex-start;
    }

    /*
      justify-content: flex-start (default)

      `.#{theme.$selector-prefix}-row.-justify-content-start`

      If emitted justify-content: flex-start

          Markup:
          <div class="#{theme.$selector-prefix}-row -justify-content-start">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
          </div>

     Styleguide: Row.justify-content-start
     */

    &.-justify-content-end {
        justify-content: flex-end;
    }

    /*
      justify-content: flex-end

      `.#{theme.$selector-prefix}-row.-justify-content-end`

          Markup:
          <div class="#{theme.$selector-prefix}-row -justify-content-end">
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
          </div>

     Styleguide: Row.justify-content-end
     */

    &.-justify-content-center {
        justify-content: center;
    }

    /*
      justify-content: center

      `.theme.-row.-justify-content-center`

          Markup:
          <div class="#{theme.$selector-prefix}-row -justify-content-center">
             <div class="#{theme.$selector-prefix}-row__col -flex-4">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -flex-4">
                 I am <strong>second</strong> in the markup
             </div>
          </div>

     Styleguide: Row.justify-content-center
     */

    &.-justify-content-between {
        justify-content: space-between;
    }

    /*
      justify-content: space-between

      `.#{theme.$selector-prefix}-row.-justify-content-between`

          Markup:
          <div class="#{theme.$selector-prefix}-row -justify-content-between">
             <div class="#{theme.$selector-prefix}-row__col -col-3">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-3">
                 I am <strong>third</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fourth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fifth</strong> in the markup
             </div>
          </div>

     Styleguide: Row.justify-content-between
     */

    &.-justify-content-around {
        justify-content: space-around;
    }

    /*
      justify-content: space-around

      `.#{theme.$selector-prefix}-row.-justify-content-around`

          Markup:
          <div class="#{theme.$selector-prefix}-row -justify-content-around">
             <div class="#{theme.$selector-prefix}-row__col -col-3">
                 I am <strong>first</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>second</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-3">
                 I am <strong>third</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fourth</strong> in the markup
             </div>
             <div class="#{theme.$selector-prefix}-row__col -col-4">
                 I am <strong>fifth</strong> in the markup
             </div>
          </div>

     Styleguide: Row.justify-content-around
     */

    &.-adjustable-ellipsis {
        display: inline-flex;
        width: auto;

        .#{theme.$selector-prefix}-row__col {
            &.-truncatable {
                min-width: 0;
            }

