@use "../vendors/sitefinity/framework";
@use "../settings/components/card";

.#{framework.$selector-prefix}-card {
    position: relative;
    border: 1px solid framework.$color-secondary-lightest;

    &.-selected {
        .sf-card__selected-indicator {
            visibility: visible;
        }

        &::after {
            @include framework.item-overlay($zIndex: framework.$zindex-sm);
        }
    }

    &.-dashed {
        border-style: dashed;
    }

    &.-darker {
        border-color: framework.$color-secondary;
    }

    &.-tooltip:hover {
        outline: 2px solid framework.$color-success-bg;
        outline-offset: -2px;

        .sf-card__label {
            display: block;
        }
    }

    &.-full-width {
        width: 100%;
    }

    &.-full-height {
        height: 100%;
    }

    &:not(.-selected) {
        &:hover,
        &:focus,
        &:active {
            &::after {
                @include framework.item-overlay(card.$card-hover-bg, 0.6);
            }
        }
    }

    &:not(:hover, :focus),
    &.-selected {
        .sf-card__overlay {
            display: none;
        }
    }

    .sf-card__overlay {
        @include framework.absolute-in-wrapper(framework.$zindex-xs);
    }

    &.-template {
        margin: card.$card-margin;
        padding: card.$card-padding;
        width: card.$card-width;
        height: card.$card-height;

        &[data-framework="mvc"] {
            &::before {
                position: absolute;
                top: 185px;
                right: 28px;
                padding: 6px 5px;
                font-size: 10px;
                text-transform: uppercase;
                content: attr(data-framework);
                color: framework.$color-primary;
                background-color: framework.$color-info;
            }
        }

        &[data-renderer="NetCore"] {
            &::before {
                display: none;
            }
        }

        .sf-card__image {
            width: card.$card-image-width;
            height: card.$card-image-height;
        }
    }

    &__selected-indicator {
        margin-right: 0;
        position: absolute;
        visibility: hidden;
        top: framework.$image-helpers-edge-offset;
        right: framework.$image-helpers-edge-offset;
        z-index: framework.$zindex-xlg;
        color: framework.$color-primary-invert;
    }

    &__label {
        position: absolute;
        display: none;
        left: -1px;
        bottom: 100%;
        font-size: 10px;
        text-transform: uppercase;
        background-color: framework.$color-success-bg;
        padding: 0 8px;
        height: 24px;
        vertical-align: middle;
        line-height: 24px;
        color: framework.$color-secondary-darker;
    }
}
