@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/button" as btnSettings;
@use "../settings/components/typography";
@use "../settings/common/colors";
@use "../settings/common/zindex";
@use "../tools/mixins/components/button" as btnMixins;
@use "../tools/mixins/base/center";
@use "../tools/mixins/base/props-from-map";
@use "../tools/mixins/base/decoration";
@use "../tools/mixins/base/text";

.#{theme.$selector-prefix}-button {
    @include btnMixins.default;

    &.-disabled {
        @include btnMixins.disabled;
    }

    &.-full-size-content {
        .#{theme.$selector-prefix}-button__content {
            height: 100%;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    @each $size-type, $sizes in btnSettings.$button-size {
        @if ($size-type == default) {
            @include props-from-map.set-properties-from-map($sizes);
        }

        @else {

            &.-#{$size-type} {
                @include props-from-map.set-properties-from-map($sizes);
            }
        }
    }

    @each $button-type, $types in btnSettings.$button-types {
        @if ($button-type == default) {
            @include btnMixins.button($types);
        }

        @else {

            &.-#{$button-type} {
                @include btnMixins.button($types);
            }
        }
    }

    &.-feedback {
        position: relative;

        .#{theme.$selector-prefix}-button__icon {
            display: none;
        }

        &.-done {

            .#{theme.$selector-prefix}-button__icon {
                display: flex;
                margin-right: 0;
                justify-content: center;
                align-items: center;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                color: inherit;
            }

            .#{theme.$selector-prefix}-button__content {
                opacity: 0;
            }
        }
    }

    &.-loader {
        position: relative;

        .#{theme.$selector-prefix}-loader {
            position: absolute;
            display: none;
            top: 0;
            left: 0;
            right: -1px;
            bottom: -1px;
        }
    }

    &.-inprogress {

        .#{theme.$selector-prefix}-button__content {
            opacity: 0;
        }

        .#{theme.$selector-prefix}-loader {
            display: block;
            border-radius: 2px;
        }

        .#{theme.$selector-prefix}-icon {
            opacity: 0;
        }
    }

    &.-white {
        color: colors.$color-primary;
    }

    &.-link {
        margin: 0;
        padding: 0;
        height: auto;
        font-size: inherit;
        vertical-align: baseline;
        border: 0;
        min-width: 0;
    }

    &.-dropdown {
        position: relative;
    }

    &.-dropped {
        &.-left {

            .#{theme.$selector-prefix}-button__dropdown {
                right: auto;
                left: -1px;
            }
        }

        .#{theme.$selector-prefix}-button__dropdown {
            opacity: 1;
            z-index: zindex.$zindex-lg;
        }
    }

    &.-fixed-width {

        .#{theme.$selector-prefix}-button__dropdown {
            width: btnSettings.$button-dropdown-fixed-width;
        }

        .#{theme.$selector-prefix}-dropdown {
            white-space: normal;
            width: 100%;
        }
    }

    &.-txt-small {
        font-size: typography.$font-size-h5;
    }

    &.-no-border {
        background: transparent;
        border: 0;

        &:hover {
            background-color: transparent;
        }

        .#{theme.$selector-prefix}-button__dropdown {
            top: 100%;
        }
    }

    &.-no-padding {
        padding: 0;
    }

    &.-icon {

        .#{theme.$selector-prefix}-button__icon {
            margin: 0;
        }

        &[disabled],
        &.-disabled,
        &.-action,
        &.-delete,
        &:not(.-no-hover):hover,
        &:not(.-no-active):active,
        &:not(.-no-focus-visible):focus {

            .#{theme.$selector-prefix}-button__icon:not(.-badge) {
                color: inherit;
            }
        }

        &:not(.-icon-and-text, .-toolset) {
            padding: 0;
        }

        &[disabled],
        &.-disabled {
            &.-no-border {
                background-color: transparent;
                color: btnSettings.$button-no-border-disabled;

                &.-darker-disabled {
                    color: btnSettings.$button-no-border-darker-disabled;
                }
            }
        }
    }

    &.-toggle {
        padding: 0;
        margin-right: 0;
        width: btnSettings.$button-toggle-width;
        height: btnSettings.$button-toggle-height;
        color: colors.$color-secondary-darkest;
        border: 0;
        background-color: colors.$color-primary;
        border-radius: 0;

        &:hover {

            .#{theme.$selector-prefix}-button__icon:not(.-badge) {
                @include decoration.fill(colors.$color-primary-invert, colors.$color-secondary-lighter, colors.$color-secondary-lighter);
            }
        }

        &.-auto-width {
            width: auto;
        }

        &:focus {
            background-color: transparent;
        }

        /* Combine these two when we have a combining mixin for elements and modifiers */

        &.-active {

            .#{theme.$selector-prefix}-button__icon {
                color: colors.$color-primary-invert;
            }

            &:hover {

                .#{theme.$selector-prefix}-button__icon {
                    background-color: transparent;
                    border-color: colors.$color-secondary-lightest;
                }
            }

            @include decoration.fill(colors.$color-primary-invert, colors.$color-primary-variant, colors.$color-secondary-lightest);
        }

        &.-dropped {

            .#{theme.$selector-prefix}-button__icon:not(.-badge) {
                color: colors.$color-primary-invert;
            }

            &:hover {

                .#{theme.$selector-prefix}-button__icon:not(.-badge) {
                    background-color: transparent;
                    border-color: colors.$color-secondary-lightest;
                }
            }

            @include decoration.fill(colors.$color-primary-invert, colors.$color-primary-variant, colors.$color-secondary-lightest);
        }

        .#{theme.$selector-prefix}-button__icon:not(.-badge) {
            width: btnSettings.$button-toggle-width;
            height: btnSettings.$button-toggle-height;
            line-height: btnSettings.$button-toggle-height;
        }

        .#{theme.$selector-prefix}-button__dropdown {
            top: 100%;
        }
    }

    &.-circle {
        border-radius: 50%;

        &.-small {
            padding: btnSettings.$button-circle-padding;
            height: btnSettings.$button-height-sm;
            width: btnSettings.$button-height-sm;
            min-width: btnSettings.$button-height-sm;
        }
    }

    &.-icon-left {

        .#{theme.$selector-prefix}-button__icon {
            order: 1;
        }

        .#{theme.$selector-prefix}-button__content {
            order: 2;
            margin-left: 5px;
        }
    }

    &.-angled {
        border-radius: 0;

        .#{theme.$selector-prefix}-button__dropdown {
            top: 100%;
        }
    }

    &.-toolset {
        @include btnMixins.toolset-button;

        height: 100%;
        margin-right: 0;
        min-width: 0;
    }

    &.-color-inherit,
    &.-toolset:not(.-white) {
        color: inherit;
    }

    &.-color-inherit {
        &:hover,
        &:focus {
            color: inherit;
        }
    }

    &__icon {
        margin: 0 btnSettings.$button-icon-offset 0 0;

        &.-right {
            margin: 0 0 0 btnSettings.$button-icon-offset;
        }
    }

    &__content {
        vertical-align: baseline;
    }

    &__dropdown {
        position: absolute;
        text-align: left;
        opacity: 0;
        z-index: zindex.$zindex--2xs;
        transition: opacity 0.3s ease-out;
        transition-property: opacity;
        top: calc(100% + #{btnSettings.$button-dropdown-top-offset});
        right: -1px;
        min-width: calc(100% + 2px);
        width: auto;

        .#{theme.$selector-prefix}-dropdown__item {
            font-size: inherit;
            line-height: normal;
            word-break: normal;
        }
    }

    &__arrow {
        margin: btnSettings.$button-arrow-margin;
        padding: btnSettings.$button-arrow-padding;
        height: btnSettings.$button-arrow-height;
        line-height: btnSettings.$button-arrow-height;
        font-size: btnSettings.$button-arrow-icon-font-size;
    }
}

/*
  Button
 */
@each $button-type, $types in btnSettings.$button-types {
    @each $size-type, $sizes in btnSettings.$button-size {
        @if ($button-type == default and $size-type == default) {

            /*
             Default Button

             `.#{theme.$selector-prefix}-button`

                 Markup:
                 <button class="#{theme.$selector-prefix}-button">Default Button</button>
                 <button class="#{theme.$selector-prefix}-button" disabled>Disabled Default Button</button>
                 <a class="#{theme.$selector-prefix}-button">Default Button</a>
                 <a class="#{theme.$selector-prefix}-button -disabled">Disabled Default Button</a>

                Styleguide: Button.default
             */
        }

        @else if ($button-type == default) {

            /*
             #{$size-type} Button

             `.#{theme.$selector-prefix}-button.-#{$size-type}`

                 Markup:
                 <button class="#{theme.$selector-prefix}-button -#{$size-type}">#{$size-type} Default Button</button>
                 <button class="#{theme.$selector-prefix}-button -#{$size-type}" disabled>Disabled #{$size-type} Default Button</button>

               Styleguide: Button.#{$size-type}
            */
        }

        @else if ($size-type == default) {

            /*
             #{$button-type} Button

             `.#{theme.$selector-prefix}-button.-#{$button-type}`

                 Markup:
                 <button class="#{theme.$selector-prefix}-button -#{$button-type}">#{$button-type} Default Button</button>
                 <button class="#{theme.$selector-prefix}-button -#{$button-type}" disabled>Disabled #{$button-type} Default Button</button>

              Styleguide: Button.#{$button-type}
            */
        }

        @else {

            /*
             #{$button-type} #{$size-type} Button

             `.#{theme.$selector-prefix}-button.-#{$size-type}.-#{$button-type}`

                 Markup:
                 <button class="#{theme.$selector-prefix}-button -#{$button-type} -#{$size-type}">#{$button-type} #{$size-type} Default Button</button>
                 <button class="#{theme.$selector-prefix}-button -#{$button-type} -#{$size-type}" disabled>Disabled #{$button-type} #{$size-type} Default Button</button>

                 Styleguide: Button.#{$button-type}-#{$size-type}
            */
        }
    }
}

