@use "sass:meta";
@use "settings/themes/sitefinity/base" as theme;

// Vendors
@include meta.load-css("vendors/font-awesome/font-awesome");

// Custom variables
@include meta.load-css("settings/custom-variables");

// base
@include meta.load-css("base/body");
@include meta.load-css("base/app");
@include meta.load-css("base/scrollbar");

@mixin load-content {
    // generic
    @include meta.load-css("generic/reset");
    @include meta.load-css("generic/normalize");
    @include meta.load-css("generic/box-sizing");

    // base
    @include meta.load-css("base/link");
    @include meta.load-css("base/list");

    // trumps
    @include meta.load-css("trumps/spacing");
    @include meta.load-css("trumps/border");
    @include meta.load-css("trumps/box");
    @include meta.load-css("trumps/disabled");
    @include meta.load-css("trumps/text");
    @include meta.load-css("trumps/visibility");
    @include meta.load-css("trumps/width");
    @include meta.load-css("trumps/float");
    @include meta.load-css("trumps/background");
    @include meta.load-css("trumps/animation");

    // components
    @include meta.load-css("components/typography");
    @include meta.load-css("components/breadcrumb");
    @include meta.load-css("components/tabstrip");
    @include meta.load-css("components/icon");
    @include meta.load-css("components/icon-stack");
    @include meta.load-css("components/status");
    @include meta.load-css("components/badge");
    @include meta.load-css("components/button");
    @include meta.load-css("components/button-group");
    @include meta.load-css("components/notification");
    @include meta.load-css("components/tooltip");
    @include meta.load-css("components/input");
    @include meta.load-css("components/dropdown");
    @include meta.load-css("components/expander");
    @include meta.load-css("components/checkbox");
    @include meta.load-css("components/radio");
    @include meta.load-css("components/choice-group");
    @include meta.load-css("components/loader");
    @include meta.load-css("components/chip");
    @include meta.load-css("components/error");
    @include meta.load-css("components/select");
    @include meta.load-css("components/form");
    @include meta.load-css("components/row");
    @include meta.load-css("components/grid");
    @include meta.load-css("components/link");
    @include meta.load-css("components/top-toolbar");
    @include meta.load-css("components/toggle");
    @include meta.load-css("components/split-button");
    @include meta.load-css("components/sidebar");
    @include meta.load-css("components/switch");
    @include meta.load-css("components/insert-body");
    @include meta.load-css("components/list");
    @include meta.load-css("components/tree-list");
    @include meta.load-css("components/tree");
    @include meta.load-css("components/media");
    @include meta.load-css("components/field");
}

@if (theme.$should-add-additional-wrap == true) {

    #{theme.$selector-app} {
        @include load-content;
    }
}

@else {
    @include load-content;
}
