@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/typography";
@use "../settings/common/spacing";
@use "../settings/common/zindex";
@use "../tools/mixins/base/common" as commonMixins;

.#{theme.$selector-prefix}-tooltip {
    position: relative;

    &:not(:hover, :focus, :active, .-dropped),
    &.-open-on-click:not(.-dropped) {

        .#{theme.$selector-prefix}-tooltip__content {
            @include commonMixins.visually-hidden;

            left: -1000px;
        }
    }

    &__content {
        position: absolute;
        top: 100%;
        left: 0;
        font-size: typography.$font-size-base;
        font-weight: typography.$font-weight-normal;

        &.-down,
        &.-up {
            left: 50%;
        }

        &.-down {
            transform: translateX(-50%);
        }

        &.-up {
            top: auto;
            transform: translate(-50%, -100%);
        }

        &.-right,
        &.-left {
            top: 50%;
            transform: translateY(-50%);
        }

        &.-right {
            left: 100%;
        }

        &.-left {
            left: auto;
            right: 100%;
        }

        &.-down-right,
        &.-down-left {
            transform: translateY(spacing.$spacing-4xs);
        }

        &.-down-right {
            left: auto;
            right: -10px;
        }

        &.-down-left {
            left: -20px;
        }

        &:not(.-toolset) {
            min-width: 250px;
            z-index: zindex.$zindex-3xlg;
        }

        &.-toolset {
            z-index: zindex.$zindex-lg;
        }
    }
}

.-side-content .#{theme.$selector-prefix}-tooltip__content {
    left: auto;
    right: 0;
}

