@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/sidebar";
@use "../settings/components/top-toolbar";
@use "../settings/common/zindex";
@use "../settings/common/spacing";
@use "../settings/common/colors";
@use "../tools/mixins/base/center";
@use "../tools/mixins/base/text";
@use "../tools/mixins/components/headings";

.#{theme.$selector-prefix}-sidebar {
    position: fixed;
    top: top-toolbar.$top-toolbar-height;
    bottom: 0;
    right: 0;
    width: sidebar.$sidebar-width;
    background-color: sidebar.$sidebar-bg-color;
    border-top: 1px solid sidebar.$sidebar-border-color;
    border-left: 1px solid sidebar.$sidebar-border-color;
    border-bottom: 1px solid sidebar.$sidebar-border-color;
    z-index: zindex.$zindex-md;

    &.-animate {
        animation: sidebar sidebar.$animation-time 1 sidebar.$animation-timing;

        &.-hidden {
            animation: hideSidebar sidebar.$animation-time 1 sidebar.$animation-timing;
        }
    }

    &.-hidden {
        //display: none;
        right: -#{sidebar.$sidebar-width};

        &.-toggle {
            display: block;
            right: -(sidebar.$sidebar-width - sidebar.$sidebar-toggle-width);
        }

        &.-wide {
            right: -#{sidebar.$sidebar-width-wide};

            &.-toggle {
                display: block;
                right: -(sidebar.$sidebar-width-wide - sidebar.$sidebar-toggle-width);
            }
        }
    }

    &.-toggle {
        .sf-sidebar__title,
        .sf-sidebar__section {
            padding-left: sidebar.$sidebar-content-horizontal-padding-toggle;
            padding-right: sidebar.$sidebar-content-horizontal-padding-toggle;
        }
    }

    &.-left {
        left: 0;
        right: auto;
    }

    &.-wide {
        width: sidebar.$sidebar-width-wide;
    }

    &__close {
        position: absolute;
        top: 0;
        right: 0;
        margin-top: spacing.$spacing-2xs;
        margin-right: spacing.$spacing-3xs;
        padding: spacing.$spacing-3xs;
        z-index: zindex.$zindex-xs;
        cursor: pointer;
    }

    &__toggle {
        @include center.flex;

        position: absolute;
        display: flex;
        top: 50%;
        width: sidebar.$sidebar-toggle-button-width;
        height: sidebar.$sidebar-toggle-button-height;
        background-color: colors.$color-secondary-lighter;
        cursor: pointer;
        z-index: zindex.$zindex-lg;
    }

    &__toggle-icon {
        width: sidebar.$sidebar-toggle-button-width;
    }

    &.-left-aligned {
        left: 0;
        right: auto;
        border-left: 0;
        border-right: 1px solid sidebar.$sidebar-border-color;
    }

    &.-static {
        position: static;
        height: 100%;
    }

    &.-width-auto {
        width: auto;
    }

    &.-no-animation {
        animation: none;
    }

    &__title {
        @include text.overflow-ellipsis;

        padding: sidebar.$sidebar-title-top-padding sidebar.$sidebar-content-horizontal-padding sidebar.$sidebar-title-top-bottom;
        border-bottom: 1px solid colors.$color-secondary-lightest;
        
        &.-no-border {
            border-bottom: 0;
        }
    }

    &__items-list {
        display: block;
        background-color: colors.$color-primary;
    }

    &__header {
        padding: sidebar.$sidebar-item-padding;
        border-bottom: sidebar.$sidebar-item-border-bottom;
        background-color: sidebar.$sidebar-header-bg-color;
    }

    &__scroll {
        display: block;
        position: absolute;
        bottom: 0;
        top: sidebar.$sidebar-scroll-top; //title height + padding-top + padding-bottom
        overflow: auto;
        width: 100%;
    }

    &__inner-scroll {
        height: 100%;
    }

    &__section {
        padding: sidebar.$sidebar-content-vertical-padding sidebar.$sidebar-content-horizontal-padding;

        // common styles for fields in sidebar
        // experiment not to use bem in few cases because it is harder to set modifiers to fields collections that are used at many places than to override css properties here

        .sf-field {
            margin-bottom: 0;
        }

        .sf-field__label,
        .sf-field__content,
        .sf-field__no-value {
            font-size: inherit;
        }

        .-sf-short-text-default {
            .sf-field {
                padding-bottom: 0;
            }
        }

        .sf-input .sf-input__title {
            @include headings.h3;

            min-height: 0;
        }

        hr {
            margin-top: 10px;
            margin-bottom: 10px;
        }
    }

    &__slider-wrapper {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
    
    &__slider {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        background-color: sidebar.$sidebar-bg-color;
    }

    &__no-items {
        display: flex;
        flex-flow: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
    }
}

@keyframes sidebar {
    from {
        transform: translateX(100%);
    }

    to {
        transform: translateX(0);
    }
}

@keyframes hideSidebar {
    from {
        transform: translateX(-100%);
    }

    to {
        transform: translateX(0);
    }
}
