@use "../vendors/sitefinity/framework";

.#{framework.$selector-prefix}-filters-list {
    --alphabet-filter-rows: 100;

    &.-alphabet {
        display: grid;
        grid-gap: 0;
        grid-template-rows: repeat(var(--alphabet-filter-rows), 1fr);
        grid-template-columns: 1fr 1fr;
        grid-auto-flow: column;
    }

    //&__item - skipped the item on purpose; no need to complicate the css; add it later if filter items get more styling
    .-secondary-group {
        margin-top: 20px;

        + .-secondary-group {
            margin-top: 0;
        }
    }
}
