@use "../themes/sitefinity/base" as theme;
@use "../common/colors";

#{theme.$selector-app} {
    --link-secondary-color: #{colors.$color-secondary-darkest};
    --link-side-secondary-color: #{colors.$color-secondary};
    --link-side-secondary-color-active: #{colors.$color-primary-invert};
    --link-side-secondary-color-hover: #{colors.$color-secondary-darker};
    --link-color: #{colors.$color-info};
}
