@use "../vendors/sitefinity/framework";

.-sf-just-added {
    animation-name: justAdded;
    animation-duration: 1s;
    animation-iteration-count: 1;
}

.-sf-just-added-disabled {
    animation-name: justAddedDisabled;
    animation-duration: 1s;
    animation-iteration-count: infinite;
}

.-sf-highlighted {
    &:not(.-on-hover) {
        background-color: framework.$color-warning-bg;
    }

    &.-on-hover:hover {
        background-color: framework.$color-warning-bg;
    }
}

@keyframes justAdded {
    from { background-color: framework.$color-warning-bg; }
    to { background-color: framework.$color-primary; }
}

@keyframes justAddedDisabled {
    from { background-color: framework.$color-warning-bg; }
    to { background-color: framework.$color-primary-variant; }
}
