@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/icon";
@use "../tools/mixins/components/icons" as iconMixins;
@use "../tools/functions/convert";
@use "../settings/common/colors";
@use "sass:map";

.#{theme.$selector-prefix}-icon {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 1em;
    width: 1em;
    font-size: icon.$default-icon-size;
    font-style: normal;
    color: icon.$default-icon-color;

    &.-close {
        &::before {
            content: convert.get-unicode(#{icon.$icon-close-content});
        }
    }

    &.-illustrative {
        font-size: icon.$icon-illustrative-font-size;
        color: icon.$icon-illustrative-font-color;

        @include iconMixins.circle-icons(icon.$icon-illustrative-size, icon.$icon-illustrative-bg-color, icon.$icon-illustrative-border);

        &.-size-3xl {
            width: icon.$icon-illustrative-size-sm;
            height: icon.$icon-illustrative-size-sm;
        }
    }

    &.-light {
        color: icon.$icon-light-color;
    }

    &.-faq {
        @include iconMixins.circle-icons(icon.$icon-faq-size, icon.$icon-faq-bg-color);

        line-height: icon.$icon-faq-size;
        box-shadow: icon.$icon-faq-box-shadow;
    }

    &.-zone-editor {
        font-size: icon.$icon-zone-editor-size;
    }

    &.-lightest {
        color: icon.$icon-lightest-color;
    }

    &.-lighter {
        color: icon.$icon-lighter-color;
    }

    &.-darker {
        color: icon.$icon-darker-color;
    }

    &.-darkest {
        color: icon.$icon-darkest-color;
    }

    &.-black {
        color: icon.$icon-black-color;
    }

    &.-color-inherit {
        color: inherit;
    }

    &.-white {
        color: icon.$icon-white-color;
    }

    &.-positive {
        color: icon.$icon-positive-color;
    }

    &.-negative {
        color: icon.$icon-negative-color;
    }

    &.-square {
        padding: icon.$icon-square-padding;
        border: icon.$icon-square-border-width solid currentColor;

        &.-positive {
            .sf-icon__svg {
                color: colors.$color-primary;
                fill: icon.$icon-positive-color;
            }

            .-no-background {
                &.sf-icon__svg {
                    color: icon.$icon-positive-color;
                }
            }
        }
    }

    &.-badge {
        @include iconMixins.circle-icons( icon.$icon-size-badge, var(--icon-badge-add-bg-color), var(--icon-badge-add-border-color));

        color: var(--icon-badge-add-color);

        &.-edit-variation {
            color: icon.$icon-white-color;
            background-color: icon.$icon-info-color;
            border-color: icon.$icon-info-color;
        }

        &.-warning-bg {
            background-color: icon.$icon-warning-color;
            border-color: icon.$icon-warning-color;
        }

        &.-small {
            width: icon.$icon-size-small-badge;
            height: icon.$icon-size-small-badge;
        }
    }

    &.-warning {
        color: icon.$icon-warning-color;
    }

    &.-svg {
        width: auto;
        height: auto;

        &.-zone-editor {
            .sf-icon__svg {
                fill: colors.$color-primary-invert;
                color: colors.$color-primary;

                &.-no-background {
                    color: colors.$color-primary-invert;
                }
            }

            &.-inverted {
                .sf-icon__svg {
                    fill: colors.$color-primary;
                    color: colors.$color-primary-invert;

                    &.-no-background {
                        color: colors.$color-primary;
                    }
                }
            }
        }
    }

    @each $i-name, $i-size in icon.$icon-sizes {

        &.-size-#{$i-name} {
            font-size: #{$i-size};
        }
    }

    &__svg {
        width: icon.$icon-svg-width;
        height: icon.$icon-svg-height;
        fill: currentColor;

        &.-color-current {
            background-color: currentColor;
        }
    }

    &__svg-template {
        width: 100%;
        max-height: icon.$icon-svg-template-width;
        max-width: icon.$icon-svg-template-height;
    }
}

/*
  Icons with text

  `.#{theme.$selector-prefix}-icon`

      Markup:
      <i class="sf-fa sf-fa-sliders #{theme.$selector-prefix}-icon"></i><span>Settings</span><br />
      <i class="sf-fa sf-fa-question-circle #{theme.$selector-prefix}-icon"></i><span>Help</span><br />
      <i class="sf-fa sf-fa-folder-o #{theme.$selector-prefix}-icon"></i><span>Folder</span><br />
      <i class="sf-fa sf-fa-exclamation-circle #{theme.$selector-prefix}-icon -exclamation"></i><br />

     Styleguide: Icon.text
 */

/*
  Close icon

  `.#{theme.$selector-prefix}-icon.-close`

      Markup:
      <i class="#{theme.$selector-prefix}-icon -close"></i>

      Styleguide: Icon.close
 */

/*
 Illustrative icons

 `.#{theme.$selector-prefix}-icon.-illustrative`

     Markup:
     <i class="sf-fa sf-fa-check-square-o #{theme.$selector-prefix}-icon -illustrative"></i>
     <i class="sf-fa sf-fa-user #{theme.$selector-prefix}-icon -illustrative -size-3xl"></i>

     Styleguide: Icon.illustrative
*/

/*
 Colors

 `.#{theme.$selector-prefix}-icon.-{light, lighter, black, white, color-inherit}`

     Markup:
     <i class="sf-fa sf-fa-folder-o #{theme.$selector-prefix}-icon -light -size-xl"></i>
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -light"></i>
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -light -size-xs"></i>
     <br />
     <i class="sf-fa sf-fa-folder-o #{theme.$selector-prefix}-icon -lighter -size-xl"></i>
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -lighter"></i>
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -lighter -size-xs"></i>
     <br />
     <i class="sf-fa sf-fa-folder-o #{theme.$selector-prefix}-icon -black -size-xl"></i>
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -black"></i>
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -black -size-xs"></i>
     <div style="background-color: grey; color: pink; padding: 5px; max-width: 150px;">
     <i class="sf-fa sf-fa-folder-o #{theme.$selector-prefix}-icon -white -size-xl"></i>
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -white"></i>
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -white -size-xs"></i>
     <br />
     <i class="sf-fa sf-fa-folder-o #{theme.$selector-prefix}-icon -color-inherit -size-xl"></i>
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -color-inherit"></i>
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -color-inherit -size-xs"></i>
     </div>

     Styleguide: Icon.colors
*/

/*
 Icon sizes

 `.#{theme.$selector-prefix}-icon.-size-{7xs, 4xs, 3xs, 2xs, xs, s, m, l, xl, 2xl, 3xl, 4xl, 5xl}`

     Markup:
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -size-7xs"></i>-size-7xs  5px<br />
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -size-4xs"></i>-size-4xs  11px<br />
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -size-3xs"></i>-size-3xs  12px<br />
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -size-2xs"></i>-size-2xs  13px<br />
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -size-xs"></i>-size-xs  14px<br />
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -size-s"></i>-size-s  16px<br />
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -size-m"></i>-size-m  18px<br />
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -size-l"></i>-size-l  20px<br />
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -size-xl"></i>-size-xl  22px<br />
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -size-2xl"></i>-size-2xl  28px<br />
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -size-3xl"></i>-size-3xl  36px<br />
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -size-4xl"></i>-size-4xl  48px<br />
     <i class="sf-fa sf-fa-ellipsis-v #{theme.$selector-prefix}-icon -size-5xl"></i>-size-5xl  60px<br />

     Styleguide: Icon.sizes
 */
