@use "../common/colors";
@use "typography";
@use "link";
@use "sass:math";

$button-margin: 0 5px 0 0;
$button-color: colors.$color-primary-invert;
$button-font-weight: normal;
$button-height-no-unit: 32;
$button-height: $button-height-no-unit*1px;
$button-font-size: typography.$font-size-base;
$button-height-lg: 36px;
$button-font-size-lg: typography.$font-size-lg;
$button-height-sm: 26px;
$button-icon-offset: 5px;
$button-icon-color: colors.$color-secondary-darkest;
$button-no-border-disabled: var(--button-disabled-color);
$button-no-border-darker-disabled: var(--button-disabled-darker-color);
$button-arrow-height: $button-height - 2px; //2px is top + bottom border
$button-arrow-margin: 0;
$button-arrow-padding: 0;
$button-arrow-icon-font-size: var(--icon-size-xs);
$button-dropdown-fixed-width: 260px;
$button-dropdown-top-offset: 8px;
$button-group-search-width: 460px;
$button-group-search-height: $button-height;
$button-group-search-padding-h: 14px;
$button-group-search-border-radius: math.div($button-height, 2);
$button-group-search-icon-font-size: var(--icon-size-s);
$button-group-search-font-size: typography.$font-size-base;
$button-group-search-secondary-bg-color: colors.$color-primary;
$button-group-search-secondary-input-padding-left: 8px;
$button-group-search-secondary-padding: 0;
$button-group-icon-position: 5px;
$button-toggle-height: 46px;
$button-toggle-width: 48px;
$button-circle-padding: 0;
$button-size: (
    default: (
        height: $button-height,
        min-width: $button-height,
        padding: 0 16px,
        font-size: $button-font-size,
        border-radius: 3px
    ),
    large: (
        height: $button-height-lg,
        min-width: $button-height-lg,
        padding: 0 20px,
        font-size: $button-font-size-lg
    ),
    small: (
        height: $button-height-sm,
        min-width: $button-height-sm,
        padding: 0 10px,
        border-radius: 2px
    )
);
$button-types: (
    default: (
        normal: (
            border: var(--button-border-color),
            background: var(--button-background-color),
            color: var(--button-color)
        ),
        hover: (
            border: var(--button-interaction-border-color),
            background: var(--button-interaction-background-color),
            color: var(--button-interaction-color)
        ),
        focus-visible: (
            border: var(--button-interaction-border-color),
            background: var(--button-interaction-background-color),
            color: var(--button-interaction-color)
        ),
        active: (
            border: var(--button-interaction-border-color),
            background: var(--button-interaction-background-color),
            color: var(--button-interaction-color)
        ),
        disabled: (
            border: var(--button-disabled-border-color),
            background: var(--button-disabled-background-color),
            color: var(--button-disabled-color)
        )
    ),
    sidebar-tree: (
        normal: (
            border: colors.$color-secondary-lighter,
            background: colors.$color-secondary-lighter,
            color: colors.$color-primary-invert
        ),
        hover: (
            border: colors.$color-secondary-lighter,
            background: colors.$color-secondary-lighter,
            color: colors.$color-primary-invert
        )
    ),
    sidebar-tree-invert: (
        normal: (
            border: colors.$color-primary-invert,
            background: colors.$color-primary-invert,
            color: colors.$color-primary
        ),
        hover: (
            border: colors.$color-primary-invert,
            background: colors.$color-primary-invert,
            color: colors.$color-primary
        )
    ),
    action: (
        normal: (
            border: var(--action-button-border-color),
            background: var(--action-button-background-color),
            color: var(--action-button-color)
        ),
        hover: (
            border: var(--action-button-interaction-border-color),
            background: var(--action-button-interaction-background-color),
            color: var(--action-button-interaction-color)
        ),
        focus: (
            border: var(--action-button-interaction-border-color),
            background: var(--action-button-interaction-background-color),
            color: var(--action-button-interaction-color)
        ),
        active: (
            border: var(--action-button-interaction-border-color),
            background: var(--action-button-interaction-background-color),
            color: var(--action-button-interaction-color)
        ),
        disabled: (
            border: var(--action-button-disabled-border-color),
            background: var(--action-button-disabled-background-color),
            color: var(--action-button-disabled-color)
        )
    ),
    delete: (
        normal: (
            border: var(--delete-button-border-color),
            background: var(--delete-button-background-color),
            color: var(--delete-button-color)
        ),
        hover: (
            border: var(--delete-button-interaction-border-color),
            background: var(--delete-button-interaction-background-color),
            color: var(--delete-button-interaction-color)
        ),
        focus-visible: (
            border: var(--delete-button-interaction-border-color),
            background: var(--delete-button-interaction-background-color),
            color: var(--delete-button-interaction-color)
        ),
        active: (
            border: var(--delete-button-interaction-border-color),
            background: var(--delete-button-interaction-background-color),
            color: var(--delete-button-interaction-color)
        ),
        disabled: (
            border: var(--delete-button-disabled-border-color),
            background: var(--delete-button-disabled-background-color),
            color: var(--delete-button-disabled-color)
        )
    ),
    link: (
        normal: (
            color: link.$link-color,
            border: transparent,
            background: transparent
        ),
        hover: (
            color: link.$link-color-hover,
            border: transparent,
            background: transparent
        ),
        focus-visible: (
            color: link.$link-color-hover,
            border: transparent,
            background: transparent
        ),
        active: (
            color: link.$link-color-hover,
            border: transparent,
            background: transparent
        ),
        disabled: (
            border: transparent
        )
    ),
    invert: (
        normal: (
            border: colors.$color-primary-invert,
            background: colors.$color-primary-invert,
            color: colors.$color-primary
        )
    )
) !default;
