@use "../vendors/sitefinity/framework";
@use "../tools/mixins/initials";

#{framework.$selector-app} {
    article, aside, details, figcaption, figure, footer, header, hgroup, main, nav, section, summary,
    button, input, optgroup, select, textarea,
    h1, h2, h3, h4, h5, h6,
    p, blockquote, pre,
    dl, dd, ol, ul, li,
    form, fieldset, legend, label,
    table, th, td, caption,
    hr {
        @include initials.all-initials;
    }
}
