@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/common/spacing";
@use "../settings/components/grid";
@use "../tools/mixins/base/utility-spacing";

.#{theme.$selector-prefix}-grid {
    display: grid;
    

    @include utility-spacing.generate-utility-spacing-class("grid-gap", "-gap", spacing.$spacing-list, "&");
    @include utility-spacing.generate-utility-spacing-class("grid-column-gap", "-gap-col", spacing.$spacing-list, "&");
    @include utility-spacing.generate-utility-spacing-class("grid-row-gap", "-gap-row", spacing.$spacing-list, "&");

    &.-albums {
        grid-template-columns: repeat(auto-fill, grid.$grid-album-width);
    }

    &.-col-3 {
        grid-template-columns: 1fr 1fr 1fr;
    }

    &.-col-4 {
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }

    &.-col-equal-4 {
        grid-template-columns: repeat(4, minmax(0, 1fr));
    }
}
