@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/notification";
@use "../settings/components/sidebar";
@use "../settings/common/colors";
@use "../settings/common/zindex";
@use "../tools/mixins/components/arrows";
@use "../tools/mixins/components/button";
@use "../tools/mixins/base/text";
@use "sass:list";
@use "sass:color";

.#{theme.$selector-prefix}-notification {
    position: relative;
    display: flex;
    text-align: left;
    vertical-align: middle;
    padding: notification.$notification-padding;
    border-radius: notification.$notification-border-radius;
    background-color: currentcolor;
    border-color: currentcolor;
    color: notification.$notification-bg;

    > * {
        color: notification.$notification-color;
    }

    @each $notification-type, $color in notification.$notification-types {
        $bg-color: list.nth($color, 1);
        $text-color: if(list.length($color) > 1 and list.nth($color, 2), list.nth($color, 2), notification.$notification-color);
        $icon-color: if(list.length($color) > 2, list.nth($color, 3), color.adjust($bg-color, $lightness: -25));  //darken($bg-color, 25)

        &.-#{$notification-type} {
            color: $bg-color;

            > *:not(.#{theme.$selector-prefix}-notification__close-button) {
                color: $text-color;
            }
        }
    }

    &.-small {
        padding: notification.$notification-sm-padding;
        height: notification.$notification-sm-height;
        align-items: center;
    }

    &.-inline {
        display: inline-flex;
    }

    &.-preline {
        .#{theme.$selector-prefix}-notification__content {
            white-space: pre-line;
        }
    }

    //up

    &.-up,
    &.-up-right,
    &.-up-left {
        margin-bottom: notification.$notification-arrow-size;

        @include arrows.arrow(
            "bottom",
            "center",
            notification.$notification-arrow-size,
            inherit,
            notification.$notification-arrow-shadow-color,
            notification.$notification-arrow-border-size
        );
    }

    //down

    &.-down,
    &.-down-right,
    &.-down-left {
        margin-top: notification.$notification-arrow-size;

        @include arrows.arrow(
            "top",
            "center",
            notification.$notification-arrow-size,
            inherit,
            notification.$notification-arrow-shadow-color,
            notification.$notification-arrow-border-size
        );
    }

    //left

    &.-left,
    &.-left-up,
    &.-left-down {
        margin-right: notification.$notification-arrow-margin;

        @include arrows.arrow(
            "right",
            "center",
            notification.$notification-arrow-size,
            inherit,
            notification.$notification-arrow-shadow-color,
            notification.$notification-arrow-border-size
        );
    }

    //right

    &.-right,
    &.-right-up,
    &.-right-down {
        margin-left: notification.$notification-arrow-margin;

        @include arrows.arrow(
            "left",
            "center",
            notification.$notification-arrow-size,
            inherit,
            notification.$notification-arrow-shadow-color,
            notification.$notification-arrow-border-size
        );
    }

    &.-down-left,
    &.-up-left {
        &::before,
        &::after {
            right: auto;
            left: 20px;
        }
    }

    &.-down-right,
    &.-up-right {
        &::before,
        &::after {
            right: 20px;
        }
    }

    &.-left-down,
    &.-right-down {
        &::before,
        &::after {
            bottom: 10px;
            top: auto;
        }
    }

    &.-left-up,
    &.-right-up {
        &::before,
        &::after {
            top: 20px;
        }
    }

    &.-tooltip {
        padding: notification.$notification-arrow-tooltip-padding;
        border: 1px solid notification.$notification-arrow-shadow-color;
        border-radius: notification.$notification-arrow-tooltip-border-radius;
        box-shadow: notification.$notification-tooltip-box-shadow;
        text-transform: none;

        .#{theme.$selector-prefix}-notification__close-button {
            position: absolute;
            top: notification.$notification-tooltip-close-button-top;
            right: notification.$notification-tooltip-close-button-right;
        }

        @each $arrow-position, $border-position in notification.$notification-arrows-positions {
            @include arrows.set-tooltip-arrow-color($arrow-position, $border-position, notification.$notification-bg);
        }
    }

    &.-help {
        display: block;
        padding: notification.$notification-help-padding;
    }

    &.-faq {
        position: absolute;
        margin-bottom: notification.$notification-faq-margin-bottom;
        bottom: notification.$notification-faq-bottom;
        right: notification.$notification-faq-right;
        width: notification.$notification-faq-width;
    }

    &.-fixed {
        max-width: notification.$notification-fixed-max-width;
    }

    &.-toolset {
        display: inline-block;
        padding: 0;
        height: notification.$notification-toolset-height;
        line-height: notification.$notification-toolset-height;

        .#{theme.$selector-prefix}-notification__content {
            display: flex;
            height: 100%;
        }

        .#{theme.$selector-prefix}-notification__tool {
            padding-left: notification.$notification-toolset-tool-offset;
            padding-right: notification.$notification-toolset-tool-offset;
        }

        .#{theme.$selector-prefix}-notification__tool-group {
            display: inline-flex;
            align-items: center;
            padding-left: notification.$notification-toolset-tool-offset;
            padding-right: notification.$notification-toolset-tool-offset;
            white-space: nowrap;

            .#{theme.$selector-prefix}-notification__tool-button {
                padding: notification.$notification-toolset-tool-inner-offset;

                &:not(:first-of-type) {
                    margin-left: notification.$notification-toolset-tool-group-offset;
                }
            }
        }

        .#{theme.$selector-prefix}-notification__tool-button {
            @include button.toolset-button;

            &:hover {
                cursor: pointer;

                &.-active {
                    color: colors.$color-primary;
                }
            }

            &.-active {
                background-color: notification.$notification-toolset-tool-active-bg;
            }
        }

        .#{theme.$selector-prefix}-notification__tool-dropdown {
            display: inline-flex;
            align-items: center;
            align-self: center;
            min-width: notification.$notification-toolset-dropdown-min-width;
            margin-left: notification.$notification-toolset-tool-offset;
            margin-right: notification.$notification-toolset-tool-offset / 2;
            color: inherit;
            background-color: inherit;

            sf-select {
                flex-grow: 1;
            }

            .sf-select {
                display: flex;
                align-items: center;

                &::after {
                    display: none;
                }

                select {
                    display: flex;
                    align-items: center;
                    background: colors.$color-primary-invert;
                    appearance: auto;
                }
            }
        }

        .#{theme.$selector-prefix}-notification__tool-link {
            @include text.ellipsis;

            display: inline-block;
            max-width: 200px;
            vertical-align: middle;
            padding-left: notification.$notification-toolset-tool-offset;
        }

        .#{theme.$selector-prefix}-notification__tool-separator {
            display: inline-block;
            width: 1px;
            background-color: notification.$notification-toolset-tool-separator-color;
        }
    }

    &.-narrow {
        width: notification.$notification-narrow-width;
    }

    &.-medium {
        width: notification.$notification-medium-width;
    }

    &__close-button {
        @include text.font-awesome;

        position: relative;
        display: block;
        margin-left: notification.$notification-close-button-margin-left;
        order: 4;
        line-height: inherit;
        cursor: pointer;

        .#{theme.$selector-prefix}-icon {
            &:hover {
                color: notification.$notification-close-button-hover-color;
            }
        }
    }

    &__content {
        flex: 1;
        order: 2;
        max-width: 100%;
        word-break: normal;
    }

    &__separator {
        vertical-align: middle;
        margin-left: notification.$notification-separator-padding-horizontal;
        margin-right: notification.$notification-separator-padding-horizontal;
    }
}

.#{theme.$selector-prefix}-notification-wrapper {
    position: fixed;
    top: notification.$notification-wrapper-position-top;
    left: notification.$notification-wrapper-position-left;
    width: notification.$notification-wrapper-width;
    text-align: center;
    z-index: zindex.$zindex-xlg;
    height: notification.$notification-wrapper-height;
}

