@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/common/colors";
@use "../settings/common/zindex";
@use "../settings/components/list";
@use "../tools/mixins/base/text";

.#{theme.$selector-prefix}-list {
    &__item {
        display: flex;
        align-items: center;
        margin-bottom: -#{list.$list-item-border-width};
        padding-left: list.$list-item-horizontal-padding;
        padding-right: list.$list-item-horizontal-padding;
        height: list.$list-item-height;
        border: list.$list-item-border-width solid list.$list-item-border-color;
        background-color: colors.$color-primary;

        &:hover,
        &:focus,
        &:focus-within {
            .sf-list__remove {
                visibility: visible;
            }
        }

        &.-error {
            position: relative;

            &:last-child {
                z-index: zindex.$zindex-xs;
            }
        }

        &.-edit {
            border-left: 0;
            border-right: 0;
            padding-left: 0;
            padding-right: 0;
        }

        &.-error + .-edit {
            position: relative;

            &::before {
                display: block;
                position: absolute;
                content: "";
                width: 100%;
                height: 1px;
                background-color: colors.$color-error;
                top: -1px;
                left: 0;
                z-index: zindex.$zindex-xs;
            }
        }
    }

    &__content {
        @include text.overflow-ellipsis;

        width: 100%;

        &.-editable {
            cursor: text;
        }
    }

    &__remove {
        visibility: hidden;
        margin-left: auto;
        margin-top: -#{list.$list-remove-icon-padding};
        padding: list.$list-remove-icon-padding;
        cursor: pointer;
    }
}
