@use "sass:map";
@use "../common/colors";
@use "icon";

$status-diameter: 22px;
$status-font-size-system: #{map.get(icon.$icon-sizes, "xl")};
$status-text-color: var(--secondary-text-color);
$status-color: colors.$color-primary;
$status-color-secondary: colors.$color-secondary;
$status-icon-default: "f064";
$status-icon-published: "f00c";
$status-icon-locked: "f023";
$status-icon-unpublished: "f00d";
$status-icon-rejected: "f00d";
$status-icon-deleted: "f014";
$status-icon-scheduled: "f017";
$status-icon-draft: "f040";
$status-icon-awaiting: $status-icon-default;
$status-icon-for-translation: "f031";
$status-icon-redirect: "f08e";
$status-icon-group: "f114";
$status-icon-flat: "f02c";
$status-bg-color-default: var(--status-bg-color-default);
$status-bg-color-system: var(--status-icon-color-system);
$status-bg-color-published: var(--status-icon-color-published);
$status-bg-color-scheduled: var(--status-icon-color-scheduled);
$status-bg-color-restricted: var(--status-icon-color-restricted);
$status-bg-color-unpublished: var(--status-icon-color-unpublished);
$status-bg-color-draft: var(--status-icon-color-draft);
