@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/common/colors";

.-#{theme.$selector-prefix}-hover-bg-light-grey:hover {
    background: colors.$color-primary-variant;
}

.-#{theme.$selector-prefix}-bg-primary {
    background: colors.$color-primary;
}
.-#{theme.$selector-prefix}-bg-primary-variant {
    background-color: colors.$color-primary-variant;
}
