@use "../common/colors";
@use "../common/zindex";
@use "../components/button";

$image-width: 240px;
$image-height: 160px;
$image-width-sm: 150px;
$image-height-sm: 100px;
$image-width-xs: 60px;
$image-height-xs: 60px;
$image-single-preview-height: 300px;
$image-single-preview-height-small: 150px;
$image-helpers-edge-offset: 5px;
$image-edit-meta-input-height: 55px;
$image-dimmed-opacity: 0.6;
$image-empty-border: var(--image-empty-border);
$image-not-translated-color: colors.$color-primary;
$image-not-translated-margin-bottom: 15px;
$image-drag-zone-height: 110px;
$image-drag-zone-z-index: zindex.$zindex-2xlg;
$image-hover-background: linear-gradient(rgba(colors.$color-primary-variant, 0), rgba(colors.$color-primary-variant, 0.25));
$document-widht: 200px;
$document-height: 260px;
$document-vertical-padding: 45px;
$document-horizontal-padding: 20px;
$document-status-offset: 20px;
$document-edit-button-right-offset: -6px;
$document-edit-button-bottom-offset: -37px;
$media-chip-max-width: calc(100% - #{button.$button-height-sm + button.$button-icon-offset});
$landscape-width-xs: 82px;
$landscape-height-xs: 58px;
$landscape-width-2xs: 42px;
$landscape-height-2xs: 39px;
$label-font-size: 8px;
$label-padding: 2px 4px;
