@use "../../../settings/components/select";
@use "../base/text";
@use "../../functions/convert";

//needed for integration mode
@mixin select() {
    position: relative;
    height: select.$select-height;

    &::after {
        @include text.font-awesome;

        display: block;
        position: absolute;
        top: 0;
        right: select.$select-arrow-right-position;
        height: select.$select-height;
        line-height: select.$select-height;
        content: convert.get-unicode(#{select.$select-arrow-code});
        font-size: select.$select-arrow-font-size;
        color: select.$select-arrow-color;
        pointer-events: none;
    }
}

//needed for integration mode
@mixin select-item() {
    height: select.$select-item-height;
    width: select.$select-item-width;
    padding-left: select.$select-item-horizontal-spacing;
    padding-right: calc(#{select.$select-item-horizontal-spacing} + #{select.$select-arrow-font-size});
    appearance: none;

    &::-ms-expand {
        display: none;
    }
}
