@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/common/colors";

.-#{theme.$selector-prefix}-border-top {
    border-top: 1px solid colors.$color-secondary;
}
.-#{theme.$selector-prefix}-border-bottom {
    border-bottom: 1px solid colors.$color-secondary;
}
.-#{theme.$selector-prefix}-border-left {
    border-left: 1px solid colors.$color-secondary;
}
.-#{theme.$selector-prefix}-border-right {
    border-right: 1px solid colors.$color-secondary;
}
.-#{theme.$selector-prefix}-border {
    border: 1px solid colors.$color-secondary;
}
.-#{theme.$selector-prefix}-border-lighter {
    border-color: colors.$color-secondary-lighter;
}
.-#{theme.$selector-prefix}-border-lightest {
    border-color: colors.$color-secondary-lightest;
}

.-#{theme.$selector-prefix}-border-radius-5 {
    border-radius: 5px;
}

/*
  Border utility classes

  Border

  `-#{theme.$selector-prefix}-border-top, -#{theme.$selector-prefix}-border-bottom, -#{theme.$selector-prefix}-border-lighter, -#{theme.$selector-prefix}-border-lightest`

      Markup:
      <div class="-#{theme.$selector-prefix}-border-top" style="margin-bottom: 20px;">.-#{theme.$selector-prefix}-border-top</div>
      <div class="-#{theme.$selector-prefix}-border-bottom" style="margin-bottom: 20px;">.-#{theme.$selector-prefix}-border-bottom</div>
      <div class="-#{theme.$selector-prefix}-border-left" style="margin-bottom: 20px;">.-#{theme.$selector-prefix}-border-left</div>
      <div class="-#{theme.$selector-prefix}-border-right" style="margin-bottom: 20px;">.-#{theme.$selector-prefix}-border-right</div>
      <div class="-#{theme.$selector-prefix}-border-bottom -#{theme.$selector-prefix}-border-lighter" style="margin-bottom: 20px;">.-#{theme.$selector-prefix}-border-lighter</div>
      <div class="-#{theme.$selector-prefix}-border-bottom -#{theme.$selector-prefix}-border-lightest">.-#{theme.$selector-prefix}-border-lightest</div>

      Styleguide: Border.classes
 */
