@use "../common/colors";
@use "typography";

//Dimensions
$checkbox-input-icon-size: 15px;
$checkbox-input-icon-font-size: var(--icon-size-2xs);
$checkbox-icon-size: 50%;
$checkbox-border-radius: 1px;

//Label
$checkbox-label-font-weight: typography.$font-weight-normal;
$checkbox-label-margin-left: 5px;
$checkbox-label-font-size: typography.$font-size-lg;

//Colors
$checkbox-color-checked: colors.$color-secondary-lightest;
$checkbox-color-disabled: colors.$color-secondary;
$checkbox-border: var(--checkbox-border);
$checkbox-transition: var(--checkbox-transtion);
$checkbox-border-hover: var(--checkbox-border-hover);
$checkbox-checked-border: var(--checkbox-checked-border);
$checkbox-checked-color: colors.$color-secondary-darkest;
$checkbox-checked-bg-color: colors.$color-secondary-darkest;
$checkbox-bg: colors.$color-primary;
$checkbox-bg-disabled: colors.$color-primary-variant;
$checkbox-bg-active: colors.$color-primary;
$checkbox-description-margin-bottom: 0;
