@use "../vendors/sitefinity/framework";
@use "../settings/base/scroll";
@use "../settings/components/layout";
@use "../settings/components/editor";

.#{framework.$selector-prefix}-main {
    padding-bottom: layout.$main-padding-vertical;
    transition: width framework.$animation-time framework.$animation-timing, padding-right framework.$animation-time framework.$animation-timing;

    &.-list {
        @include framework.center-block;

        display: flex;
        flex-direction: column;
        padding-top: layout.$list-padding-top + framework.$top-toolbar-height;
        padding-left: layout.$list-padding-horizontal;
        padding-right: layout.$list-padding-horizontal;
        width: 100%;

        &.-shrink {
            padding-right: layout.$list-padding-horizontal + framework.$sidebar-width;

            &.-skip-animation {
                transition: none;
            }

            &.-no-width-constraint {
                .sf-main__header,
                .sf-main__paging {
                    width: calc(100vw - #{layout.$list-viewport-horizontal-spacing} - #{framework.$sidebar-width});
                }
            }
        }

        &.-no-width-constraint {
            width: auto;

            .sf-main__header,
            .sf-main__paging {
                transition: width framework.$animation-time framework.$animation-timing;
                max-width: none;
                width: calc(100vw - #{layout.$list-viewport-horizontal-spacing});
            }
        }
    }

    &.-details {
        padding-top: framework.$top-toolbar-height + layout.$main-padding-vertical*0.5 + editor.$editor-height;
        width: calc(100vw - #{scroll.$scroll-width});
        min-height: 100vh;

        &.-shrink,
        &.-shrink-reversed {
            width: calc(100vw - #{framework.$sidebar-width});
        }

        &.-shrink {
            &.-narrow {
                width: calc(100vw - #{framework.$sidebar-width-wide});
            }
        }

        &.-shrink-reversed {
            margin-left: framework.$sidebar-width;
        }
    }
}
