@use "../themes/sitefinity/base" as theme;
@use "../common/colors";

#{theme.$selector-app} {
    //default button
    --button-border-color: #{colors.$color-secondary-lighter};
    --button-background-color: #{colors.$color-primary};
    --button-color: #{colors.$color-primary-invert};
    --button-interaction-border-color: #{colors.$color-secondary-lighter};
    --button-interaction-background-color: #{colors.$color-secondary-lighter};
    --button-interaction-color: #{colors.$color-primary-invert};
    --button-disabled-border-color: #{colors.$color-secondary-lightest};
    --button-disabled-background-color: #{colors.$color-primary};
    --button-disabled-color: #{colors.$color-secondary-lighter};
    --button-disabled-darker-color: #{colors.$color-secondary-darker};

    //action button
    --action-button-border-color: #{colors.$color-success};
    --action-button-background-color: #{colors.$color-success};
    --action-button-color: #{colors.$color-primary};
    --action-button-interaction-border-color: #{darken(desaturate(colors.$color-success, 0.15), 6.47)};
    --action-button-interaction-background-color: #{darken(desaturate(colors.$color-success, 0.15), 6.47)};
    --action-button-interaction-color: #{colors.$color-primary};
    --action-button-disabled-border-color: #{lighten(desaturate(adjust-hue(colors.$color-success, 1), 10.18), 39.02)};
    --action-button-disabled-background-color: #{lighten(desaturate(adjust-hue(colors.$color-success, 1), 10.18), 39.02)};
    --action-button-disabled-color: #{colors.$color-primary};

    //delete button
    --delete-button-border-color: #{colors.$color-error};
    --delete-button-background-color: #{colors.$color-error};
    --delete-button-color: #{colors.$color-primary};
    --delete-button-interaction-border-color: #{darken(desaturate(colors.$color-error, 21.46), 9.8)};
    --delete-button-interaction-background-color: #{darken(desaturate(colors.$color-error, 21.46), 9.8)};
    --delete-button-interaction-color: #{colors.$color-primary};
    --delete-button-disabled-border-color: #{lighten(colors.$color-error, 25.1)};
    --delete-button-disabled-background-color: #{lighten(colors.$color-error, 25.1)};
    --delete-button-disabled-color: #{colors.$color-primary};
}
