@use "../common/colors";

$link-color: var(--link-color);
$link-color-hover: colors.$color-primary-invert;
$link-secondary-color: var(--link-secondary-color);
$link-navigation-margin-bottom: 2px;
$link-navigation-padding: 3px 6px;
$link-navigation-border-radius: 2px;
$link-toolbar-padding: 0 10px;
$link-toolbar-border-width: 0;
$link-toolbar-height: 100%;
$link-side-secondary-color-hover: var(--link-side-secondary-color-hover);
$link-side-secondary-color-active: var(--link-side-secondary-color-active);
$link-side-secondary-color: var(--link-side-secondary-color);
