@use "vendors/sitefinity/framework";
@use "tools/mixins/outline";

/* --- PAGE EDITOR --- */
.sfPageContainer,
.ZoneEditorToolboxContainer {
    height: calc(100vh - 92px);
}

.sf-app {
    display: block;
}

:root {
    &.-sf-integrated-app {
        .sfPageEditor:not(.sfTemplateEditor) .sfMessagePane {
            position: relative;
            display: flex;
            padding: 45px 0 0 48px;
            height: 45px;
            background-color: #f5f5f5;
            border-bottom: 1px solid #eee;
            box-shadow: none;
        }

        .sfPageEditor:not(.sfTemplateEditor) .sfMessagePane::before {
            content: "";
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            height: 46px;
            width: 100%;
            background-color: #fff;
        }

        .sfPageEditor:not(.sfTemplateEditor) .sfMessagePane .sfPagePersomalizationDdlWrp {
            margin-top: 15px;
            margin-left: 0;
            float: none;
            z-index: 999;
        }

        .sfPageEditor:not(.sfTemplateEditor) .RadToolBar.sfLayoutToolbar {
            display: block;
            width: 279px;
            margin-left: auto;
            visibility: visible;
            border-left: 1px solid #eee;
            background-color: transparent;
            position: relative;
            z-index: 999 !important;
        }

        .sfPageEditor:not(.sfTemplateEditor) .RadToolBar.sfLayoutToolbar .rtbOuter {
            padding: 0;
            background-image: none;
            background-color: #f5f5f5;
        }

        .sfPageEditor:not(.sfTemplateEditor) .RadToolBar.sfLayoutToolbar .rtbUL {
            display: flex !important;
            position: relative !important;
        }

        .sfPageEditor:not(.sfTemplateEditor) .RadToolBar.sfLayoutToolbar .rtbItem:last-child::after {
            content: "";
            display: block;
            position: absolute;
            left: 50%;
            bottom: -1px;
            width: 50%;
            height: 2px;
            background-color: #777;
            z-index: 1000 !important;
            transition: left 0.4s;
        }

        .sfPageEditor:not(.sfTemplateEditor) .RadToolBar.sfLayoutToolbar .rtbItem.rtbChecked + .rtbItem:last-child::after {
            left: 0;
        }

        .sfPageEditor:not(.sfTemplateEditor) .RadToolBar.sfLayoutToolbar .rtbItem {
            flex-basis: 50%;
        }

        .sfPageEditor:not(.sfTemplateEditor) .RadToolBar.sfLayoutToolbar .rtbItem .rtbWrap {
            width: 100% !important;
            height: 44px !important;
            margin: 0 !important;
            padding: 0 !important;
            text-align: center !important;
            vertical-align: bottom !important;
            line-height: 44px !important;
            border: 0 !important;
            border-bottom: 0 !important;
            color: #006cd9 !important;
            background-color: transparent !important;
            border-radius: 0 !important;
        }

        .sfPageEditor:not(.sfTemplateEditor) .RadToolBar.sfLayoutToolbar .rtbItemHovered .rtbWrap,
        .sfPageEditor:not(.sfTemplateEditor) .RadToolBar.sfLayoutToolbar .rtbItem.rtbChecked .rtbWrap {
            color: #333 !important;
        }

        .sfPageEditor:not(.sfTemplateEditor) .RadToolBar.sfLayoutToolbar .rtbItem .rtbWrap::before {
            display: none;
        }

        .sfPageEditor:not(.sfTemplateEditor) .ZoneEditorToolboxContainer {
            background-color: #f5f5f5;
        }
        /* Page editor modal overlay */
        .sfPageEditor .sf-loading-wrapper {
            z-index: 1010 !important;
        }

        .sfPageEditor sf-app .sf-top-toolbar {
            &.-page-editor,
            &.-form-editor {
                z-index: 1000 !important;
            }
        }
    }
}

/* end of Page editor modal overlay */
#{framework.$integrated-app} {
    --font-size-base: 1.3rem;

    /* Copied from Sitefintiy */
    &,
    body,
    body > form {
        margin: 0;
        padding: 0;
        border: 0;
        height: 100%;
    }

    body {
        float: left;
        clear: both;
        min-width: 100%;
        /* this is fix for body getting content width when there is horizontal scroll */
        font-family: "Open Sans", Arial, Verdana, sans-serif;
    }
    /* Forms Rules - hide classic page editor when form rules is opened */
    body.sfAdminAppControl {
        overflow-y: auto !important;
    }

    body.sfAdminAppControl .sfMessagePane,
    body.sfAdminAppControl .sfPageEditorToolbarWrapper,
    body.sfAdminAppControl .sfPageWrapper,
    body.sfAdminAppControl .sfWarningLangWrapper {
        display: none !important;
    }

    string, b {
