    }

    .sfTopBar .sfLogo {
        margin-top: 12px;
        order: 3;
    }

    .sfTopBar .sfInlineBlock {
        vertical-align: top;
    }

    div.sfTopBar a.sfLiveSite {
        display: inline-block;
        margin-top: 16px;
        margin-left: 20px;
        color: var(--sf-live-link);
        text-decoration: underline;
        order: 1;
