        overflow: hidden;
        color: #333;
        text-align: right;
        cursor: pointer;
    }

    .sfTopBar .sfUserInfo {
        display: none;
        position: absolute;
        right: 9px;
        margin-top: 0;
        margin-right: -10px;
        margin-bottom: 0;
        padding: 15px 0;
        width: 280px;
        background-color: #fff;
        border: 1px solid var(--sf-user-info-border-color);
