        font-size: 13px;
    }

    .sfTopBar .sfUserHeader img {
        width: 36px;
        height: 36px;
        display: block;
    }

    .sfTopBar .sfUserInfo .sfShortInfo {
        padding: 0 15px;
    }

    .sfTopBar .sfUserInfo .sfShortInfo .sfProfileName {
