        display: block;
        line-height: 130px;
        /*font-size: 4.2rem;*/
    }
    /* Button Switch */
    .sfWrapper .-list sf-button:not(.sf-top-toolbar__nav-button) .-icon.-toggle {
        margin-right: 4px;
        width: 36px;
        height: 32px;
        line-height: 32px;
        border-radius: 3px;
        border: 1px solid transparent;
    }

    .sfWrapper .-list sf-button:not(.sf-top-toolbar__nav-button) .-icon.-toggle .sf-button__icon {
        width: 34px;
        height: 30px;
        line-height: 30px;
        border-radius: 3px;
    }

    .sfWrapper .-list .sf-button.-icon.-toggle.-active {
        border-color: #eee;
    }
    /* Stand alone navigation */
    .sfWrapper .sf-host.-animated {
        position: relative;
        z-index: 1001;
    }
    /* Hide jquery validation messages in Iris */
    sf-app .error[generated] {
        display: none !important;
    }
    /* Send for tranlation pane*/
    .sfWarningLangWrapper .sfWarningField .sfWarningMsg {
        position: absolute;
        z-index: 100;
        top: 46px;
        margin-top: 11px;
        left: 190px; /* 48px + 50px + 85px */
        max-width: calc(100vw - 500px); /* 48px + 50px + 85px + 280px */
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    /* Modal window styles*/
    &.-sf-modal-window-open {
        .modal-open {
            overflow: auto !important;
        }

        .sfPageEditor {
            overflow: initial;

            .k-calendar {
                .k-calendar-monthview,
                .k-calendar-view,
                .k-content.k-scrollable {
                    box-sizing: content-box !important;
                }
            }
        }

        .sfPageWrapper,
        .sfMessagePane,
        .sfWarningLangWrapper {
            display: none !important;
        }

        .k-editor-toolbar > li,
        .k-colorpicker .k-tool-icon {
            margin: 0;
        }

        .k-editor-toolbar .k-editor-widget .k-input-button.k-button {
            background: transparent;
        }

        .k-editor-toolbar .k-button {
            background-color: #f5f5f5;
            border-radius: 0;

            &:hover {
                background-color: #ebebeb;
            }
        }

        .k-window-titlebar {
            .k-spacer {
                display: none;
            }

            .k-window-actions {
                margin: 0;
            }
        }

        .k-picker-wrap .k-select {
            position: static;
        }

        .sfFooter {
            visibility: hidden;
        }

        .sf-upload a {
            font-weight: normal;
        }

        .sf-expander__checkbox,
        .sf-checkbox__input,
        .sf-radio__input {
            display: inline-flex !important;
        }

        .sf-expander__header {
            display: inline-block !important;
            padding: 10px 0 !important;
        }

        .sf-checkbox__label {
            display: flex !important;
            padding: 0 !important;
        }

        .sf-expander__header,
        .sf-checkbox__label {
            &::after,
            &::before {
                display: none !important;
            }
        }

        .sfPageEditor:not(.sfTemplateEditor):not(.-sf-content-block-open) {
            input[type="email"],
            input[type="text"],
            textarea,
            input[type="tel"],
            input[type="password"] {
                &:hover,
                &:focus,
                &:active {
                    border: 1px solid var(--input-border-hover) !important;
                }
            }

            .k-editor-toolbar .k-combobox {
                .k-input {
                    &,
                    &:hover,
                    &:focus,
                    &:active {
                        border: 0 !important;
                    }
                }
            }
        }
    }

    footer.sf-modal-window__buttons.-bottom {
        z-index: 1000;
    }
    /* end of Modal window styles*/
    /* Kendo overrides */
    .k-widget {
        &.k-window.k-window-maximized {
            overflow: auto !important;
        }

        &.k-datepicker,
        .k-state-default > .k-select,
        .k-editor-dropdown .k-dropdown-wrap.k-state-default {
            background-color: transparent;
        }

        &.k-datepicker,
        &.k-timepicker {
            &.-small {
                width: 105px !important;
            }

            &.-medium {
                width: 140px !important;
            }
        }

        .k-picker-wrap {
            &.k-state-default {
                background-color: transparent;
            }

            &.k-state-focused,
            &.k-state-active {
                border-radius: 0;
                box-shadow: none;
            }

            .k-input {
                padding: 0 10px;
            }
        }

        .k-colorpicker {
            display: inline-flex;
            align-items: center;
            box-shadow: none;

            .k-picker-wrap {
                padding: 0;
                height: 100%;
            }

            .k-select {
                position: static;
            }

            .k-icon {
                display: flex;
                align-items: center;
            }
        }

        .k-editor-dropdown .k-dropdown-wrap {
            &.k-state-default .k-input {
                color: inherit;
            }

            &.k-state-active.k-state-border-down {
                box-shadow: none;
            }

            .k-input,
            .k-select {
                display: flex;
            }
        }

        .k-editor-dropdown,
        .k-editor-table-wizard-dialog {
            .k-dropdown-wrap .k-input,
            .k-numeric-wrap .k-input,
            .k-textbox {
                padding: 4px 0 4px 8px !important;
            }

            .k-checkbox,
            .k-checkbox-label {
                display: inline-flex !important;
            }

            .k-checkbox-label::after {
                display: none !important;
            }
        }

        .k-editor-toolbar {
            & > li {
                margin-right: 0;
            }

            .k-state-selected {
                box-shadow: none;
                background-color: transparent !important;
            }

            .k-tool:hover {
                cursor: pointer;
            }
        }

        .k-editor-table-wizard-dialog {
            .k-window-action {
                .k-icon {
                    width: 32px;
                    height: 32px;
                }
            }

            .k-tabstrip-items-wrapper {
                justify-content: center;

                &,
                .k-tabstrip-items {
                    border: 0;
                }

                .k-tabstrip {
                    font-size: 13px;
                }

                .k-item {
                    background: none;
                }

                .k-link {
                    color: #006cd9;
                }
            }

            .k-tabstrip-items .k-item.k-state-hover,
            .k-dropdown-wrap.k-state-hover {
                background-color: transparent;
            }

            .k-edit-field {
                .k-textbox,
                .k-dropdown {
                    width: 100%;
                }

                .k-textbox {
                    @include framework.text-font-family-base;

                    border-radius: 0;
                }

                .k-numerictextbox + .k-dropdown {
                    width: auto;
                }

                .k-numeric-wrap,
                .k-dropdown-wrap,
                .k-picker-wrap {
                    border-radius: 0;
                }

                .k-dropdown-wrap {
                    background-color: transparent;

                    &.k-state-focused {
                        background-color: transparent;
                        box-shadow: 0 0 2px rgb(0 108 217 / 80%);
                    }
                }

                .k-dropdown-wrap .k-select,
                .k-numeric-wrap .k-select,
                .k-picker-wrap .k-select {
                    position: static;
                }
            }

            .k-edit-buttons {
                background: transparent;
                border-top-color: framework.$color-secondary-lighter;
            }

            .k-tabstrip-wrapper .k-content {
                height: auto;
            }
        }
    }

    .k-animation-container .k-list-container.k-state-border-up .k-list {
        .k-item {
            display: flex;

            &:last-child {
                display: flex;
            }

            &.k-state-focused {
                background-color: transparent;
            }
        }
    }
}
/* Outline styles */
body:not(.-sf-outline) {
    :focus {
        outline: 0;
    }

    button::-moz-focus-inner,
    input::-moz-focus-inner {
        border: 0;
        padding: 0;
    }
}

.-sf-outline {
    .sf-checkbox__input,
    .sf-radio__input {
        &:focus {
            + label {
                .sf-radio__icon,
                .sf-checkbox__icon {
                    @include outline.iris-outline;
                }
            }
        }
    }

    .sf-input-file {
        input:focus + label {
            .sf-button {
                @include outline.iris-outline;
            }
        }
    }

    a.k-tool:focus,
    .k-dropdown.k-widget:focus {
        @include outline.iris-outline;
    }

    &.sfPageEditor:not(.sfTemplateEditor) {
        .sf-input input:focus,
        .sf-input textarea:focus {
            box-shadow: 0 0 2px rgba(#006cd9, 0.8) !important;
        }
    }
}

@-webkit-keyframes loadingAnimation {
    0% {
        transform: scaleX(0);
    }

    50% {
        transform: scaleX(1);
    }

    100% {
        transform: translateX(100%);
    }
}

@keyframes loadingAnimation {
    0% {
        transform: scaleX(0);
    }

    50% {
        transform: scaleX(1);
    }

    100% {
        transform: translateX(100%);
    }
}
