@use "../vendors/sitefinity/framework";

.#{framework.$selector-prefix}-sticky {
    position: fixed;
    left: 0;
    right: 0;
    z-index: framework.$zindex-2xlg;
    background-color: framework.$color-primary;

    &.-top {
        top: 0;
        height: framework.$top-toolbar-height;
    }

    &.-bottom {
        bottom: 0;
    }

    &.-left-indent {
        left: 50px;
        padding-right: 50px;
        padding-bottom: 0;
    }
}
