@use "../common/colors";
@use "form";
@use "tree-common";

$tree-list-width-medium: form.$form-width-medium;
$tree-list-width-normal: form.$form-width;
$tree-list-border-color: tree-common.$tree-common-border-color;
$tree-list-border-width: 1px;
$tree-list-hover-bg-color: colors.$color-primary-variant;
$tree-list-active-bg-color: colors.$color-info-bg;
$tree-list-horizontal-spacing: 10px;
$tree-list-horizontal-spacing-bulk: 20px;
$tree-list-vertical-spacing-head: 5px;
$tree-list-vertical-spacing-cell: 10px;
$tree-list-top-offset-cell: 6px;
$tree-list-offset-adjustment-cell: 1px;
$tree-list-vertical-spacing-title: 8px;
$tree-list-expander-flat-width: 32px; //12 + 2 * $tree-list-expander-margin-horizontal
$tree-list-expander-width: 36px; //12 + 2 * $tree-list-expander-margin-horizontal
$tree-list-expander-margin-top: 7px;
$tree-list-expander-margin-horizontal: 10px;
$tree-list-expander-status-margin-top: 9px;
$tree-list-head-cell-title-spacing-left: 52px; // 10px (col padding-left) +  22px (status icon) + 20px (title padding-left)
$tree-list-head-cell-title-thumbnail-spacing: $tree-list-head-cell-title-spacing-left + 54px; // status spacing + 44px thumbanail width + 10px margin between thumbnail and title
$tree-list-head-cell-title-toggle-spacing: $tree-list-expander-width;
$tree-list-child-left-indent: 18;
$tree-list-sidebar-child-left-indent: 25;
$tree-list-row-min-height: 67px;
$tree-list-controls-height: 45px;
$tree-list-status-min-width: 42px;
$tree-list-expander-color: var(--tree-list-expander-color);
$tree-sidebar-padding: 4px 7px;
$tree-sidebar-border-radius: 3px;
$tree-sidebar-margin-bottom: 2px;
$tree-sidebar-hover-bg-color: colors.$color-secondary-lighter;
$tree-sidebar-active-bg-color: colors.$color-primary-invert;
$tree-sidebar-active-color: colors.$color-primary;
$tree-list-media-height: 60px;
$tree-list-padding-horizontal: 50px;
$tree-list-negative-expander-margin: 19px;
$tree-list-cell-4xs: 30px;
$tree-list-cell-3xs: 50px;
$tree-list-cell-2xs: 100px;
$tree-list-cell-xs: 120px;
$tree-list-cell-s: 150px;
$tree-list-cell-sm: 180px;
$tree-list-cell-m: 200px;
$tree-list-cell-l: 250px;
$tree-list-cell-lxl: 270px;
$tree-list-cell-xl: 300px;
$tree-list-cell-2xl: 350px;
$tree-list-cell-3xl: 400px;
$tree-list-cell-4xl: 450px;
$tree-list-cell-media: 190px;
$tree-list-cell-media-wide: 280px;
$tree-list-group-letter-offset: 10px + $tree-list-top-offset-cell - $tree-list-offset-adjustment-cell;

