@use "../common/colors";

$default-icon-size: var(--default-icon-size);
$default-icon-color: var(--default-icon-color);
$icon-svg-width: 1em;
$icon-svg-height: $icon-svg-width;
$icon-svg-template-width: 77px;
$icon-svg-template-height: 300px;

//Icon sizes
$icon-sizes: (
    "7xs": var(--icon-size-7xs),
    "5xs": var(--icon-size-5xs),
    "4xs": var(--icon-size-4xs),
    "3xs": var(--icon-size-3xs),
    "2xs": var(--icon-size-2xs),
    "xs": var(--icon-size-xs),
    "s": var(--icon-size-s),
    "m": var(--icon-size-m),
    "l": var(--icon-size-l),
    "xl": var(--icon-size-xl),
    "2xl": var(--icon-size-2xl),
    "3xl": var(--icon-size-3xl),
    "4xl": var(--icon-size-4xl),
    "5xl": var(--icon-size-5xl),
) !default;

//Illustrative
$icon-illustrative-size: var(--icon-illustrative-size);
$icon-illustrative-font-size: var(--icon-illustrative-font-size);
$icon-illustrative-size-sm: var(--icon-illustrative-size-sm);
$icon-illustrative-font-color: var(--icon-illustrative-font-color);
$icon-illustrative-bg-color: transparent;
$icon-illustrative-border: $icon-illustrative-font-color;

//Faq
$icon-faq-box-shadow: 0 3px 5px rgba(228, 228, 228, 50%);
$icon-faq-size: var(--icon-faq-size);
$icon-faq-bg-color: colors.$color-primary;

//Colors
$icon-light-color: var(--icon-light-color);
$icon-lighter-color: var(--icon-lighter-color);
$icon-lightest-color: colors.$color-secondary;
$icon-darker-color: colors.$color-secondary-darker;
$icon-darkest-color: colors.$color-secondary-darkest;
$icon-black-color: colors.$color-primary-invert;
$icon-white-color: colors.$color-primary;
$icon-warning-color: colors.$color-warning;
$icon-positive-color: var(--icon-positive-color);
$icon-negative-color: var(--icon-negative-color);
$icon-info-color: colors.$color-info;

//Close icon
$icon-close-content: "2716";

//Square icon
$icon-square-border-width: 2px;
$icon-square-padding: 5px;

//Zone editor
$icon-zone-editor-size: var(--icon-zone-editor-size);

//Badge icon

$icon-size-badge: var(--icon-size-badge);
$icon-size-small-badge: var(--icon-size-s);
