@use "../../../settings/components/input";
@use "../../../settings/components/typography";
@use "../../../settings/common/zindex";
@use "../base/props-from-map";
@use "../base/focus";

@mixin read-only {
    padding: input.$input-padding-readonly;
    color: input.$input-color-readonly;
    background-color: input.$input-bg-readonly;
    border-color: input.$input-border-color-readonly;

    &:focus,
    &:hover {
        border-color: input.$input-border-color-readonly;
    }
}

@mixin input-placeholder($element, $styles) {

    #{$element}::-webkit-input-placeholder {
        @include props-from-map.set-properties-from-map($styles);
    }

    #{$element}::-moz-placeholder {  /* Firefox 19+ */
        @include props-from-map.set-properties-from-map($styles);
    }

    #{$element}:-ms-input-placeholder {
        @include props-from-map.set-properties-from-map($styles);
    }
}

@mixin default-inputs() {
    @include focus.focusable;

    display: block;
    font-family: typography.$font-family;
    transition: border-color 0.3s;
    width: 100%;
    border: 1px solid input.$input-border;
    border-radius: input.$input-border-radius;
    background-color: input.$input-bg;
    box-sizing: border-box;
    // Safari hard styles the search input
    appearance: none;

    &:hover {
        border-color: input.$input-border-hover;
    }

    &:disabled {
        background-color: input.$input-bg-disabled;
        color: input.$input-color-disabled;
        user-select: none;

        &:focus,
        &:hover {
            border-color: input.$input-border;
        }
    }
}

@mixin icon() {
    position: absolute;
    text-decoration: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: input.$input-height;
    height: 100%;
    top: 0;
    right: 0;
    margin: 0;
    padding: 0;
    color: inherit;
    opacity: 0;
    transform: scale(0);
    transition: opacity 0.3s ease-out;
    transition-property: opacity, transform;
    transition-delay: 100ms;
    outline: none;
    z-index: zindex.$zindex-2xs;
}

@mixin show-clear-icon() {
    opacity: 1;
    transform: scale(1);
}
