@use "../common/colors";
@use "typography";
@use "sass:math";

$chip-height: 28px;
$chip-max-width: 100%;
$chip-horizontal-padding: 15px;
$chip-font-size: typography.$font-size-h4;
$chip-line-height: $chip-height;
$chip-margin-right: 5px;
$chip-margin-bottom: 5px;
$chip-identifier-dimenssion: 24px;
$chip-identifier-position-top: math.div(($chip-height - $chip-identifier-dimenssion), 2);
$chip-identifier-position-left: $chip-identifier-position-top;
$chip-identifier-position-right: math.div($chip-horizontal-padding, 2);
$chip-identifier-width: $chip-identifier-dimenssion + $chip-identifier-position-left + $chip-identifier-position-right;
$chip-initials-font-size: typography.$font-size-h5;
$chip-editable-min-width: 65px;
$chip-dropdown-top: $chip-height + 5px;
$chip-removable-content-text-indent: 3px;
$chip-removable-content-padding-right: 21px;
$chip-dropdown-max-width: 800px;
$tag-types: (
    default: colors.$color-info-bg, /* background color */
    success: colors.$color-success-bg, /* background color */
    error: colors.$color-error-bg /* background color */,
    invert: colors.$color-primary-invert, /* background color */
    info: colors.$color-info  /* background color */
) !default;
