@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/button";
@use "../settings/common/colors";
@use "../tools/mixins/base/focus";

.#{theme.$selector-prefix}-button-group {
    margin: 0;
    padding: 0;
    display: inline-flex;
    align-items: center;

    .#{theme.$selector-prefix}-input .#{theme.$selector-prefix}-input__field,
    .#{theme.$selector-prefix}-button {
        margin: 0;
        border-right-width: 0;
        border-radius: 0;
    }

    &__input {
        width: 100%;
    }

    &.-search {
        padding: 0 button.$button-group-search-padding-h;
        height: button.$button-group-search-height;
        width: button.$button-group-search-width;
        border-radius: button.$button-group-search-border-radius;
        background-color: colors.$color-primary-variant;

        &:focus-within {
            @include focus.focusable($native: false);
        }

        .#{theme.$selector-prefix}-input__field {
            margin-left: 5px;
            padding-left: 0;
            height: button.$button-group-search-height - 2px;
            font-size: button.$button-group-search-font-size;

            &:focus {
                box-shadow: none;
            }
        }

        .#{theme.$selector-prefix}-input__field,
        .#{theme.$selector-prefix}-button {
            border: 0;
            background-color: transparent;
        }

        .#{theme.$selector-prefix}-button.-icon,
        .#{theme.$selector-prefix}-input__clear-button {
            margin-right: -10px;
        }

        &.-secondary {
            padding: button.$button-group-search-secondary-padding;
            border-radius: 0;
            background-color: button.$button-group-search-secondary-bg-color;
            height: auto;

            .#{theme.$selector-prefix}-input__field {
                padding-left: button.$button-group-search-secondary-input-padding-left;
            }
        }
    }

    &.-fluid {
        width: 100%;
    }

    &.-no-focus-visible {
        &:focus-within {
            box-shadow: none;
        }
    }

    &__icon {
        position: absolute;
        right: button.$button-group-icon-position;
        top: button.$button-group-icon-position;
    }
}

/*
  Button group

  `.#{theme.$selector-prefix}-button-group`

      Markup:
