      <button class="#{theme.$selector-prefix}-button -icon -toggle"><i class="#{theme.$selector-prefix}-icon sf-fa sf-fa-sitemap #{theme.$selector-prefix}-button__icon"></i></button>
      <button class="#{theme.$selector-prefix}-button -active -icon -toggle"><i class="#{theme.$selector-prefix}-icon sf-fa sf-fa-filter #{theme.$selector-prefix}-button__icon"></i></button>
      <button class="#{theme.$selector-prefix}-button -icon -toggle"><i class="#{theme.$selector-prefix}-icon sf-fa sf-fa-cog #{theme.$selector-prefix}-button__icon"></i></button>
      <button class="#{theme.$selector-prefix}-button -icon -toggle"><i class="#{theme.$selector-prefix}-icon sf-fa sf-fa-lightbulb-o #{theme.$selector-prefix}-button__icon"></i></button>
      <button class="#{theme.$selector-prefix}-button -icon -toggle"><i class="#{theme.$selector-prefix}-icon sf-fa sf-fa-th-list #{theme.$selector-prefix}-button__icon"></i></button>
