@use "../vendors/sitefinity/framework";
@use "../tools/mixins/text";
@use "../settings/components/logo";

.#{framework.$selector-prefix}-logo {
    @include text.hide-text-for-bg-image;
    @include framework.center-block;

    padding-top: logo.$logo-top-offset;
    width: logo.$logo-width;
    height: logo.$logo-height;
    background: transparent url(#{logo.$logo-image}) no-repeat 0 logo.$logo-top-offset;

    &.-fixed {
        position: fixed;
        top: logo.$logo-position-top;
    }
}
