@mixin all-initials($important: null) {
    align-content: stretch $important;
    align-items: stretch $important;
    align-self: auto $important;
    animation: none $important;
    backface-visibility: visible $important;
    background: transparent none $important;
    border: 0 $important;
    bottom: auto $important;
    box-decoration-break: slice $important;
    box-shadow: none $important;
    box-sizing: border-box $important; // not initial
    break-after: auto $important;
    break-before: auto $important;
    break-inside: auto $important;
    caption-side: top $important;
    caret-color: auto $important;
    clear: none $important;
    clip: auto $important;
    clip-path: none $important;
    color: inherit $important;
    columns: auto $important;
    content: normal $important;
    counter-increment: none $important;
    counter-reset: none $important;
    //cursor: auto;
    //display: inline; // ??????
    empty-cells: show $important;
    font-family: inherit $important;
    filter: none $important;
    flex-direction: row $important;
    flex: 0 1 auto $important;
    float: none $important;
    font-feature-settings: normal $important;
    font-kerning: auto $important;
    font-language-override: normal $important;
    font-size-adjust: none $important;
    font-stretch: normal $important;
    font-synthesis: weight style $important;
    grid: none $important;
    height: auto $important;
    hyphens: manual $important;
    image-orientation: 0deg $important;
    image-rendering: auto $important;
    image-resolution: 1dppx $important;
    ime-mode: auto $important;
    inline-size: auto $important;
    isolation: auto $important;
    justify-content: flex-start $important;
    left: auto $important;
    letter-spacing: normal $important;
    line-break: auto $important;
    margin: 0 $important;
    max-height: none $important;
    max-width: none $important;
    min-height: 0 $important;
    min-width: 0 $important;
    object-fit: fill $important;
    object-position: 50% 50% $important;
    opacity: 1 $important;
    order: 0 $important;
    orphans: 2 $important;
    outline: medium invert initial $important;
    outline-offset: 0 $important;
    overflow: visible $important;
    overflow-wrap: normal $important;
    overflow-x: visible $important;
    overflow-y: visible $important;
    padding: 0 $important;
    page-break-after: auto $important;
    page-break-before: auto $important;
    page-break-inside: auto $important;
    perspective: none $important;
    perspective-origin: 50% 50% $important;
    position: static $important;
    quotes: initial $important;
    resize: none $important;
    right: auto $important;
    ruby-align: space-around $important;
    ruby-merge: separate $important;
    ruby-position: over $important;
    scroll-behavior: auto $important;
    scroll-snap-coordinate: none $important;
    scroll-snap-destination: 0 0 $important;
    scroll-snap-points-x: none $important;
    scroll-snap-points-y: none $important;
    scroll-snap-type: none $important;
    shape-image-threshold: 0% $important;
    shape-margin: 0 $important;
    shape-outside: none $important;
    tab-size: 8 $important;
    table-layout: auto $important;
    text-align: inherit $important; // not initial
    text-align-last: auto $important;
    text-combine-upright: none $important;
    text-decoration: none $important;
    text-emphasis: none $important;
    text-indent: 0 $important;
    text-justify: auto $important;
    text-orientation: mixed $important;
    text-overflow: clip $important;
    text-rendering: auto $important;
    text-shadow: none $important;
    text-transform: inherit $important; //not initial
    text-underline-position: auto $important;
    top: auto $important;
    touch-action: auto $important;
    transform: none $important;
    transform-box: border-box $important;
    transform-origin: 50% 50% 0 $important;
    transform-style: flat $important;
    transition: all 0s ease 0s $important;
    vertical-align: inherit $important; //not initial
    //visibility: visible;
    white-space: inherit $important; //not initial
    widows: 2 $important;
    width: auto $important;
    will-change: auto $important;
    word-break: normal $important;
    word-spacing: normal $important;
    word-wrap: normal $important;
    z-index: auto $important;
}
