@use "../settings/components/scrollbar";
@use "../tools/functions/colors" as colorFunctions;

::-webkit-scrollbar {
    width: scrollbar.$scrollbar-size;
    height: scrollbar.$scrollbar-size;
}

::-webkit-scrollbar-button {
    width: 0;
    height: 0;
    display: none;
}

::-webkit-scrollbar-track {
    background-color: scrollbar.$scrollbar-secondary;
}

::-webkit-scrollbar-thumb {
    border-radius: 2px;
    background-color: colorFunctions.get-tint(scrollbar.$scrollbar-primary, 70);

    &:hover {
        background-color: colorFunctions.get-tint(scrollbar.$scrollbar-primary, 65);
    }

    &:active {
        background-color: colorFunctions.get-tint(scrollbar.$scrollbar-primary, 60);
    }
}

.-scrollbar-small {
    &::-webkit-scrollbar {
        border-radius: 1px;
        width: 5px;
        height: 5px;
    }
}
