@use "../vendors/sitefinity/framework";
@use "../settings/components/masonry";

.#{framework.$selector-prefix}-masonry {
    &__gutter {
        width: masonry.$gutter-width;
    }

    &__item {
        width: calc(100% / masonry.$columns-count - (masonry.$gutter-width * (masonry.$columns-count - 1) / masonry.$columns-count));

        img {
            margin-bottom: masonry.$img-margin-bottom;
        }
    }
}
