@use "../vendors/sitefinity/framework";

.#{framework.$selector-prefix}-form {

    .#{framework.$selector-prefix}-field:not(.-sequence):not(.-table):not(.-group) {
        margin-bottom: framework.$spacing-4xs;
        padding-top: framework.$spacing-3xs;
        padding-bottom: framework.$spacing-2xs;

        .#{framework.$selector-prefix}-input {
            margin-bottom: 0;
        }
    }
}
