@use "../../../settings/components/tree";
@use "../../../settings/components/tree-common";

//needed for integration mode
@mixin tree-checkbox() {
    padding-top: tree.$tree-items-outer-padding-top;
    width: tree.$tree-checkbox-wrapper-width;
    text-align: center;
}

//needed for integration mode
@mixin tree-inner() {
    display: flex;
    align-items: center;
    flex: auto;
    position: relative;
    padding: tree.$tree-items-space-between;
    max-width: tree.$tree-items-inner-max-width;
    //otherwise elements overflows its parent before triggering overflow ellipsis
    min-width: 0;
    cursor: pointer;
}

@mixin tree-common-header() {
    font-size: tree-common.$tree-common-head-font-size;
    font-weight: 400;
    text-transform: uppercase;
    text-align: left;
    color: tree-common.$tree-common-head-color;
}
