@use "button";

$expander-margin-bottom: 10px;
$expander-header-padding-vertical: var(--expander-header-padding-vertical, 10px);
$expander-header-padding-horizontal: var(--expander-header-padding-horizontal, 0);
$expander-icon-width: var(--expander-icon-width, #{button.$button-height});
$expander-icon-margin-right: var(--expander-icon-margin-right, 28px);
$expander-icon-margin-left: var(--expander-icon-margin-left, 0);
$expander-left-offset: var(--expander-left-offset);
$expander-section-padding: var(--expander-section-padding, 0);
$expander-section-margin-right: var(--expander-section-margin-right, 0);
$expander-section-background: var(--expander-section-background, transparent);
