@use "../themes/sitefinity/base" as theme;
@use "../common/colors";

#{theme.$selector-app} {
    --default-icon-color: #{colors.$color-secondary-darkest};
    --icon-lighter-color: #{colors.$color-secondary-lightest};
    --icon-light-color: #{colors.$color-secondary};
    --icon-illustrative-font-color: #{colors.$color-secondary-lightest};
    --icon-positive-color: #{colors.$color-success};
    --icon-negative-color: #{colors.$color-error};
    --icon-badge-add-color: #{colors.$color-primary};
    --icon-badge-add-bg-color: #{colors.$color-info-light};
    --icon-badge-add-border-color: #{colors.$color-info-light};
}
