@mixin generate-list-row-indent($parentClass, $childClass, $step: 40, $depth: 20) {
    $selector: $parentClass + " " + $childClass;

    @for $i from 1 to $depth {
        #{$selector} {
            $paddingLeft: ($i * $step) + "px !important";

            margin-left: #{$paddingLeft};
        }

        $selector: $parentClass + " " + $selector;
    }
}

@mixin generate-list-title-min-width($parentClass, $childClass, $step: 40, $depth: 20, $width: 200) {
    $selector: $parentClass + " " + $childClass;

    @for $i from 1 to $depth {
        #{$selector} {
            $minWidth: ($width - $i * $step) + "px";

            min-width: #{$minWidth};
        }

        $selector: $parentClass + " " + $selector;
    }
}

@mixin generate-reorder-list-row-indent($parentClass, $childClass, $step: 18, $depth: 20, $initialStep: 25) {
    $selector: $parentClass + " " + $childClass;

    @for $i from 1 to $depth {
        #{$selector} {
            $paddingLeft: ($initialStep + ($i * $step));

            margin-left: #{$paddingLeft + "px !important"};
            z-index: #{1000 + $paddingLeft};
        }

        $selector: $parentClass + " " + $selector;
    }
}
