@use "../vendors/sitefinity/framework";

.#{framework.$selector-prefix}-main-list-header {
    display: grid;
    grid-template-areas: "nav nav"
        "title buttons"
        "progress progress"
        "bulk bulk"
        "notification notification"
        "empty-message empty-message";
    grid-template-columns: minmax(100%, max-content) minmax(0, max-content);
    width: 100%;

    &.-media {
        grid-template-areas: "title buttons"
            "progress progress"
            "bulk bulk"
            "notification notification"
            "nav nav"
            "empty-message empty-message";
    }

    &.-empty {
        grid-template-areas: "nav" "title" "notification" "progress" "empty-message" "buttons" "bulk";
        justify-items: center;

        &.-media {
            grid-template-areas: "title" "notification" "nav" "progress" "empty-message" "buttons" "bulk";
        }
    }

    &:not(.-empty) {
        .sf-main-list-header__buttons {
            justify-self: end;
            align-self: end;
            white-space: nowrap;
        }
    }

    &__nav {
        grid-area: nav;
    }

    &__title {
        grid-area: title;
        max-width: 70%;
    }

    &__buttons {
        grid-area: buttons;
    }

    &__bulk {
        grid-area: bulk;
    }

    &__notification {
        grid-area: notification;
    }

    &__empty-message {
        grid-area: empty-message;
    }

    &__progress {
        grid-area: progress;
        width: 350px;
    }
}

@media (min-height: 540px) {
    .#{framework.$selector-prefix}-main-list-header__empty-message {
        margin-bottom: 60px;
        margin-top: 25px;
    }
}
