@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/common/rem";
@use "../settings/common/colors";
@use "../settings/components/typography";
@use "sass:math";

html, body {
    margin: 0;
    padding: 0;
    height: 100%;
}

html:not(.sfPageEditorWrp) {
    font-size: math.div(rem.$zoom, 16px) * rem.$rem-baseline;
    min-height: 100%;
}

#{theme.$integrated-app} body:not(.sfPageEditor) {
    font-size: typography.$font-size-base;
    background-color: colors.$color-primary;
    overflow-x: auto;
    overflow-y: scroll;
}
