@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/form";
@use "../settings/common/colors";

.#{theme.$selector-prefix}-form {
    max-width: form.$form-width;
    padding: form.$form-padding;

    .#{theme.$selector-prefix}-input {
        margin-bottom: form.$form-field-margin-bottom;

        &.-shrinked,
        .#{theme.$selector-prefix}-input {
            margin-bottom: 0;
        }
    }

    .#{theme.$selector-prefix}-checkbox:not(.-inline):not(.-in-group),
    .#{theme.$selector-prefix}-radio:not(.-inline):not(.-in-group) {
        margin-bottom: form.$form-field-choice-margin-bottom;

        &:last-child {
            margin-bottom: 0;
        }
    }

    .#{theme.$selector-prefix}-checkbox,
    .#{theme.$selector-prefix}-radio {
        margin-right: form.$form-field-choice-margin-bottom;

        &:last-child {
            margin-right: 0;
        }
    }

    &.-auto {
        width: auto;
    }

    &.-small {
        width: form.$form-width-small;
    }

    &.-medium {
        width: form.$form-width-medium;
    }

    &.-large {
        width: form.$form-width-large;
        max-width: form.$form-width-large;
    }

    &.-short {
        padding-top: 0;
        padding-bottom: 0;
    }

    &.-framed {
        border: 1px solid colors.$color-secondary-lightest;
        border-radius: 5px;
    }

    &.-condensed {

        .#{theme.$selector-prefix}-input {
            margin-bottom: 15px;
        }
    }
}

