@import "./font/WebComponentsIcons.scss";

@include exports( "icons/layout" ) {
    $icon-font-url: map-get( $data-uris, "WebComponentsIcons.ttf" ) !default;

    // Keep this as a root selector, because nested themes will not work
    @at-root {
        @font-face {
            font-family: "WebComponentsIcons";
            font-style: normal;
            font-weight: normal;
            src: url(#{$icon-font-url}) format( "truetype" );
        }
    }

    // Icon wrap
    .k-icon-wrap {
        flex: none;
        display: inline-flex;
        flex-flow: row nowrap;
        gap: 0;
        align-items: center;
        align-self: flex-start;
        vertical-align: middle;
        position: relative;

        &::before {
            content: "\200b";
            width: 0;
            overflow: hidden;
            flex: none;
            display: inline-block;
            vertical-align: top;
        }
    }

    .k-icon {
        width: 1em;
        height: 1em;
        outline: 0;
        font-size: $icon-size;
        font-family: "WebComponentsIcons"; // stylelint-disable-line
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        line-height: 1;
        speak: none;
        text-transform: none;
        text-decoration: none;
        flex-shrink: 0;
        display: inline-flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: center;
        vertical-align: middle;
        position: relative;
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;

        &:hover,
        &:focus {
            text-decoration: none;
        }
    }

    .k-svg-icon {
        width: 1em;
        height: 1em;
        display: inline-flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: center;
        vertical-align: middle;
        position: relative;

        > svg {
            fill: currentColor;
            flex: 1 0 auto;
        }

        &.k-flip-h,
        &.k-flip-v,
        &.k-flip-h.k-flip-v {
            transform: none;
        }

        &.k-flip-h {
            > svg {
                transform: scaleX( -1 );
            }
        }

        &.k-flip-v {
            > svg {
                transform: scaleY( -1 );
            }
        }

        &.k-flip-h.k-flip-v {
            > svg {
                transform: scale( -1, -1 );
            }
        }
    }

    .k-icon-with-modifier {
        position: relative;
        margin: .25em;
    }

    .k-icon-modifier {
        position: absolute;
        font-size: .5em;
        bottom: 0;
        right: 0;
        margin: 0 -.5em -.5em 0;
    }

    .k-i-none::before {
        content: "";
        display: none;
    }

    .k-icon-action {
        padding: $icon-padding;
        cursor: pointer;
    }

    .k-icon-xs { font-size: ( $icon-size * .5 ); }
    .k-icon-sm { font-size: ( $icon-size * .75 ); }
    .k-icon-md { font-size: ( $icon-size * 2 ); }
    .k-icon-lg { font-size: ( $icon-size * 3 ); }
    .k-icon-xl { font-size: ( $icon-size * 4 ); }


