@use "../../vendors/sitefinity/framework";

$item-height: framework.$input-height;
$item-padding: framework.$input-padding;
$item-font-size: framework.$input-font-size;
$item-border-color: framework.$input-border;
$item-border-radius: framework.$input-border-radius;
$item-border-width: 1px;
$item-error-color: framework.$input-required-color;

// Right align
$item-right-margin: 10px;
$item-right-font-size: framework.$font-size-base;

// Unit field
$item-unit-horizontal-padding: framework.$input-unit-horizontal-padding;
$item-unit-dimensions: framework.$input-unit-dimensions - (2 * $item-border-width); // removing the size of the borders so that when validation is triggered there's no need for overriding colors
$item-unit-dimensions-in-tree: 36px;
$item-unit-bg-color: framework.$input-unit-bg-color;
$item-unit-before-horizontal-position: framework.$input-unit-before-horizontal-position;
$item-unit-before-horizontal-width: framework.$input-unit-before-horizontal-width;
$item-unit-bg-color: framework.$input-unit-bg-color;
