@use "../vendors/sitefinity/framework";

.#{framework.$selector-prefix}-zone {
    &__frame {
        position: fixed;
        left: 0;
        border: 0;
        //sidebar toggle width
        width: calc(100% - #{framework.$sidebar-toggle-width});
        //toolbar height
        height: calc(100vh - #{framework.$top-toolbar-height});
        top: framework.$top-toolbar-height;
        z-index: framework.$zindex-xs;
        transition: framework.$animation-time framework.$animation-timing, padding-right framework.$animation-time framework.$animation-timing;

        &.-shrink {
            padding-right: framework.$sidebar-width - framework.$sidebar-toggle-width;
        }

        &.-wide-shrink {
            padding-right: framework.$sidebar-width-wide - framework.$sidebar-toggle-width;
        }
    }
}

//scroll is in the iframe, we shouldn't have a scrollbar in the page editor
.-sf-integrated-app .-sf-new-zone-editor:not(.sfPageEditor) {
    overflow-y: auto;
    overflow-x: hidden;
}
//hiding Sitefinity Header in new page editor for .net core
.-sf-new-zone-editor .sfHeader {
    display: none;
}
