@use "../common/colors";
@use "typography";

$input-height: 36px;
$input-short-height: 28px;
$input-line-height: 18px;
$input-padding: 0 10px;
$input-font-size: typography.$font-size-lg;
$input-border-radius: 0;
$input-color: colors.$color-primary-invert;
$input-placeholder-color: var(--input-placeholder-color);
$input-color-disabled: colors.$color-secondary;
$input-color-readonly: colors.$color-primary-invert;
$input-bg: colors.$color-primary;
$input-bg-disabled: colors.$color-primary-variant;
$input-bg-readonly: transparent;
$input-border-color-readonly: transparent;
$input-border: var(--input-border);
$input-border-hover: var(--input-border-hover);
$input-padding-readonly: 0;
$textarea-min-height: 80px;
$textarea-min-height-short: 60px;
$textarea-min-height-large: 145px;
$textarea-padding: 8px 10px;
$input-label-margin: 0 0 5px;
$input-label-font-weight: 700;
$input-label-font-size: typography.$font-size-h4-lg;
$input-required-margin: 5px 0 0;
$input-title-required-padding-top: 3px;
$input-title-required-margin-top: -20px;
$input-required-color: var(--color-error);
$input-hint-color: var(--secondary-text-color);
$input-hint-margin: 5px 0 0;
$input-title-placeholder-color: var(--input-title-placeholder-color);
$input-title-font-family: typography.$font-family-title;
$input-title-font-size: typography.$font-size-h1;
$input-title-line-height: typography.$line-height-heading;
$input-title-font-weight: bold;
$input-title-required-color: colors.$color-error;
$input-title-placeholder-color: var(--input-title-placeholder-color);
$input-side-buttons-top: 2em;
$input-clear-button-color: colors.$color-secondary-darkest;
$input-clear-button-color-hover: colors.$color-primary-invert;
$input-side-content-position-top: 1.3em;
$input-side-content-color: colors.$color-secondary;
$input-unit-dimensions: $input-height;
$input-unit-bg-color: colors.$color-primary-variant;
$input-unit-horizontal-padding: 10px;
$input-unit-before-horizontal-width: 3px;
$input-unit-before-horizontal-position: $input-unit-horizontal-padding + 1px;
$textarea-side-content-margin-left: 10px;
$textarea-side-content-background: transparent;
$textarea-side-content-border: 0;
$textarea-side-content-font-size: typography.$font-size-base;
$textarea-side-content-line-height: typography.$line-height-base;
$textarea-side-content-color: colors.$color-primary-invert;
$textarea-side-content-position-left: 100%;
$textarea-side-content-position-right: auto;
$textarea-side-content-position-top: 1.8em;
$textarea-side-content-height: auto;
$textarea-side-content-width: auto;
$textarea-side-content-text-align: left;
$textarea-side-content-margin-bottom: 0;
$textarea-side-content-counter-position-top: 4.3em;
$input-side-content-padding-right: 46px;
$input-side-content-padding-left: 10px;
$input-char-counter-margin-left: 10px;
$input-char-counter-font-size: typography.$font-size-base;
$input-char-counter-position-top: 0.7em;
$input-char-counter-position-left: 100%;
$input-char-counter-success-color: var(--input-char-counter-success-color);
$input-char-counter-error-color: var(--input-char-counter-error-color);
$input-autofill-shadow-color: colors.$color-primary;
$input-no-value-font-size: typography.$font-size-lg;
$input-no-value-color: colors.$color-secondary;
$input-inline-height: 22px;
$input-inline-font-size: typography.$font-size-base;
