@mixin block() {
    margin-left: auto;
    margin-right: auto;
}

@mixin horizontal-and-vertical() {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
}

@mixin flex() {
    align-items: center;
    justify-content: center;
}
