@use "../vendors/sitefinity/framework";

progress {
    &[value] {
        appearance: none;
        width: 100%;
        height: 7px;
        border: 0;
        background-color: framework.$color-secondary-lighter;
        color: framework.$color-success;

        &::-webkit-progress-bar {
            background-color: framework.$color-secondary-lighter;
        }

        &::-webkit-progress-value {
            background-color: framework.$color-success;
        }

        &::-moz-progress-bar {
            background-color: framework.$color-success;
        }
    }

    &.-failure[value] {
        &::-webkit-progress-value {
            background-color: framework.$color-error;
        }

        &::-moz-progress-bar {
            background-color: framework.$color-error;
        }

        color: framework.$color-error;
    }

    &.-small {
        width: 80px;
    }
}
