@use "../vendors/sitefinity/framework";
@use "../settings/components/item";

.#{framework.$selector-prefix}-item {
    display: flex;
    align-items: center;
    height: item.$item-height;
    font-size: item.$item-font-size;
    border: item.$item-border-width solid item.$item-border-color;
    border-radius: item.$item-border-radius;
    background-color: framework.$color-primary;

    &:hover,
    &:focus,
    &:focus-within,
    &.-loading {
        .sf-item__right-align {
            visibility: visible;
        }
    }

    &.-error {
        border-color: item.$item-error-color;

        .sf-item__unit::before {
            color: item.$item-error-color;
        }
    }

    &.-loading {
        .sf-item__right-align {
            min-width: 40px;
        }
    }

    &__content {
        @include framework.text-overflow-ellipsis;

        flex: 1;
        padding: item.$item-padding;
        width: 100%;
    }

    &__right-align {
        position: relative;
        visibility: hidden;
        flex-shrink: 0;
        font-size: item.$item-right-font-size;
        margin-left: auto;
        margin-right: item.$item-right-margin;
    }

    &__unit {
        text-decoration: none;
        text-align: center;
        vertical-align: middle;
        padding-left: item.$item-unit-horizontal-padding;
        padding-right: item.$item-unit-horizontal-padding;
        width: auto;
        height: item.$item-unit-dimensions;
        line-height: item.$item-unit-dimensions;
        background-color: item.$item-unit-bg-color;
        outline: none;
        white-space: nowrap;

        sup {
            position: relative;
            top: -0.4rem;
            vertical-align: baseline;
        }

        &.-in-tree {
            height: item.$item-unit-dimensions-in-tree;
            line-height: item.$item-unit-dimensions-in-tree;
        }

        &.-block {
            display: block;
        }
    }
}
