@use "../settings/themes/sitefinity/base" as theme;
@use "../tools/mixins/base/center";

.-#{theme.$selector-prefix}-centered-box {
    @include center.block;
}
.-#{theme.$selector-prefix}-centered-vertical-box {
    @include center.horizontal-and-vertical;
}

.-#{theme.$selector-prefix}-full-height {
    height: 100% !important;
}

.-#{theme.$selector-prefix}-auto-height {
    height: auto!important;
}

.-#{theme.$selector-prefix}-full-width {
    width: 100% !important;
}

/*
  Centered box

  `-#{theme.$selector-prefix}-centered-box`

  Sets box's right and left margins to auto to center the box horizontally if it has width.

      Markup:
      <div style="width: 200px" class="-#{theme.$selector-prefix}-centered-box">Centered box with fixed width</div>

      Styleguide: Center utility classes.centered
 */
/*
  Centered horizontally and vertically box

  `-#{theme.$selector-prefix}-centered-vertical-box` Sets box's right and left margins to auto to center the box horizontally if it has width and set it's display to flex and min-height to 100% of the view height.

      Markup:
      <div class="-#{theme.$selector-prefix}-centered-vertical-box">Centered box with fixed width</div>

      Styleguide: Center utility classes.horizontally-vertically-centered
 */
/*
  Expands element to 100% height of parent

  `-#{theme.$selector-prefix}-full-height`

      Markup:
      <div style="height: 300px; width: 300px; border:1px solid #eee">
       <div class="-#{theme.$selector-prefix}-full-height" style="background-color: #fefefe;">Grey child is as tall as parent</div>
      </div>

      Styleguide: Center utility classes.full-height
 */
