@use "../settings/themes/sitefinity/base" as theme;
@use "../tools/mixins/components/item-overlay";
@use "../tools/mixins/base/image";
@use "../settings/components/typography";
@use "../settings/common/colors";
@use "../settings/common/zindex";
@use "../settings/components/media";
@use "../settings/components/video";
@use "../tools/mixins/base/animation";

.#{theme.$selector-prefix}-media {
    position: relative;
    z-index: zindex.$zindex-xs;

    &:not(.-document),
    &:not(.-album) {
        height: 100%;
    }

    &:hover,
    &:focus,
    &:active {
        z-index: zindex.$zindex-md;

        .sf-media__details,
        .sf-media__selected-indicator {
            visibility: visible;
        }
    }

    &:not(:focus-within, :hover, .-remove-button-visible) {
        .sf-media__remove {

            .#{theme.$selector-prefix}-button {
                padding: 0;
                position: absolute;
                height: 1px;
                width: 1px;
                overflow: hidden;
                clip: rect(1px, 1px, 1px, 1px);
                border: 0;
            }
        }
    }

    &:not(.-selected) {
        .sf-media__selected-indicator {
            opacity: 0.5;
        }
    }

    .sf-media__svg-wrapper {
        flex: 1;
    }

    &:not(.sf-placeholder.-svg) {
        .sf-media__svg-wrapper {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            max-height: 100%;
            overflow: hidden;
        }
    }

    &.-mvc::after {
        content: "mvc";
        position: absolute;
        bottom: 0;
        right: 0;
        padding: media.$label-padding;
        font-size: media.$label-font-size;
        color: colors.$color-primary;
        background-color: colors.$color-info;
        text-transform: uppercase;
    }

    .-auto-height {
        .sf-placeholder {
            height: auto;
        }
    }

    img {
        max-width: 100%;
        max-height: 100%;
        height: auto;
        vertical-align: top;
    }

    &__chip {
        max-width: media.$media-chip-max-width;

        .sf-chip {
            margin-bottom: 0;
        }
    }

    &.-empty {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: media.$image-width;
        height: media.$image-height;
        color: colors.$color-secondary-lightest;
        border: 1px dashed media.$image-empty-border;

        &.-small {
            width: media.$image-width-sm;
            height: media.$image-height-sm;
        }
    }

    &.-no-zindex {
        z-index: auto;

        &.-dimmed {
            &::after {
                z-index: auto;
            }
        }
    }

    &.-smaller {
        width: media.$image-width-xs;
        height: media.$image-height-xs;
    }

    &.-landscape {
        width: media.$landscape-width-xs;
        height: media.$landscape-height-xs;
        border: 1px solid colors.$color-primary-variant;
    }

    &.-landscape-smaller {
        width: media.$landscape-width-2xs;
        height: media.$landscape-height-2xs;
    }

    &.-cropped {
        overflow: hidden;

        img {
            max-height: none;
        }
    }

    &.-wide {
        img {
            max-width: none;
            height: 100%;
            width: auto;
        }
    }

    &.-preview {
        height: auto;
        max-width: 100%;

        video {
            min-height: Min(#{video.$video-single-preview-height}, 100%);
        }

        &.-document {
            .sf-input-file {
                position: absolute;
                right: media.$document-edit-button-right-offset;
                bottom: media.$document-edit-button-bottom-offset;
            }
        }
    }

    // Sets image and video height in order to be responsive and not to go above certain height
    // Used in modals: Pages displaying this image/video
    &.-no-placeholder {
        video,
        img {
            max-height: media.$image-single-preview-height-small;
            width: auto;
        }
    }

    &.-prepreview {
        height: 300px;

        &.-small {
            height: 150px;
        }
    }

    &.-selectable {
        cursor: pointer;

        &:not(.-dimmed, .-selected) {
            &:hover,
            &:focus,
            &:active {
                &::after {
                    @include item-overlay.item-overlay(media.$image-hover-background, 1);

                    pointer-events: none;
                }
            }
        }
    }

    &.-selected {
        .sf-media__selected-indicator,
        .sf-media__remove {
            visibility: visible;
        }

        &::after {
            @include item-overlay.item-overlay($zIndex: zindex.$zindex-sm);
        }
    }

    &.-dimmed {
        .sf-media__no-selectable {
            visibility: visible;
        }

        &::after {
            @include item-overlay.item-overlay($bg-color: colors.$color-secondary, $opacity: media.$image-dimmed-opacity, $zIndex: zindex.$zindex-sm);
        }
    }

    &.-dimmed-hover {
        &:hover {
            .sf-media__properties-label {
                visibility: visible;
            }

            &::after {
                @include item-overlay.item-overlay($bg-color: colors.$color-secondary, $opacity: media.$image-dimmed-opacity, $zIndex: zindex.$zindex-sm);
            }
        }
    }

    &.-upload-progress {
        height: 100%;

        &::after {
            @include item-overlay.item-overlay($zIndex: zindex.$zindex-sm);

            font-size: 0;
        }

        &:not(.-upload-image-preview) {
            &::after {
                background-color: transparent;
            }
        }
    }

    &.-edit-meta {
        .sf-media__details:not(.-video) {
            bottom: media.$image-edit-meta-input-height + media.$image-helpers-edge-offset;
        }

        .#{theme.$selector-prefix}-input {
            z-index: zindex.$zindex-lg;
        }

        img {
            max-height: calc(100% - #{media.$image-edit-meta-input-height});
        }
    }

    &.-stretched {
        img {
            position: absolute;
            top: 0;
            left: 0;
            max-width: 100%;
            max-height: 100%;
            height: auto;
            vertical-align: top;
            width: 100%;
        }

        &.-centered {
            img {
                position: relative;
            }
        }
    }

    &.-document {
        width: media.$document-widht;
        height: media.$document-height;
        padding: media.$document-vertical-padding media.$document-horizontal-padding;
        text-align: left;
        background-color: colors.$color-primary;
        border: 1px solid colors.$color-secondary-lightest;

        > sf-component-loader {
            flex: 1;
        }

        .sf-media__thumbnail {
            margin-left: -#{media.$document-horizontal-padding};
            margin-right: -#{media.$document-horizontal-padding};
            padding-bottom: media.$document-vertical-padding;
            text-align: center;
            border-bottom: 1px solid colors.$color-secondary-lightest;
        }

        .sf-media__title {
            min-height: 42px;
        }

        .sf-media__title,
        .sf-media__progress-status {
            position: relative;
        }

        .sf-media__status {
            position: absolute;
            bottom: media.$document-status-offset;
        }

        .sf-media__file-info {
            &.-right-align {
                position: absolute;
                right: media.$document-status-offset;
                bottom: media.$document-status-offset;
            }

            &.-badge {
                bottom: calc(#{media.$document-height} / 2);
            }
        }

        .sf-media__progress-status {
            z-index: zindex.$zindex-md;
        }

        .sf-media__error-message {
            position: absolute;
            bottom: media.$document-edit-button-bottom-offset;
            left: -#{media.$document-horizontal-padding};
            white-space: nowrap;
        }
    }

    &.-fade-in-effect {
        img, video {
            @include animation.fadeIn(0.6s);
        }
    }

    &__progress {
        &.-absolute {
            position: absolute;
            left: 0;
            bottom: 0;
            z-index: zindex.$zindex-lg;
        }

        &[value].-vertical {
            &.-document {
                height: 200px;
                width: 260px;
                transform: rotate(-90deg) translate(-246px, -21px);
                transform-origin: top left;
                background-color: transparent;
                opacity: 0.5;

                &::after {
                    display: none;
                }

                &::-webkit-progress-bar {
                    background-color: transparent;
                }

                &::-webkit-progress-value {
                    transition: width 1s linear;
                    background-color: colors.$color-success-bg;
                    color: #fff;
                }

                &::-moz-progress-bar {
                    background-color: colors.$color-success-bg;
                }

                &.-failure {
                    &::-webkit-progress-value {
                        background-color: colors.$color-error-bg;
                    }

                    &::-moz-progress-bar {
                        background-color: colors.$color-error-bg;
                    }
                }
            }
        }
    }

    &__details {
        position: absolute;
        visibility: hidden;
        bottom: media.$image-helpers-edge-offset;
        left: media.$image-helpers-edge-offset;
        z-index: zindex.$zindex-xlg;
    }

    &__remove {
        position: absolute;
        top: media.$image-helpers-edge-offset;
        right: media.$image-helpers-edge-offset;
        z-index: zindex.$zindex-xlg;
    }

    &__labeled {
        padding-left: 2px;
        padding-right: 2px;
        font-size: typography.$font-size-base;
        text-align: center;
        z-index: zindex.$zindex-xlg;
        color: media.$image-not-translated-color;
        background-color: var(--not-translated-image-label-bg);

        &.-down {
            align-self: flex-end;
            margin-bottom: media.$image-not-translated-margin-bottom;
        }
    }

    &__stretched {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }

    &__properties-label {
        visibility: hidden;
        z-index: zindex.$zindex-xlg;
    }

    &__selected-indicator,
    &__no-selectable {
        margin-right: 0;
        position: absolute;
        visibility: hidden;
        top: media.$image-helpers-edge-offset;
        right: media.$image-helpers-edge-offset;
        z-index: zindex.$zindex-xlg;
        color: colors.$color-primary-invert;
    }

    &__file-info {
        &.-right-align {
            position: absolute;
            right: media.$image-helpers-edge-offset;
            bottom: media.$image-helpers-edge-offset;
        }
    }

    video {
        @include image.fit-contain;
    }
}

.sf-image {
    &__empty {
        &.-view-in-edit {
            min-height: 150px;
        }

        &.-video {
            width: media.$landscape-width-xs;
            height: media.$landscape-height-xs;
            border: 1px solid colors.$color-primary-variant;
        }
    }
}

.sf-placeholder {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: colors.$color-primary-variant;
    height: 100%;

    &.-upload-image-preview {
        height: calc(100% - #{media.$image-edit-meta-input-height});
    }

    &.-video {
        background-color: colors.$color-primary-invert-variant;
    }

    &.-error {
        position: relative;
        z-index: 20;
    }

    &.-svg {
        flex: 1;

        sf-component-loader {
            flex: 1;
            width: 100%;
            text-align: center;
        }
    }

    &.-no-background {
        background-color: transparent;
    }
}

.sf-flickr-gallery {
    position: relative;
    width: 100%;

    &__item {
        position: absolute;
        list-style-type: none;

        &.-static {
            position: static;
        }

        &.-video {
            sf-component-loader,
            sf-video-player,
            .sf-video {
                display: block;
                width: 100%;
                height: 100%;
            }
        }
    }

    &__drop-wrapper {
        position: absolute;
        display: flex;
        top: 0;
        width: 2px;
        background-color: colors.$color-primary-invert;
        z-index: zindex.$zindex-lg;
        pointer-events: none;
        align-items: center;
    }

    &__drop-image {
        position: absolute;
        left: -30px;
        width: 60px;
        height: auto;
        outline: 4px solid colors.$color-warning-bg;
        user-select: none;
        background-color: colors.$color-primary-variant;

        &.-wide {
            width: 90px;
            left: -45px;
        }

        &.-placeholder {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 60px;
            background-color: colors.$color-primary;
            border: 1px solid colors.$color-secondary-lightest;
        }
    }

    &__drag-zone {
        position: fixed;
        display: none;
        top: 0;
        left: 0;
        height: media.$image-drag-zone-height;
        width: 100%;
        z-index: media.$image-drag-zone-z-index;

        &.-bottom {
            top: auto;
            bottom: 0;
        }

        &.-visible {
            display: block;
        }
    }
}

/***
 * Image:
 */
/**
 * Image placeholder:
 * `.sf-media.-empty`
 *
 *     @example
 *     <div class="sf-media -empty">
 *         <i class="#{theme.$selector-prefix}-icon sf-fa sf-fa-picture-o -size-4xl -sf-mr-zero"></i>
 *     </div>
 *     <br>
 *     <div class="sf-media -empty -small">
 *         <i class="#{theme.$selector-prefix}-icon sf-fa sf-fa-picture-o -size-4xl -sf-mr-zero"></i>
 *     </div>
 *
 */
/**
 * Image with remove button and info:
 * `.sf-media`
 *
 *     @example
 *     <div class="sf-media">
 *         <img src="image.png">
 *         <button class="sf-media__remove #{theme.$selector-prefix}-button -icon -circle -small #{theme.$selector-prefix}-element"><i class="#{theme.$selector-prefix}-icon sf-fa sf-fa-times -normal % sf-button__icon -left"></i><span class="sf-button__content"></span></button>
 *         <#{theme.$selector-prefix}-button type="icon circle small" icon="times" class="sf-media__remove"></#{theme.$selector-prefix}-button>
 *         <div class="sf-tooltip sf-media__details">
 *             <i class="#{theme.$selector-prefix}-icon sf-fa sf-fa-info-circle -black -size-m"></i>
 *             <div class="#{theme.$selector-prefix}-notification -tooltip sf-tooltip__content -condensed">
 *              <div class="sf-notification__content">
 *                  1920 x 1200<br>
 *			        <span class="-sf-txt-upper-case">jpeg</span><br>
 *			        762.72 KB
 *              </div>
 *             </div>
 *          </div>
 *     </div>
 *     <br>
 *     <div class="sf-media -small">
 *         <img src="image.png">
 *         <button class="sf-media__remove #{theme.$selector-prefix}-button -icon -circle -small #{theme.$selector-prefix}-element"><i class="#{theme.$selector-prefix}-icon sf-fa sf-fa-times -normal % sf-button__icon -left"></i><span class="sf-button__content"></span></button>
