 *
 *     @example
 *     <div class="sf-media -selectable">
 *         <div class="sf-badge -white -icon"><i class="#{theme.$selector-prefix}-icon sf-fa sf-fa-check-circle sf-media__selected-indicator -size-xl"></i></div>
 *         <img src="image.png">
