@use "sass:map";
@use "../../../settings/components/typography";

/// Headings helper mixin
@mixin headings() {
    @each $heading, $heading-props in typography.$headings {
        #{$heading},
        .#{$heading} {
            @include _get_heading_styles($heading);
        }
    }
}

@mixin h1() {
    @include _get_heading_styles("h1");
}

@mixin h2() {
    @include _get_heading_styles("h2");
}

@mixin h3() {
    @include _get_heading_styles("h3");
}

@mixin h4() {
    @include _get_heading_styles("h4");
}

@mixin h5() {
    @include _get_heading_styles("h5");
}

@mixin h1-large() {
    font-size: typography.$font-size-h1-lg;
}

@mixin h4-large() {
    font-size: typography.$font-size-h4-lg;
}

@mixin _get_heading_styles($heading) {
    $heading-props: map.get(typography.$headings, $heading);
    $font-family: map.get($heading-props, "font-family");
    $font-size: map.get($heading-props, "font-size");
    $font-weight: map.get($heading-props, "font-weight");
    $font-line-height: map.get($heading-props, "line-height");
    $font-color: map.get($heading-props, "color");
    $font-text-transform: map.get($heading-props, "text-transform");
    $font-margin-top: map.get($heading-props, "margin-top");
    $font-margin-bottom: map.get($heading-props, "margin-bottom");

    @if ($font-family) {
        font-family: $font-family;
    }

    @if ($font-size) {
        font-size: $font-size;
    }

    @if ($font-weight) {
        font-weight: $font-weight;
    }

    @if ($font-line-height) {
        line-height: $font-line-height;
    }

    @if ($font-color) {
        color: $font-color;
    }

    @if ($font-text-transform) {
        text-transform: $font-text-transform;
    }

    @if ($font-margin-top) {
        margin-top: $font-margin-top;
    }

    @if ($font-margin-bottom) {
        margin-bottom: $font-margin-bottom;
    }
}
