@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/top-toolbar";
@use "../settings/components/toggle";
@use "../settings/common/colors";
@use "../settings/common/zindex";

.#{theme.$selector-prefix}-top-toolbar {
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: top-toolbar.$top-toolbar-height;
    box-shadow: top-toolbar.$top-toolbar-box-shadow;
    background-color: colors.$color-primary;
    z-index: zindex.$zindex-xlg;

    &.-zindex-sm {
        z-index: zindex.$zindex-sm;
    }

    &.-in-menu {
        box-shadow: none;

        .sf-top-toolbar__nav-button {
            border-right: 0;
        }
    }

    &__nav-button {
        width: toggle.$toggle-width;
        height: toggle.$toggle-height;
        border-right: 1px solid toggle.$toggle-open-border-color;
    }

    &.-no-border {
        box-shadow: none;
    }
}
