@use "sass:map";
@use "../../../settings/themes/sitefinity/base" as theme;
@use "../base/decoration";
@use "../base/center";
@use "../../../settings/common/spacing";
@use "../../../settings/common/colors";
@use "../../../settings/components/button";

@mixin default {
    @include center.flex;

    display: inline-flex;
    margin: button.$button-margin;
    font-weight: button.$button-font-weight;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;

    border: {
        width: 1px;
        style: solid;
    }

    white-space: nowrap;

    &:hover,
    &:active,
    &:focus {
        @include focus;
    }

    &[disabled] {
        @include disabled;
    }
}

@mixin disabled {
    pointer-events: none;
    cursor: default;

    &.-icon {
        .#{theme.$selector-prefix}-button__icon {
            color: inherit;
        }
    }
}

@mixin button($types) {
    @each $type, $color in $types {
        $border: map.get($color, border);
        $background: map.get($color, background);
        $color: map.get($color, color);

        @if ($type == normal) {
            @include decoration.fill($color, $background, $border);

            @if ($color) {

                &:#{focus} {
                    color: $color;
                }
            }
        }

        @else if ($type == disabled) {

            &[#{$type}],
            &.-#{$type} {
                &:not(.-done, .-inprogress, .-no-border) {
                    @include decoration.fill($color, $background, $border);
                }
            }
        }
        
        @else {

            &:not(.-no-#{$type}):#{$type} {
                @include decoration.fill($color, $background, $border);
            }
        }
    }
}

@mixin toolset-button() {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding-left: spacing.$spacing-xs;
    padding-right: spacing.$spacing-xs;

    &:hover {
        cursor: pointer;
        color: colors.$color-secondary;
    }
}

@mixin focus() {
    transition: border-color 0.3s, background-color 0.3s, color 0.3s;
}
