@use "../../../settings/components/typography";

@mixin hide-text-for-bg-image() {
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
}

@mixin wrap-ellipsis() {
    overflow: hidden;
    text-overflow: ellipsis;
}

@mixin overflow-ellipsis() {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

@mixin ellipsis() {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: break-word;
}

@mixin multiline-text-overflow-ellipsis() {
    display: block;
    display: box;
    word-break: break-all;
    max-height: 3em;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    // This rule is removed when compiled using postcss and autoprefix plugin: https://github.com/postcss/autoprefixer/issues/776.
    -webkit-box-orient: vertical;
}

@mixin font-awesome() {
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/// Set base font family
@mixin font-family-base() {
    font-family: typography.$font-family;
}

@mixin size-large() {
    font-size: typography.$font-size-lg;
}

@mixin size-small() {
    font-size: typography.$font-size-h5;
}

@mixin size-base() {
    font-size: typography.$font-size-base;
}

@mixin hint() {
    color: var(--secondary-text-color);
}
