@use "../settings/themes/sitefinity/base" as theme;
@use "../settings/components/dropdown";
@use "../settings/common/colors";
@use "../tools/mixins/base/common" as commonMixins;
@use "../tools/mixins/components/headings";

.#{theme.$selector-prefix}-dropdown {
    @include commonMixins.overflow-y-auto;

    position: relative;
    display: inline-block;

    &:not(.-empty) {
        padding: dropdown.$dropdown-padding  0;
        min-width: dropdown.$dropdown-min-width;
        white-space: dropdown.$dropdown-white-space;
        border: 1px solid dropdown.$dropdown-border;
        box-shadow: dropdown.$dropdown-box-shadow;
        background-color: dropdown.$dropdown-bg;
        color: dropdown.$dropdown-color;
        max-height: dropdown.$dropdown-max-height;
    }

    &.-condensed {
        padding-top: dropdown.$dropdown-condensed-vertical-padding;
        padding-bottom: dropdown.$dropdown-condensed-vertical-padding;
    }

    &.-color-picker {
        width: dropdown.$dropdown-color-picker-width;
        padding: dropdown.$dropdown-color-picker-padding;
    }

    &__item {
        display: flex;
        flex-wrap: nowrap;
        width: auto;
        position: relative;
        margin-bottom: dropdown.$dropdown-item-margin-bottom;
        padding: dropdown.$dropdown-item-padding dropdown.$dropdown-item-padding-horizontal;
        word-break: break-all;
        transition: background-color 0.3s, color 0.3s;
        cursor: pointer;

        &:hover,
        &:focus {
            background-color: dropdown.$dropdown-hover-bg;

            .#{theme.$selector-prefix}-dropdown__delete {
                display: block;
            }
        }

        &.-active {
            background-color: dropdown.$dropdown-active-bg;
        }

        &.-not-clickable {
            cursor: default;

            &:hover,
            &:focus {
                background-color: dropdown.$dropdown-transparent-hover-bg;
                outline: 0;
            }
        }

        &[disabled],
        &.-disabled {
            color: dropdown.$dropdown-item-disabled-color;
            cursor: default;
            pointer-events: none;
        }
    }

    &__delete {
        display: none;
        margin-left: auto;
    }

    &__no-items {
        padding: 5px dropdown.$dropdown-item-padding-horizontal-small 30px;
        color: colors.$color-secondary-darker;
        cursor: default;
    }

    &__heading {
        @include headings.h5;

        padding: dropdown.$dropdown-item-heading-padding-top dropdown.$dropdown-item-padding-horizontal dropdown.$dropdown-item-heading-padding-bottom;
        line-height: 1;
        cursor: default;

        &.-condensed {
            padding-left: dropdown.$dropdown-item-padding-horizontal-small;
            padding-right: dropdown.$dropdown-item-padding-horizontal-small;
        }

        &:hover {
            background: transparent;
        }
    }

    &__notification {
        margin: dropdown.$dropdown-item-notification-margin-top dropdown.$dropdown-item-padding-horizontal dropdown.$dropdown-item-notification-margin-bottom;
        white-space: normal;
        cursor: default;
    }

    &__content {
        cursor: default;
    }

    &__separator {
        margin-top: dropdown.$dropdown-item-separator-margin-top;
        padding-top: dropdown.$dropdown-item-separator-padding-top;
        border-top: 1px solid dropdown.$dropdown-item-separator-color;
        cursor: default;
    }

    &__description {
        @include headings.h5;

        display: block;
        text-transform: uppercase;
        line-height: dropdown.$dropdown-item-description-line-height;
    }

    &__counter {
        margin-left: auto;
        min-width: 50px;
        text-align: right;

        &.-positive {
            color: dropdown.$dropdown-item-counter-positive;
        }

        &.-neutral {
            color: dropdown.$dropdown-item-counter-neutral;
        }
    }

    &__icon {
        font-size: inherit;
        margin-top: -5px;
    }
}

